import * as THREE from 'three';
import { MODEL_COLOR_PRESETS } from '../../presets.js';
import { ErrorHandler } from '../../utils/ErrorHandler.mjs';

export class PhoneModelBuilder_Rounded {
  constructor(scene, config = {}) {
    this.scene = scene;
    this.config = config;
    this.phoneGroup = new THREE.Group();
    this.phoneGroup.name = 'phoneModel';
    
    // --- Fallback Defaults (Used if config doesn't provide values) ---
    const fallbackDimensions = {
      width: 2.4,
      height: 5,
      depth: 0.2,
      cornerRadius: 0.25
    };
    const fallbackMaterials = {
      frame: { color: 0x333333, metalness: 0.8, roughness: 0.2, envMapIntensity: 1.0, flatShading: false, shadowSide: THREE.FrontSide },
      screen: {
        color: 0xffffff,
        side: THREE.FrontSide,
        depthTest: true,
        polygonOffset: true,
        polygonOffsetFactor: -100,
        metalness: 0.0,
        roughness: 0.0,
        envMapIntensity: 0.0,
        emissive: 0xffffff,
        emissiveIntensity: 0.3
      },
      glass: { color: 0xffffff, transparent: true, opacity: 0.05, roughness: 0.05, transmission: 0.98, thickness: 0.01 },
      notch: { color: 0x222222, metalness: 0.8, roughness: 0.2, envMapIntensity: 1.0 },
      backCamera: { color: 0x111111, metalness: 0.9, roughness: 0.1, envMapIntensity: 1.0 },
      frontCamera: { color: 0x111111, metalness: 0.9, roughness: 0.1, envMapIntensity: 1.0 },
      flash: { color: 0xffffff, metalness: 0.0, roughness: 0.1, emissive: 0xffffff, emissiveIntensity: 0.0 },
      volumeUpButton: { color: 0x444444, metalness: 0.8, roughness: 0.2, envMapIntensity: 1.0 },
      volumeDownButton: { color: 0x444444, metalness: 0.8, roughness: 0.2, envMapIntensity: 1.0 },
      powerButton: { color: 0x444444, metalness: 0.8, roughness: 0.2, envMapIntensity: 1.0 }
    };
    const fallbackFeatures = {
        hasNotch: true,
        hasCameras: true,
        hasButtons: true
    };
    // --- End Fallback Defaults ---

    // Use provided config or fallbacks
    this.effectiveConfig = {
        dimensions: { ...fallbackDimensions, ...(config.dimensions || {}) },
        materials: { /* Needs careful merging below */ },
        features: { ...fallbackFeatures, ...(config.features || {}) }
    };

    // --- Store original dimensions for reset ---
    this.originalDimensions = { ...this.effectiveConfig.dimensions };
    // ------------------------------------------

    // Initialize visibility state based on features config
    this.visibilityState = {
      frame: true, // Always visible
      screen: true, // Always visible
      glass: true, // Always visible? Maybe tie to screen?
      notch: this.effectiveConfig.features.hasNotch,
      backCamera: this.effectiveConfig.features.hasCameras, // Might need finer control
      frontCamera: this.effectiveConfig.features.hasCameras && this.effectiveConfig.features.hasNotch, // Front camera often in notch
      flash: this.effectiveConfig.features.hasCameras, // Usually near back camera
      volumeUpButton: this.effectiveConfig.features.hasButtons,
      volumeDownButton: this.effectiveConfig.features.hasButtons,
      powerButton: this.effectiveConfig.features.hasButtons
    };

    // Create materials based on effective config
    this.materials = {};
    for (const key in fallbackMaterials) {
        const configMaterialProps = config.materials?.[key] || {};
        const fallbackMaterialProps = fallbackMaterials[key];

        // Merge properties, giving precedence to config
        const finalProps = { ...fallbackMaterialProps, ...configMaterialProps };

        // Handle specific material types if needed (e.g., glass uses MeshPhysicalMaterial)
        if (key === 'glass') {
            this.materials[key] = new THREE.MeshPhysicalMaterial(finalProps);
        } else {
            this.materials[key] = new THREE.MeshStandardMaterial(finalProps);
        }
    }

    // Store mesh references
    this.meshes = {};
  }
  
  build() {
    // Create all parts
    this.createPhoneBody(); // Uses effectiveConfig.dimensions
    this.createScreen(); // Uses effectiveConfig.dimensions (indirectly via MAX_WIDTH/HEIGHT for now)
    this.createGlass(); // Uses screen dimensions

    if (this.effectiveConfig.features.hasNotch) {
        this.createNotch(); // Uses hardcoded dimensions for now
    }
    if (this.effectiveConfig.features.hasCameras) {
        this.createCameras(); // Uses hardcoded dimensions/positions
    }
    if (this.effectiveConfig.features.hasButtons) {
        this.createButtons(); // Uses hardcoded dimensions/positions
    }
    
    // Store reference to builder in userData for later access
    this.phoneGroup.userData = {
      builder: this
    };
    
    // Convert materials object to Map for compatibility with ModelManager
    const materialsMap = new Map();
    Object.entries(this.materials).forEach(([key, material]) => {
      materialsMap.set(key, material);
    });
    
    // Add phone to scene only if scene is valid
    if (this.scene && (typeof this.scene.add === 'function' || this.scene instanceof THREE.Scene)) {
      this.scene.add(this.phoneGroup);
    } else {
      console.warn('PhoneModelBuilder_Rounded: Invalid scene object, model created but not added to scene.');
    }
    
    return {
      model: this.phoneGroup,
      screen: this.screen,
      screenMaterial: this.materials.screen,
      materials: materialsMap,
      builder: this
    };
  }
  
  createPhoneBody() {
    // Use dimensions from effectiveConfig
    const { width, height, depth, cornerRadius: radius } = this.effectiveConfig.dimensions;
    
    // Create rounded rectangle shape for the phone
    const shape = new THREE.Shape();
    
    // Add console log to debug shape creation
    console.log('Creating phone body shape with dimensions:', width, height, radius);
    
    shape.moveTo(width / 2 - radius, -height / 2);
    shape.lineTo(-width / 2 + radius, -height / 2);
    shape.quadraticCurveTo(-width / 2, -height / 2, -width / 2, -height / 2 + radius);
    shape.lineTo(-width / 2, height / 2 - radius);
    shape.quadraticCurveTo(-width / 2, height / 2, -width / 2 + radius, height / 2);
    shape.lineTo(width / 2 - radius, height / 2);
    shape.quadraticCurveTo(width / 2, height / 2, width / 2, height / 2 - radius);
    shape.lineTo(width / 2, -height / 2 + radius);
    shape.quadraticCurveTo(width / 2, -height / 2, width / 2 - radius, -height / 2);
    
    // Log shape to verify it has path points
    console.log('Shape created with points:', shape.curves.length, 'curves');
    
    // Extrude the shape to create 3D geometry
    const extrudeSettings = {
      steps: 2,
      depth: depth,
      bevelEnabled: true,
      bevelThickness: 0.05,
      bevelSize: 0.05,
      bevelOffset: 0,
      bevelSegments: 5
    };
    
    const bodyGeometry = new THREE.ExtrudeGeometry(shape, extrudeSettings);
    
    const phoneBody = new THREE.Mesh(bodyGeometry, this.materials.frame);
    
    // Center the phone body
    // For ExtrudeGeometry, we need to adjust the z position to center it
    phoneBody.position.z = -depth; 
    
    // Add to group and store reference
    this.phoneGroup.add(phoneBody);
    this.phoneBody = phoneBody;
    this.meshes.frame = phoneBody;
  }
  
  createScreen(contentAspectRatio = null) {
    // Use dimensions from effectiveConfig to set MAX limits?
    // Or should screen dimensions be part of config?
    // For now, keep MAX limits based on body size relative adjustments
    const bodyWidth = this.effectiveConfig.dimensions.width;
    const bodyHeight = this.effectiveConfig.dimensions.height;
    const padding = this.config.bezelThickness ?? 0.1; // Example padding; configurable via config.bezelThickness

    // Maximum allowed dimensions relative to body
    const MAX_WIDTH = bodyWidth - 2 * padding;
    const MAX_HEIGHT = bodyHeight - 2 * padding;
    
    // Default dimensions - start with maximum
    let width = MAX_WIDTH;
    let height = MAX_HEIGHT;
    
    // If content aspect ratio is provided, adjust screen dimensions to match
    // while ensuring it fits within the maximum dimensions
    if (contentAspectRatio) {
      console.log('Adjusting screen dimensions to match content aspect ratio:', contentAspectRatio);
      
      // Calculate dimensions that would fit within the phone
      // Phone aspect ratio is MAX_WIDTH / MAX_HEIGHT
      const phoneAspectRatio = MAX_WIDTH / MAX_HEIGHT;
      
      if (contentAspectRatio < phoneAspectRatio) {
        // Content is taller relative to width than the phone screen
        // So use max height and calculate proportional width
        height = MAX_HEIGHT;
        width = height * contentAspectRatio;
      } else {
        // Content is wider relative to height than the phone screen
        // So use max width and calculate proportional height
        width = MAX_WIDTH;
        height = width / contentAspectRatio;
      }
      
      // Always ensure dimensions don't exceed maximums
      width = Math.min(width, MAX_WIDTH);
      height = Math.min(height, MAX_HEIGHT);
      
      // Store the aspect ratio for future reference
      this.lastContentAspectRatio = contentAspectRatio;
      
      console.log('Adjusted screen dimensions:', width, 'x', height, 'aspect ratio:', width/height);
    }
    
    // --- Use rounded rectangle shape for the screen ---
    const screenRadius = Math.max(0, this.effectiveConfig.dimensions.cornerRadius - padding);
    const shape = new THREE.Shape();
    const hw = width / 2;
    const hh = height / 2;
    const r = screenRadius;

    shape.moveTo(hw - r, -hh);
    shape.lineTo(-hw + r, -hh);
    shape.quadraticCurveTo(-hw, -hh, -hw, -hh + r);
    shape.lineTo(-hw, hh - r);
    shape.quadraticCurveTo(-hw, hh, -hw + r, hh);
    shape.lineTo(hw - r, hh);
    shape.quadraticCurveTo(hw, hh, hw, hh - r);
    shape.lineTo(hw, -hh + r);
    shape.quadraticCurveTo(hw, -hh, hw - r, -hh);

    const screenGeometry = new THREE.ShapeGeometry(shape);
    screenGeometry.computeVertexNormals();
    screenGeometry.computeBoundingBox();
    const bbox = screenGeometry.boundingBox;
    const min = bbox.min;
    const size = new THREE.Vector2(bbox.max.x - min.x, bbox.max.y - min.y);
    const uv = [];
    for (let i = 0; i < screenGeometry.attributes.position.count; i++) {
      const x = screenGeometry.attributes.position.getX(i);
      const y = screenGeometry.attributes.position.getY(i);
      uv.push((x - min.x) / size.x, (y - min.y) / size.y);
    }
    screenGeometry.setAttribute('uv', new THREE.Float32BufferAttribute(uv, 2));
    // --- End rounded screen geometry ---

    // Store dimensions for consistent glass and future use
    this.screenDimensions = { width, height };

    // Create the screen mesh
    this.screen = new THREE.Mesh(screenGeometry, this.materials.screen);
    
    // Position the screen slightly in front of the body's front face (which is at z=0 for extruded body)
    this.screen.position.z = 0.055; // Increased slightly from 0.01 to potentially avoid z-fighting
    
    // Add screen to group
    this.phoneGroup.add(this.screen);
    
    // Store reference
    this.meshes.screen = this.screen;
    
    // Return the current screen dimensions for reference
    return { width, height, aspectRatio: width/height };
  }

  createGlass() {
    if (!this.screen) {
        console.warn("Screen mesh doesn't exist, cannot create glass overlay.");
        return;
    }
    // Use stored screen dimensions to keep corners consistent
    const screenWidth = this.screenDimensions.width;
    const screenHeight = this.screenDimensions.height;
    const glassDepth = 0.02; // Thin glass layer

    // --- Use BoxGeometry for the glass ---
    const glassGeometry = new THREE.BoxGeometry(screenWidth, screenHeight, glassDepth);
    // --- End Use BoxGeometry ---

    this.glass = new THREE.Mesh(glassGeometry, this.materials.glass);
    
    // Position glass slightly in front of the screen
    this.glass.position.z = this.screen.position.z + 0.01; // Position relative to screen
    
    // Add glass to group
    this.phoneGroup.add(this.glass);
    
    // Store reference in meshes map
    this.meshes.glass = this.glass;
  }
  
  createNotch() {
    // TODO: Make notch dimensions/shape configurable via this.config.features.notchDetails?
    // Notch dimensions (currently hardcoded)
    const width = 0.6;
    const height = 0.15;
    const radius = 0.075;
    
    // Create rounded rectangle shape for the notch
    const shape = new THREE.Shape();
    
    shape.moveTo(width / 2 - radius, -height / 2);
    shape.lineTo(-width / 2 + radius, -height / 2);
    shape.quadraticCurveTo(-width / 2, -height / 2, -width / 2, -height / 2 + radius);
    shape.lineTo(-width / 2, height / 2 - radius);
    shape.quadraticCurveTo(-width / 2, height / 2, -width / 2 + radius, height / 2);
    shape.lineTo(width / 2 - radius, height / 2);
    shape.quadraticCurveTo(width / 2, height / 2, width / 2, height / 2 - radius);
    shape.lineTo(width / 2, -height / 2 + radius);
    shape.quadraticCurveTo(width / 2, -height / 2, width / 2 - radius, -height / 2);
    
    const notchGeometry = new THREE.ShapeGeometry(shape);
    this.notch = new THREE.Mesh(notchGeometry, this.materials.notch);

    // Position the notch at the top center of the screen area
    const screenTopY = (this.screen?.geometry?.parameters?.height || this.effectiveConfig.dimensions.height - 0.2) / 2;
    // Calculate Z position independently based on depth config, mirroring screen's logic
    const notchBaseZ = -this.effectiveConfig.dimensions.depth + 0.01; // Same base Z as screen

    this.notch.position.set(
        0, // Center horizontally
        screenTopY - height / 2, // Position at the top edge
        notchBaseZ + 0.001 // Place notch slightly in front of the calculated screen plane
    );

    // Ensure visibility state is respected
    this.notch.visible = this.visibilityState.notch;
    
    // Add to group and store reference
    this.phoneGroup.add(this.notch);
    this.meshes.notch = this.notch;
  }
  
  createCameras() {
    // Use dimensions from effectiveConfig?
    const bodyWidth = this.effectiveConfig.dimensions.width;
    const bodyHeight = this.effectiveConfig.dimensions.height;
    const notchHeight = 0.15; // Assuming this is defined or calculated somewhere
    const MAX_HEIGHT = bodyHeight - 0.2; // Example padding adjusted height
    
    // Define Z positions for clarity
    const Z_POS_CAMERA_BACK = -this.effectiveConfig.dimensions.depth - 0.01; // Slightly behind the body back face
    const Z_POS_CAMERA_FRONT = 0.01; // Slightly in front of the screen
    
    // Back Camera
    const backCameraRadius = 0.1;
    const backCameraGeometry = new THREE.CircleGeometry(backCameraRadius, 32);
    const backCameraMesh = new THREE.Mesh(backCameraGeometry, this.materials.backCamera);
    // Position relative to height
    backCameraMesh.position.set(0, bodyHeight * 0.4, Z_POS_CAMERA_BACK); // Changed Y from 2.0
    this.phoneGroup.add(backCameraMesh);
    this.meshes.backCamera = backCameraMesh;
    
    // Flash (position relative to back camera or body?)
    const flashRadius = 0.05;
    const flashGeometry = new THREE.CircleGeometry(flashRadius, 32);
    const flashMesh = new THREE.Mesh(flashGeometry, this.materials.flash);
    // Position relative to height, slightly below back camera
    flashMesh.position.set(0, bodyHeight * 0.36, Z_POS_CAMERA_BACK + 0.001); // Changed Y from 1.8
    this.phoneGroup.add(flashMesh);
    this.meshes.flash = flashMesh;
    
    // Front Camera (usually inside notch)
    // Ensure notch is created first if this depends on its dimensions
    if (this.meshes.notch) { // Check if notch exists
        const frontCameraRadius = 0.06;
        const frontCameraGeometry = new THREE.CircleGeometry(frontCameraRadius, 32);
        const frontCameraMesh = new THREE.Mesh(frontCameraGeometry, this.materials.frontCamera);
        // Position relative to the top edge (derived from bodyHeight)
        frontCameraMesh.position.set(0, bodyHeight / 2 - notchHeight / 2, Z_POS_CAMERA_FRONT); // Y depends on notch pos, which is relative
        this.phoneGroup.add(frontCameraMesh);
        this.meshes.frontCamera = frontCameraMesh;
    } else {
        console.warn("Notch mesh not found, cannot create front camera within it.");
    }
  }
  
  createButtons() {
    const { width, height, depth } = this.effectiveConfig.dimensions;
    const buttonWidth = 0.08;
    const buttonHeight = 0.3;
    const buttonDepth = 0.05;
    
    // Create standard BoxGeometry for buttons instead of extrusion
    const buttonGeometry = new THREE.BoxGeometry(buttonWidth, buttonHeight, buttonDepth);
    
    // Volume Up Button (Left side)
    const volumeUpButton = new THREE.Mesh(buttonGeometry, this.materials.volumeUpButton);
    this.meshes.volumeUpButton = volumeUpButton;
    // Position buttons to be centered in the phone thickness (at z = -depth/2)
    volumeUpButton.position.set(-width/2 - buttonWidth/2, height * 0.3, -depth/2);
    this.phoneGroup.add(volumeUpButton);
    
    // Volume Down Button (Left side)
    const volumeDownButton = new THREE.Mesh(buttonGeometry, this.materials.volumeDownButton);
    this.meshes.volumeDownButton = volumeDownButton;
    volumeDownButton.position.set(-width/2 - buttonWidth/2, height * 0.2, -depth/2);
    this.phoneGroup.add(volumeDownButton);
    
    // Power Button (Right side)
    const powerButton = new THREE.Mesh(buttonGeometry, this.materials.powerButton);
    this.meshes.powerButton = powerButton;
    powerButton.position.set(width/2 + buttonWidth/2, height * 0.25, -depth/2);
    this.phoneGroup.add(powerButton);
  }
  
  // Helper method to create rounded buttons
  createRoundedButton(width, height, depth, radius) {
    // Create rounded rectangle shape for the button
    const shape = new THREE.Shape();
    
    shape.moveTo(width / 2 - radius, -height / 2);
    shape.lineTo(-width / 2 + radius, -height / 2);
    shape.quadraticCurveTo(-width / 2, -height / 2, -width / 2, -height / 2 + radius);
    shape.lineTo(-width / 2, height / 2 - radius);
    shape.quadraticCurveTo(-width / 2, height / 2, -width / 2 + radius, height / 2);
    shape.lineTo(width / 2 - radius, height / 2);
    shape.quadraticCurveTo(width / 2, height / 2, width / 2, height / 2 - radius);
    shape.lineTo(width / 2, -height / 2 + radius);
    shape.quadraticCurveTo(width / 2, -height / 2, width / 2 - radius, -height / 2);
    
    // Extrude the shape to create 3D geometry
    const extrudeSettings = {
      steps: 1,
      depth: depth,
      bevelEnabled: true,
      bevelThickness: 0.01, // Reduced bevel thickness
      bevelSize: 0.005,      // Reduced bevel size
      bevelOffset: 0,
      bevelSegments: 3
    };
    
    const buttonGeometry = new THREE.ExtrudeGeometry(shape, extrudeSettings);
    
    // Create button mesh
    const buttonMesh = new THREE.Mesh(buttonGeometry);
    
    // Rotate and position for proper orientation (buttons need to face outward)
    buttonMesh.rotation.y = Math.PI / 2; // Rotate 90° to orient properly
    
    return buttonMesh;
  }

  getCustomizableComponents() {
    const components = {};
    
    // Use the materials object which was created based on config/fallbacks
    Object.entries(this.materials).forEach(([key, material]) => {
      // Check visibility state for this component key
      const isVisible = this.visibilityState[key];
      // Only add component if it's potentially visible (based on features)
      // and the corresponding mesh exists (was actually built)
      if (isVisible !== false && this.meshes[key]) {
          const color = material.color ? '#' + material.color.getHexString() : undefined;

          components[key] = {
            name: key.charAt(0).toUpperCase() + key.slice(1).replace(/([A-Z])/g, ' $1'), // Add spaces before caps
            // color: color, // Color handled within properties
            show: isVisible, // Use current visibility state
            properties: {}
          };

          // Add common properties if they exist on the material
          if (material.color) components[key].properties.color = color;
          if (this.visibilityState[key] !== undefined) components[key].properties.show = isVisible; // Reflect actual visibility

          const commonProperties = [
            'metalness', 'roughness', 'opacity', 'transmission',
            'emissive', 'emissiveIntensity', 'envMapIntensity',
            'thickness', 'transparent' // Removed flatShading, shadowSide as less common UI controls
          ];

          commonProperties.forEach(prop => {
            if (prop in material && typeof material[prop] !== 'undefined' && material[prop] !== null) {
                // Special handling for emissive color?
                if (prop === 'emissive' && material.emissive) {
                     components[key].properties[prop] = '#' + material.emissive.getHexString();
                } else {
                     components[key].properties[prop] = material[prop];
                }
            }
          });
      }
    });
    
    return components;
  }
  
  updateMaterialProperty(componentKey, property, value) {
    // Use componentKey directly as it comes from getCustomizableComponents keys
    if (!this.meshes[componentKey]) {
      console.warn(`No mesh found for component key: ${componentKey}`);
      return;
    }

    // Special handling for the 'show' property - update visibility state and mesh
    if (property === 'show') {
      this.visibilityState[componentKey] = value;
      this.meshes[componentKey].visible = value;

      // Handle visibility dependencies (e.g., hide front camera if notch is hidden)
      if (componentKey === 'notch' && !value && this.meshes.frontCamera) {
          this.meshes.frontCamera.visible = false;
          this.visibilityState.frontCamera = false;
          // Need to notify UI to update? Or assume UI initiated this?
      }
      // Add more dependencies if needed

      return;
    }

    // For all other properties, update the material
    const material = this.materials[componentKey]; // Use the stored material
    if (!material) {
      console.warn(`No material found for component key: ${componentKey}`);
      return;
    }

    // Handle color property specially (assuming value is hex string like '#RRGGBB')
    if (property === 'color' || property === 'emissive') {
      try {
        material[property].set(value); // Use set() for THREE.Color
      } catch (e) {
        console.error(`Error setting ${property} color for ${componentKey}:`, e);
      }
      return;
    }

    // For numeric properties, ensure value is a number
    if (typeof material[property] === 'number') {
      value = parseFloat(value);
      if (isNaN(value)) {
          console.warn(`Invalid numeric value "${value}" for property "${property}" on component "${componentKey}"`);
          return;
      }
    }

     // For boolean properties
    if (typeof material[property] === 'boolean') {
        value = Boolean(value);
    }

    // Apply the property if it exists on the material
    if (property in material) {
        material[property] = value;
        material.needsUpdate = true; // Flag material for update
    } else {
        console.warn(`Property "${property}" not found on material for component "${componentKey}"`);
    }
  }

  /**
   * Apply a material preset to the model
   * @param {string} presetName - Name of the preset to apply
   * @param {THREE.Object3D} modelObject - The model object to update
   * @param {Map} materialsMap - Map of materials by name
   */
  applyMaterialPreset(presetName, modelObject = null, materialsMap = null) {
    // Import MODEL_COLOR_PRESETS if not available directly
    if (typeof MODEL_COLOR_PRESETS === 'undefined') {
      try {
        // Try to access from window.app.presets
        if (window.app && window.app.presets && window.app.presets.MODEL_COLOR_PRESETS) {
          const presets = window.app.presets.MODEL_COLOR_PRESETS;
          return this._applyPresetInternal(presetName, presets, materialsMap);
        }
        console.warn(`PhoneModelBuilder_Rounded: Cannot apply preset '${presetName}' - MODEL_COLOR_PRESETS not available.`);
      } catch (error) {
        console.error(`PhoneModelBuilder_Rounded: Error applying preset: ${error.message}`);
      }
      return false;
    }
    
    return this._applyPresetInternal(presetName, MODEL_COLOR_PRESETS, materialsMap);
  }
  
  // Internal method to apply preset
  _applyPresetInternal(presetName, presets, materialsMap) {
    // Handle numeric preset indices (useful if UI passes index)
    if (!isNaN(presetName)) {
      const presetNames = Object.keys(presets);
      const index = parseInt(presetName);
      if (index >= 0 && index < presetNames.length) {
        presetName = presetNames[index];
        console.log(`Converting numeric index ${index} to preset name: ${presetName}`);
      } else {
         console.warn(`Numeric index ${index} is out of bounds for MODEL_COLOR_PRESETS`);
         return false;
      }
    }

    if (!presets[presetName]) {
      console.warn(`Material preset "${presetName}" not found in MODEL_COLOR_PRESETS`);
      return false;
    }

    const preset = presets[presetName];
    console.log(`PhoneModelBuilder_Rounded: Applying material preset: ${presetName}`);

    // Map preset component keys to builder material keys
    const componentMap = {
      'body': 'frame',
      'cameraRing': 'backCamera',
      'button': 'volumeUpButton', // Apply to all buttons? Maybe clone for each?
      'screenFrame': 'notch',
      'screen': 'screen',
      'glass': 'glass',
      // Add logo mapping to ignore it (since we don't have a logo component)
      'logo': null
    };
    
    // Apply preset properties to matching materials
    for (const presetKey in preset) {
      // Get the corresponding material key for this component
      const materialKey = componentMap[presetKey] || presetKey;
      
      // Skip components that are explicitly mapped to null
      if (materialKey === null) {
        continue;
      }
      
      // Get the material to update (use materialsMap if provided, otherwise use local materials)
      let material = null;
      if (materialsMap && materialsMap.has(materialKey)) {
        material = materialsMap.get(materialKey);
      } else if (this.materials && this.materials[materialKey]) {
        material = this.materials[materialKey];
      }
      
      if (material) {
        // Apply all properties from preset to material
        const properties = preset[presetKey];
        for (const propName in properties) {
          const value = properties[propName];
          
          if (propName === 'color' && material.color) {
            if (typeof value === 'number') {
              material.color.setHex(value);
            } else if (typeof value === 'string' && value.startsWith('#')) {
              material.color.set(value);
            }
          } else if (propName === 'emissive' && material.emissive) {
            if (typeof value === 'number') {
              material.emissive.setHex(value);
            } else if (typeof value === 'string' && value.startsWith('#')) {
              material.emissive.set(value);
            }
          } else if (material[propName] !== undefined) {
            material[propName] = value;
          }
        }
        
        material.needsUpdate = true;
      } else if (presetKey === 'body') {
        // Special case for body - try to apply it to buttons and other components if they exist
        ['volumeUpButton', 'volumeDownButton', 'powerButton'].forEach(buttonKey => {
          if (materialsMap && materialsMap.has(buttonKey)) {
            const buttonMat = materialsMap.get(buttonKey);
            if (buttonMat && preset[presetKey].color && buttonMat.color) {
              buttonMat.color.setHex(preset[presetKey].color);
              buttonMat.needsUpdate = true;
            }
          }
        });
      } else if (!componentMap[presetKey]) {
        // Only warn about unknown components that aren't explicitly mapped
        console.debug(`PhoneModelBuilder_Rounded: No material found for '${materialKey}' - skipping component '${presetKey}'`);
      }
    }
    
    return true;
  }

  /**
   * Sets the aspect ratio of the screen, resizing the phone body accordingly.
   * @param {number} aspectRatio - The desired width/height aspect ratio for the screen content.
   */
  async setAspectRatio(aspectRatio) {
    if (typeof aspectRatio !== 'number' || aspectRatio <= 0) {
      console.error(`Invalid aspect ratio provided: ${aspectRatio}`);
      return;
    }

    console.log(`Setting aspect ratio. Target: ${aspectRatio}`);

    // --- Calculate new dimensions ---
    const originalWidth = this.originalDimensions.width;
    const originalHeight = this.originalDimensions.height;
    const padding = this.config.bezelThickness ?? 0.1; // Consistent padding from createScreen
    const maxWidth = originalWidth - 2 * padding;
    const maxHeight = originalHeight - 2 * padding;

    // Calculate screen dimensions based on target aspect ratio, fitting within max bounds
    let screenWidth = maxWidth;
    let screenHeight = screenWidth / aspectRatio;

    if (screenHeight > maxHeight) {
        // Height constrained
        screenHeight = maxHeight;
        screenWidth = screenHeight * aspectRatio;
    }
    // Ensure width doesn't exceed max (might happen with very wide aspect ratios)
    screenWidth = Math.min(screenWidth, maxWidth);

    // Calculate the new *body* height based on the required screen height + padding
    const newBodyHeight = screenHeight + 2 * padding;
    const newBodyWidth = originalWidth; // Keep body width constant

    // Check if dimensions actually changed significantly to avoid unnecessary rebuilds
    const currentBodyHeight = this.effectiveConfig.dimensions.height;
    if (Math.abs(newBodyHeight - currentBodyHeight) < 0.01) {
      console.log('Aspect ratio change results in negligible dimension change. Skipping rebuild.');
      // Still might need to update screen geometry if its aspect changed, even if body didn't
      // TODO: Refine this - maybe force screen rebuild anyway?
      // For now, let's assume if body doesn't change, screen fitting handles it.
      return;
    }

    // Update effective config dimensions (only height changes for the body)
    this.effectiveConfig.dimensions.height = newBodyHeight;
    // Body width, depth, cornerRadius remain based on original config
    this.effectiveConfig.dimensions.width = this.originalDimensions.width;
    this.effectiveConfig.dimensions.depth = this.originalDimensions.depth;
    this.effectiveConfig.dimensions.cornerRadius = this.originalDimensions.cornerRadius;

    console.log(`Updating body height to: ${newBodyHeight} for screen aspect ratio ${aspectRatio}`);

    // --- Rebuild necessary parts ---
    try {
      // Dispose old geometries and remove meshes
      this._disposeAndRemoveMesh('frame');
      this._disposeAndRemoveMesh('screen');
      this._disposeAndRemoveMesh('glass');
      // TODO: Handle buttons, cameras, notch if their position depends on height
      this._disposeAndRemoveMesh('notch');
      this._disposeAndRemoveMesh('backCamera');
      this._disposeAndRemoveMesh('frontCamera');
      this._disposeAndRemoveMesh('flash');
      this._disposeAndRemoveMesh('volumeUpButton');
      this._disposeAndRemoveMesh('volumeDownButton');
      this._disposeAndRemoveMesh('powerButton');

      // Recreate parts with new dimensions
      this.createPhoneBody(); // Uses updated effectiveConfig.dimensions
      this.createScreen();    // Uses updated effectiveConfig.dimensions
      this.createGlass();     // Uses updated screen size
      // Recreate features - Need to update their position calculation if height-dependent
      if (this.effectiveConfig.features.hasNotch) this.createNotch();
      if (this.effectiveConfig.features.hasCameras) this.createCameras();
      if (this.effectiveConfig.features.hasButtons) this.createButtons();

       console.log("Phone model rebuilt with new aspect ratio.");

    } catch (error) {
        console.error("Error rebuilding phone model after aspect ratio change:", error);
        // TODO: Attempt to revert to original dimensions?
        ErrorHandler.handleError(error, "Failed to rebuild model for aspect ratio");
    }
  }

  /**
   * Helper to safely dispose geometry and remove mesh from group and cache.
   * @param {string} meshKey - The key in this.meshes (e.g., 'frame', 'screen')
   * @private
   */
  _disposeAndRemoveMesh(meshKey) {
    const mesh = this.meshes[meshKey];
    if (mesh) {
        if (mesh.geometry) {
            mesh.geometry.dispose();
            // console.log(`Disposed geometry for ${meshKey}`);
        }
        // Material is shared in this.materials, don't dispose here unless unique
        this.phoneGroup.remove(mesh);
        delete this.meshes[meshKey];
        // console.log(`Removed mesh ${meshKey}`);
    } else {
        // console.log(`Mesh key ${meshKey} not found for disposal/removal.`);
    }
    // Also clear direct references if they exist (like this.screen)
    if (meshKey === 'screen' && this.screen === mesh) {
        this.screen = null;
    }
    if (meshKey === 'frame' && this.phoneBody === mesh) {
        this.phoneBody = null;
    }
    // Add others if needed (e.g., this.glass)
  }

  /**
   * Resets the phone dimensions and geometry back to the original configuration.
   */
  async resetDimensions() {
    console.log("Resetting phone dimensions to original...");

    // Check if dimensions are already original
    if (Math.abs(this.effectiveConfig.dimensions.height - this.originalDimensions.height) < 0.01 &&
        Math.abs(this.effectiveConfig.dimensions.width - this.originalDimensions.width) < 0.01) { // Check width too for robustness
      console.log("Dimensions are already original. Skipping reset rebuild.");
      return;
    }

    // Restore original dimensions in effectiveConfig
    this.effectiveConfig.dimensions = { ...this.originalDimensions };

    // --- Rebuild necessary parts ---
    try {
      // Dispose old geometries and remove meshes
      this._disposeAndRemoveMesh('frame');
      this._disposeAndRemoveMesh('screen');
      this._disposeAndRemoveMesh('glass');
      this._disposeAndRemoveMesh('notch');
      this._disposeAndRemoveMesh('backCamera');
      this._disposeAndRemoveMesh('frontCamera');
      this._disposeAndRemoveMesh('flash');
      this._disposeAndRemoveMesh('volumeUpButton');
      this._disposeAndRemoveMesh('volumeDownButton');
      this._disposeAndRemoveMesh('powerButton');

      // Recreate parts with original dimensions
      this.createPhoneBody();
      this.createScreen();
      this.createGlass();
      if (this.effectiveConfig.features.hasNotch) this.createNotch();
      if (this.effectiveConfig.features.hasCameras) this.createCameras();
      if (this.effectiveConfig.features.hasButtons) this.createButtons();

      console.log("Phone model reset to original dimensions.");

    } catch (error) {
        console.error("Error rebuilding phone model during dimension reset:", error);
        // If reset fails, the state might be inconsistent. Log error.
        ErrorHandler.handleError(error, "Failed to rebuild model during reset");
    }
  }
} 