import * as THREE from 'three';

export class MaterialManager {
  constructor() {
    this.materials = new Map();
    this.currentMaterial = null;
    this.currentGlassMode = 'realistic';
    this.materialPresets = {
      screen: {
        color: 0x000000,
        metalness: 0.0,
        roughness: 0.0,
        transmission: 0.0,
        transparent: false,
        opacity: 1.0,
        clearcoat: 0.0,
        clearcoatRoughness: 0.1,
        side: THREE.FrontSide,
        polygonOffset: true,
        polygonOffsetFactor: -1,
        polygonOffsetUnits: -4,
        depthWrite: true,
        depthTest: true,
        envMapIntensity: 0.0
      },
      glass: {
        color: 0xffffff,
        metalness: 0.0,
        roughness: 0.1,
        transmission: 0.95,
        thickness: 0.01,
        transparent: true,
        opacity: 0.1,
        clearcoat: 0.5,
        clearcoatRoughness: 0.1,
        side: THREE.DoubleSide,
        envMapIntensity: 1.0
      },
      glassPerformant: {
        color: 0xffffff,
        metalness: 0.0,
        roughness: 0.1,
        transmission: 0.0,
        thickness: 0.0,
        transparent: true,
        opacity: 0.25,
        clearcoat: 0.0,
        clearcoatRoughness: 0.1,
        side: THREE.FrontSide,
        envMapIntensity: 1.0
      },
      plastic: {
        color: 0x333333,
        metalness: 0.1,
        roughness: 0.5,
        transmission: 0.0,
        transparent: false,
        clearcoat: 0.5,
        clearcoatRoughness: 0.1,
        envMapIntensity: 1.0
      }
    };
  }

  /**
   * Create a new material based on a preset
   * @param {string} presetName - Name of the preset to use
   * @param {Object} options - Additional options to override preset values
   * @returns {THREE.Material}
   */
  createMaterial(presetName, options = {}) {
    const preset = this.materialPresets[presetName];
    if (!preset) {
      console.warn(`Material preset "${presetName}" not found`);
      return null;
    }

    // Create a new material with preset values
    let material;
    switch (presetName) {
      case 'screen':
        material = new THREE.MeshStandardMaterial({
          ...preset,
          ...options,
          emissive: new THREE.Color(0x000000),
          emissiveIntensity: 0.0
        });
        break;
      case 'glass':
        material = new THREE.MeshPhysicalMaterial({
          ...this.materialPresets.glass,
          ...options,
          transmission: options.transmission ?? this.materialPresets.glass.transmission,
          thickness: options.thickness ?? this.materialPresets.glass.thickness,
        });
        break;
      default:
        material = new THREE.MeshStandardMaterial({
          ...preset,
          ...options
        });
    }

    // Ensure proper material properties
    material.needsUpdate = true;
    material.envMapIntensity = preset.envMapIntensity ?? 1.0;
    material.metalness = preset.metalness ?? 0.1;
    material.roughness = preset.roughness ?? 0.5;
    if (preset.side !== undefined) {
        material.side = preset.side;
    }

    // Store the material
    this.materials.set(material.uuid, material);
    return material;
  }

  /**
   * Apply a material preset's properties to an existing material
   * @param {THREE.Material} material - Material to update
   * @param {string} presetName - Name of the preset whose properties to apply
   * @param {Object} options - Additional options to override preset values
   */
  applyPresetProperties(material, presetName, options = {}) {
    const preset = this.materialPresets[presetName];
    if (!preset) {
      console.warn(`Material preset "${presetName}" not found`);
      return;
    }
    if (!material) {
      console.warn(`Cannot apply preset "${presetName}", material is null.`);
      return;
    }

    // Apply preset values, then options
    for (const key in preset) {
        // Avoid overwriting complex Color-like properties; handle them explicitly below
        if (key === 'color' || key === 'emissive' || key === 'attenuationColor') continue;
        if (material.hasOwnProperty(key)) {
            material[key] = preset[key];
        } else if (key === 'thickness' && material.isMeshPhysicalMaterial) {
            material.thickness = preset[key];
        }
    }
    for (const key in options) {
        // Avoid overwriting complex Color-like properties; handle them explicitly below
        if (key === 'color' || key === 'emissive' || key === 'attenuationColor') continue;
        if (material.hasOwnProperty(key)) {
            material[key] = options[key];
        } else if (key === 'thickness' && material.isMeshPhysicalMaterial) {
            material.thickness = options[key];
        }
    }

    if (preset.color !== undefined && material.color && material.color.isColor) material.color.set(preset.color);
    if (options.color !== undefined && material.color && material.color.isColor) material.color.set(options.color);
    if (preset.emissive !== undefined && material.emissive && material.emissive.isColor) material.emissive.set(preset.emissive);
    if (options.emissive !== undefined && material.emissive && material.emissive.isColor) material.emissive.set(options.emissive);
    if (preset.attenuationColor !== undefined && material.attenuationColor && material.attenuationColor.isColor) material.attenuationColor.set(preset.attenuationColor);
    if (options.attenuationColor !== undefined && material.attenuationColor && material.attenuationColor.isColor) material.attenuationColor.set(options.attenuationColor);

    material.needsUpdate = true;
    console.log(`Applied properties from preset ${presetName} to material ${material.uuid}`);
  }

  /**
   * Sets the rendering mode for glass materials.
   * @param {string} mode - The desired mode ('realistic' or 'performant').
   * @param {THREE.MeshPhysicalMaterial} glassMaterial - The specific glass material instance to update.
   */
  setGlassMode(mode, glassMaterial) {
      if (!glassMaterial || !glassMaterial.isMeshPhysicalMaterial) {
          console.warn('setGlassMode requires a valid MeshPhysicalMaterial instance.');
          return;
      }
      if (mode !== 'realistic' && mode !== 'performant') {
          console.warn(`Invalid glass mode specified: ${mode}. Use 'realistic' or 'performant'.`);
          return;
      }

      if (this.currentGlassMode === mode) {
          return;
      }

      this.currentGlassMode = mode;
      console.log(`Setting glass mode to: ${mode}`);

      if (mode === 'realistic') {
          this.applyPresetProperties(glassMaterial, 'glass');
      } else {
          this.applyPresetProperties(glassMaterial, 'glassPerformant');
      }
  }

  /**
   * Create a custom shader material for screen backlight
   * @param {THREE.Texture} texture - Texture to use
   * @param {number} brightness - Initial brightness value
   * @param {THREE.Color} color - Base color
   * @returns {THREE.ShaderMaterial}
   */
  createBacklightShaderMaterial(texture, brightness = 1.0, color = new THREE.Color(0xffffff)) {
    const uniforms = {
      diffuseMap: { value: texture },
      brightness: { value: brightness },
      diffuseColor: { value: color }
    };

    const fragmentShader = `
      uniform sampler2D diffuseMap;
      uniform float brightness;
      uniform vec3 diffuseColor;
      varying vec2 vUv;
      
      void main() {
        vec4 texColor = texture2D(diffuseMap, vUv);
        gl_FragColor = vec4(texColor.rgb * brightness * diffuseColor, texColor.a);
      }
    `;

    const vertexShader = `
      varying vec2 vUv;
      
      void main() {
        vUv = uv;
        gl_Position = projectionMatrix * modelViewMatrix * vec4(position, 1.0);
      }
    `;

    const material = new THREE.ShaderMaterial({
      uniforms,
      vertexShader,
      fragmentShader,
      transparent: true
    });

    // Mark as custom shader
    material._isCustomBacklightShader = true;
    this.materials.set(material.uuid, material);
    return material;
  }

  /**
   * Update a material property
   * @param {THREE.Material} material - Material to update
   * @param {string} property - Property name to update
   * @param {*} value - New value
   */
  updateProperty(material, property, value) {
    if (!material) return;

    // Handle color-like properties robustly (accept number, string '#hex', or THREE.Color)
    if (property === 'color') {
      if (material.color && material.color.isColor) {
        if (value instanceof THREE.Color) material.color.copy(value);
        else material.color.set(value);
      }
      material.needsUpdate = true;
      return;
    }
    if (property === 'emissive') {
      if (material.emissive && material.emissive.isColor) {
        if (value instanceof THREE.Color) material.emissive.copy(value);
        else material.emissive.set(value);
      }
      material.needsUpdate = true;
      return;
    }
    if (property === 'attenuationColor') {
      if (material.attenuationColor && material.attenuationColor.isColor) {
        if (value instanceof THREE.Color) material.attenuationColor.copy(value);
        else material.attenuationColor.set(value);
      }
      material.needsUpdate = true;
      return;
    }

    material[property] = value;
    material.needsUpdate = true;
  }

  /**
   * Apply glass material properties - DEPRECATED? Use setGlassMode or direct creation.
   * @param {THREE.Material} material - Material to update
   */
  /*
  applyGlassProperties(material) {
    if (!material) return;
    this.applyPreset(material, 'glass');
  }
  */

  /**
   * Apply standard material properties
   * @param {THREE.Material} material - Material to update
   * @param {Object} options - Additional options
   */
  applyStandardProperties(material, options = {}) {
    if (!material) return;

    this.applyPresetProperties(material, 'plastic', options);
  }

  /**
   * Get a material by UUID
   * @param {string} uuid - Material UUID
   * @returns {THREE.Material|null}
   */
  getMaterial(uuid) {
    return this.materials.get(uuid) || null;
  }

  /**
   * Remove a material
   * @param {string} uuid - Material UUID
   */
  removeMaterial(uuid) {
    const material = this.materials.get(uuid);
    if (material) {
      material.dispose();
      this.materials.delete(uuid);
    }
  }

  /**
   * Dispose of all materials
   */
  dispose() {
    this.materials.forEach(material => {
      material.dispose();
    });
    this.materials.clear();
    this.currentMaterial = null;
  }
} 