import * as THREE from 'three';
import { VideoManager } from './VideoManager.js';
import { TextureManager } from './TextureManager.js';
import { ScreenGeometryManager } from './ScreenGeometryManager.js';
import { ErrorHandler } from '../utils/ErrorHandler.mjs'; // Assuming ErrorHandler is available
// Forward declaration for ModelManager to avoid circular dependencies if necessary
// import ModelManager from './ModelManager.js'; // This might cause issues if ModelManager imports ScreenManager

export default class ScreenManager {
    constructor(screenMesh, screenMaterial) { // Remove ModelManager from constructor initially
    this.screenMesh = screenMesh;
        // Ensure material is properly accessed
        this.screenMaterial = screenMaterial || (screenMesh && screenMesh.material instanceof THREE.Material ? screenMesh.material : null);
    this.backlightIntensity = 1.0;
        this.modelManager = null; // Initialize as null, will be injected
        
        // --- NEW: Store different material types ---
        this.standardMaterial = null;
        this.basicMaterial = null;
        // --- END NEW ---
    
        // --- NEW: Add pending operations queue ---
        this.pendingOperations = [];
        this._meshIsReady = false;
        // --- END NEW ---
    
    // Initialize component managers
    this.videoManager = new VideoManager();
    this.textureManager = new TextureManager();
    this.geometryManager = new ScreenGeometryManager();
    // One-shot flag to force adaptation on the next texture apply
    this._forceAdaptOnce = false;
    // Persistent adapt mode controlled by UI ('Device Shape' = Adapt)
    this.adaptModeEnabled = false;
    // Track currently applied content so UI can reapply
    this.currentContentSource = null;
    this.currentContentValue = null;
    
    /**
     * Clear maps on screen materials to ensure fresh texture assignment.
     * @private
     */
    this._clearScreenMapsOnAllMaterials = () => {
        const clearMaps = (mat) => {
          if (!mat) return;
          if (Object.prototype.hasOwnProperty.call(mat, 'map')) mat.map = null;
          if (Object.prototype.hasOwnProperty.call(mat, 'emissiveMap')) mat.emissiveMap = null;
          mat.needsUpdate = true;
        };
        clearMaps(this.basicMaterial);
        clearMaps(this.standardMaterial);
        const current = this.getScreenMaterial();
        if (current) clearMaps(current);
    };
    
    // Initialize video manager
    this.videoManager.initialize();
    
    // Set screen mesh if provided
    if (screenMesh) {
      this.geometryManager.setScreenMesh(screenMesh);
             // If mesh is provided, try to get material from it if not provided explicitly
             if (!this.screenMaterial && screenMesh.material instanceof THREE.Material) {
                 this.screenMaterial = screenMesh.material;
             }
        }
         // Ensure material is an instance of Material, otherwise set to null
         // Only warn if a material was PASSED but was invalid.
         if (screenMaterial && !(screenMaterial instanceof THREE.Material)) {
             console.warn("ScreenManager received an invalid screenMaterial during initialization.");
             this.screenMaterial = null;
         } else if (!this.screenMaterial) {
             // If no material was passed or derived, just ensure it's null without warning.
             this.screenMaterial = null;
         }
    }

     // --- Inject ModelManager dependency ---
     setModelManager(modelManager) {
        this.modelManager = modelManager;
        
        // --- NEW: Reset mesh ready state when model manager changes ---
        this._meshIsReady = false;
        console.log('[ScreenManager] Model manager set, mesh ready state reset');
        // --- END NEW ---
    }
  
  /**
   * Set the screen mesh and configure it for optimal display
   * @param {THREE.Mesh} mesh - The screen mesh to use
     * @param {THREE.Material} [material] - Optional material to assign
     */
    setScreenMesh(mesh, material) {
        if (!mesh || !(mesh instanceof THREE.Mesh)) {
            console.warn('Attempted to set invalid screen mesh');
            this.screenMesh = null;
            this.screenMaterial = null;
            this.geometryManager.setScreenMesh(null); // Clear in geometry manager too
      return;
    }

        console.log(`[ScreenManager] Setting new screen mesh: ${mesh.name || 'unnamed'}`);

        // Clean up any previous screen mesh references
        if (this.screenMesh && this.screenMesh !== mesh) {
          console.log(`[ScreenManager] Replacing existing screen mesh reference`);
          // Note: Don't dispose geometry/material here as they may be shared
        }

        this.screenMesh = mesh;
        
        // Assign provided material or get from mesh, ensuring it's a Material instance
        let effectiveMaterial = material;
        if (!effectiveMaterial && mesh.material instanceof THREE.Material) {
          effectiveMaterial = mesh.material;
        }
        
        if (effectiveMaterial instanceof THREE.Material) {
          this.screenMaterial = effectiveMaterial;
          
          // If the mesh's material differs from our reference, update the mesh
          if (mesh.material !== this.screenMaterial) {
            console.log(`[ScreenManager] Replacing mesh's material with provided material`);
            mesh.material = this.screenMaterial;
          }
          
          // --- NEW: Initialize standard and basic materials ---
          if (this.screenMaterial instanceof THREE.MeshStandardMaterial) {
              this.standardMaterial = this.screenMaterial;
              // Create a basic material counterpart with proper property copying
              this.basicMaterial = new THREE.MeshBasicMaterial({
                  name: this.standardMaterial.name + '_Basic',
                  map: this.standardMaterial.map,
                  color: this.standardMaterial.color ? this.standardMaterial.color.clone() : new THREE.Color(0xffffff),
                  transparent: this.standardMaterial.transparent,
                  opacity: this.standardMaterial.opacity,
                  side: this.standardMaterial.side,
                  blending: this.standardMaterial.blending,
                  alphaTest: this.standardMaterial.alphaTest
              });
              
              // Store emissive properties for later use when switching back to standard
              this._lastEmissiveColor = this.standardMaterial.emissive ? this.standardMaterial.emissive.clone() : new THREE.Color(0xffffff);
              this._lastEmissiveIntensity = this.standardMaterial.emissiveIntensity !== undefined ? this.standardMaterial.emissiveIntensity : 1.0;
              this._lastEmissiveMap = this.standardMaterial.emissiveMap;
              
              console.log("[ScreenManager] Created basic material with properties from standard material");
          } else {
              // If the initial material isn't standard, just store it as standard
              // and don't create a basic version for now.
              console.warn("[ScreenManager] Initial screen material is not MeshStandardMaterial. Basic toggle might not work as expected.");
              this.standardMaterial = this.screenMaterial;
              this.basicMaterial = null; 
          }
          // --- END NEW ---

        } else {
          console.warn("[ScreenManager] Screen mesh provided, but its material is invalid or missing. Creating default material.");
          // Create a default material if none is valid
           this.screenMaterial = new THREE.MeshStandardMaterial({ 
             color: 0x000000, 
             emissive: 0x050505,
             name: 'DefaultScreenMaterial'
           });
           mesh.material = this.screenMaterial;
           // --- NEW: Also set standard/basic for default case ---
           this.standardMaterial = this.screenMaterial;
           this.basicMaterial = new THREE.MeshBasicMaterial({
               name: 'DefaultScreenMaterial_Basic',
               map: null, // No map initially
               color: 0x000000, // Match default standard color
               transparent: this.standardMaterial.transparent,
               opacity: this.standardMaterial.opacity,
               side: this.standardMaterial.side
           });
           
           // Store default emissive properties 
           this._lastEmissiveColor = new THREE.Color(0x050505); // Match the default emissive
           this._lastEmissiveIntensity = 1.0;
           this._lastEmissiveMap = null;
           // --- END NEW ---
        }

        // Update the geometry manager with the new mesh
        this.geometryManager.setScreenMesh(mesh);
        
        // Save current material state in case we need to reapply
        this._saveScreenMaterialState();
        
        // Apply stored backlight intensity to the new material immediately
        this.setScreenBacklight(this.backlightIntensity);
        
        console.log("[ScreenManager] Screen mesh and material set:", 
          this.screenMesh, this.screenMaterial);
          
        // --- NEW: Mark mesh as ready and execute any pending operations ---
        this._meshIsReady = true;
        if (this.pendingOperations.length > 0) {
            console.log(`[ScreenManager] Executing ${this.pendingOperations.length} pending operations now that mesh is ready`);
            while (this.pendingOperations.length > 0) {
                const operation = this.pendingOperations.shift();
                try {
                    operation();
                } catch (error) {
                    console.error('[ScreenManager] Error executing pending operation:', error);
                }
            }
        }
        // --- END NEW ---
    }
  
  /**
     * Find the screen mesh in the scene - DEPRECATED (kept for compatibility if needed)
   * @returns {THREE.Mesh|null} The screen mesh if found, null otherwise
   */
  findScreenMesh() {
        console.warn("findScreenMesh is deprecated. Use getScreenMesh.");
        return this.getScreenMesh();
  }
  
  /**
   * Get the screen mesh
   * @returns {THREE.Mesh|null}
   */
  getScreenMesh() {
        // Ensure geometryManager has the mesh, could have been reset
    return this.geometryManager.getScreenMesh();
  }

     /**
     * Get the screen geometry manager instance
     * @returns {ScreenGeometryManager}
     */
    getGeometryManager() {
        return this.geometryManager;
    }

  
  /**
   * Get the screen material
   * @returns {THREE.Material|null}
   */
  getScreenMaterial() {
        // Ensure the material reference is current, could have changed after model resize
        if(this.screenMesh && this.screenMesh.material instanceof THREE.Material) {
            this.screenMaterial = this.screenMesh.material;
        } else if (this.screenMesh){
             console.warn("getScreenMaterial: Screen mesh exists but has no valid material.");
             this.screenMaterial = null;
        } else {
             // If screenMesh is null, material should also be null
             this.screenMaterial = null;
        }
    return this.screenMaterial;
  }
  
  /**
   * Load a solid color to the screen
   * @param {string|number} color - Color value (hex string or number)
   */
  async loadSolidColor(color) {
        try {
            // Ensure we have a valid material reference
            const material = this.getScreenMaterial();
            if (!material) {
                console.warn('[ScreenManager] Cannot apply solid color: No screen material available.');
                return;
            }

            // Clear any video and texture state
            this.videoManager.clear();

            // Clear previous maps on all screen materials to prevent stale frames
            this._clearScreenMapsOnAllMaterials();

            // For a solid color, avoid creating a texture. Just set material properties.
            material.map = null;
            material.emissiveMap = null;
            if (material.color && material.color.isColor) {
                material.color.set(color);
            }
            if (material.emissive && material.emissive.isColor) {
                // Use a very dim emissive for an "off" look
                material.emissive.set(0x000000);
            }
            // Keep a small backlight/glow
            // For solid colour, fall backlight to a faint value or zero if using Basic
            const isBasic = this.getScreenMaterial() && this.getScreenMaterial().isMeshBasicMaterial;
            material.emissiveIntensity = isBasic ? 0.0 : Math.min(this.backlightIntensity, 0.1);
            material.needsUpdate = true;

            // Force Basic material for solid colour to ensure unlit, accurate display
            try { this.toggleScreenMaterial(true); } catch (_) {}

            // Do not reset procedural dimensions for solid colour; no aspect to adapt

            console.debug(`[ScreenManager] Solid color applied: ${color}`);
        } catch (error) {
            ErrorHandler.handleError(error, `Failed to load solid color: ${color}`);
            this.resetScreenMaterial();
        }
    }


     /**
     * Internal method to apply a texture to the screen mesh material
     * @private
     * @param {THREE.Texture} texture - Texture to apply
     * @param {string} fitMode - How to fit the texture ('fit', 'fill')
     * @param {boolean} handleAdaptation - Whether to trigger device shape adaptation
     */
    async _applyTextureInternal(texture, fitMode = 'fit', handleAdaptation = true) {
        // Validate input
        if (!texture) {
            console.warn('[ScreenManager] Cannot apply null/undefined texture.');
            return false;
        }
        
        // Validate that we have a screen mesh and material
        let finalMesh = this.getScreenMesh();
        if (!finalMesh) {
            console.warn('[ScreenManager] Cannot apply texture: No screen mesh available.');
            return false;
        }
        
        let finalMaterial = this.getScreenMaterial();
        if (!finalMaterial) {
            console.warn('[ScreenManager] Screen material not available. Creating new material.');
            finalMaterial = new THREE.MeshStandardMaterial({
                color: 0xffffff,
                emissive: 0xffffff,
                name: 'NewScreenMaterial'
            });
            finalMesh.material = finalMaterial;
            this.screenMaterial = finalMaterial;
        }
        
        // Measure aspect ratios (handle image and video correctly)
        let textureAspect = 1.0;
        const isVideoTex = texture.isVideoTexture || (this.videoManager && this.videoManager.videoElement && texture.image === this.videoManager.videoElement);
        if (isVideoTex && this.videoManager && this.videoManager.videoElement && this.videoManager.videoElement.videoWidth > 0) {
            textureAspect = this.videoManager.videoElement.videoWidth / this.videoManager.videoElement.videoHeight;
        } else if (texture.image && texture.image.width && texture.image.height) {
            textureAspect = texture.image.width / texture.image.height;
        }
        console.log(`[ScreenManager] Texture aspect ratio: ${textureAspect}`);
        
        // Get screen aspect ratio from mesh if possible
        let screenAspect = 1.0;
        if (finalMesh.geometry) {
            if (finalMesh.geometry.parameters) {
                // For PlaneGeometry and similar
                const params = finalMesh.geometry.parameters;
                screenAspect = params.width / params.height;
            } else if (finalMesh.geometry.boundingBox) {
                // Use bounding box if available
                const box = finalMesh.geometry.boundingBox;
                const size = new THREE.Vector3();
                box.getSize(size);
                screenAspect = size.x / size.y;
            }
        }
        console.log(`[ScreenManager] Screen aspect ratio: ${screenAspect}`);
        
        // --- Adapt device shape if needed and enabled ---
        if (handleAdaptation && textureAspect > 0 && this.modelManager?.isProceduralModel()) {
            // Determine if we should adapt: either UI checkbox is checked or a one-shot request was made
            let shouldAdapt = false;
            const adaptCheckbox = document.getElementById('adapt-device-shape');
            if (adaptCheckbox && adaptCheckbox.checked) shouldAdapt = true;
            if (this._forceAdaptOnce) { shouldAdapt = true; this._forceAdaptOnce = false; }
            // Consider current 'Device Shape' dropdown value
            const deviceShapeSelect = document.getElementById('device-shape');
            if (deviceShapeSelect && deviceShapeSelect.value === 'adapt') shouldAdapt = true;
            // Respect persistent adapt mode flag set by UI
            if (this.adaptModeEnabled) shouldAdapt = true;
            if (shouldAdapt) {
                console.log(`[ScreenManager] Adapting device to match content aspect ratio: ${textureAspect}`);
                try {
                    // This will resize the device and potentially replace the screen mesh
                    await this.modelManager.resizeProceduralModel(textureAspect);
                    
                    // Re-get the mesh and material after adaptation
                    const newMesh = this.modelManager.getCurrentScreenMesh();
                    const newMaterial = this.modelManager.getCurrentScreenMaterial();
                    
                    if (newMesh && newMesh !== finalMesh) {
                        console.log('[ScreenManager] Screen mesh changed during adaptation. Updating references.');
                        finalMesh = newMesh;
                        if (newMaterial) finalMaterial = newMaterial;
                        // Refresh internal references so later calls see updated mesh/material
                        this._updateMeshAndMaterialReferences();
                        
                        // Re-measure screen aspect ratio
                        if (finalMesh.geometry && finalMesh.geometry.parameters) {
                            screenAspect = finalMesh.geometry.parameters.width / finalMesh.geometry.parameters.height;
                            console.log(`[ScreenManager] Updated screen aspect ratio: ${screenAspect}`);
                        }
                    }
                } catch (error) {
                    ErrorHandler.handleError(error, 'Failed to adapt device shape');
                }
            } else {
                console.log('Adaptation checkbox not checked or not found. Proceeding without adaptation.');
            }
        }
        
            // --- Final texture apply ---
        try {
            // Use the 'fitMode' parameter
            this.textureManager.fitTextureToScreen(texture, textureAspect, screenAspect, fitMode);

                // Apply based on material type to avoid undefined uniforms
                // Capture previous maps for cleanup
                const previousMap = finalMaterial.map || null;
                const previousEmissiveMap = finalMaterial.emissiveMap || null;

                if (finalMaterial.isMeshBasicMaterial) {
                    finalMaterial.map = texture;
                    // Remove emissive fields that Basic material doesn't use
                    if (Object.prototype.hasOwnProperty.call(finalMaterial, 'emissiveMap')) finalMaterial.emissiveMap = null;
                    if (Object.prototype.hasOwnProperty.call(finalMaterial, 'emissiveIntensity')) finalMaterial.emissiveIntensity = 0;
                    if (finalMaterial.color && finalMaterial.color.isColor) {
                        finalMaterial.color.set(0xffffff); // Do not tint the texture
                    }
                } else {
                    // Treat others (Standard/Physical) as emissive screen
                    finalMaterial.map = texture;
                    if (Object.prototype.hasOwnProperty.call(finalMaterial, 'emissiveMap')) finalMaterial.emissiveMap = texture;
                    if (finalMaterial.emissive && finalMaterial.emissive.isColor) {
                        finalMaterial.emissive.set(0xffffff);
                    }
                    if (Object.prototype.hasOwnProperty.call(finalMaterial, 'emissiveIntensity')) {
                        finalMaterial.emissiveIntensity = this.backlightIntensity;
                    }
                    if (finalMaterial.color && finalMaterial.color.isColor) {
                        finalMaterial.color.set(0x000000);
                    }
                }

                // Force GPU upload of the new texture
                if (texture && texture.isTexture) texture.needsUpdate = true;
                finalMaterial.needsUpdate = true; // Ensure material updates

                // Mirror assignment onto the counterpart material so toggling material does not drop the texture
                if (this.standardMaterial && finalMaterial !== this.standardMaterial) {
                    this.standardMaterial.map = texture;
                    if (Object.prototype.hasOwnProperty.call(this.standardMaterial, 'emissiveMap')) this.standardMaterial.emissiveMap = texture;
                    this.standardMaterial.needsUpdate = true;
                }
                if (this.basicMaterial && finalMaterial !== this.basicMaterial) {
                    this.basicMaterial.map = texture;
                    this.basicMaterial.needsUpdate = true;
                }

                // Dispose old canvas textures to avoid stale visuals and leaks
                const disposeIfCanvas = (tex) => {
                    if (tex && tex !== texture && tex.isTexture && tex.isCanvasTexture && typeof tex.dispose === 'function') {
                        try { tex.dispose(); } catch (_) {}
                    }
                };
                disposeIfCanvas(previousMap);
                disposeIfCanvas(previousEmissiveMap);
            console.log(`[ScreenManager] Texture applied to material. Mode: ${fitMode}. type: ${finalMaterial.type}`);
            return true;
        } catch (error) {
            ErrorHandler.handleError(error, 'Failed to apply texture to screen');
            return false;
        }
    }


     /**
      * Public method to apply texture, wraps internal method with adaptation enabled.
      * @param {THREE.Texture} texture - Texture to apply
      * @param {string} fitMode - How to fit the texture ('fit', 'fill')
      */
     async applyTexture(texture, fitMode = 'fit') {
         await this._applyTextureInternal(texture, fitMode, true); // Enable adaptation
     }


    /**
     * Set screen content based on type, handling device adaptation.
     * @param {Object|string|File} content - Content config, URL, Preset key, or File object
     * @param {string} [fitMode='fit'] - How to fit the content ('fit', 'fill')
     * @returns {Promise<boolean>} Whether content was set successfully
     */
    async setScreenContent(content, fitMode = 'fit') {
        console.log(`[ScreenManager] setScreenContent received:`, content, `Fit: ${fitMode}`); // Log received content
        if (!content) {
            console.warn('No screen content specified. Resetting screen.');
            await this.resetScreen(); // Use unified reset
            return false;
        }
        
        // Validate that we have a screen mesh and material
        const screenMesh = this.getScreenMesh();
        if (!screenMesh) {
            console.warn('[ScreenManager] Cannot set screen content: No screen mesh available.');
            
            // --- NEW: Queue the operation for later if mesh not ready ---
            if (!this._meshIsReady) {
                console.log(`[ScreenManager] Queuing setScreenContent for when mesh is ready`, content);
                this.pendingOperations.push(() => this.setScreenContent(content, fitMode));
                
                // Return promise that will resolve when operation is eventually executed
                return new Promise(resolve => {
                    const originalOperation = this.pendingOperations[this.pendingOperations.length - 1];
                    this.pendingOperations[this.pendingOperations.length - 1] = () => {
                        const result = originalOperation();
                        resolve(result);
                        return result;
                    };
                });
            }
            // --- END NEW ---
            
            return false;
        }
        
        const screenMaterial = this.getScreenMaterial();
        if (!screenMaterial) {
            console.warn('[ScreenManager] Cannot set screen content: No screen material available.');
            return false;
        }
        
        let loadedTexture = null;
        let contentType = 'unknown';
        let videoNeedsPlaying = false;
        let handled = false; // Flag to track if content was handled

        // --- Mean Edge Extension UI State ---
        let meanEdgeEnabled = false;
        const meanEdgeCheckbox = document.getElementById('mean-edge-extension');
        if (meanEdgeCheckbox) {
          meanEdgeEnabled = meanEdgeCheckbox.checked;
        } else if (window.USE_MEAN_EDGE_EXTENSION) {
          meanEdgeEnabled = true;
        }

        try {
             // Handle Preset Keys (assuming presets.js maps keys to URLs/configs)
             if (typeof content === 'string' && window.app?.presets?.SAMPLE_SCREENS && window.app.presets.SAMPLE_SCREENS[content]) {
                const preset = window.app.presets.SAMPLE_SCREENS[content];
                content = preset; // Replace key with actual config/URL
                console.log(`Resolved preset key "${Object.keys(window.app.presets.SAMPLE_SCREENS).find(k=>window.app.presets.SAMPLE_SCREENS[k]===content)}" to:`, content);
             }


            // --- Determine Content Type and Load Texture ---
            if (typeof content === 'string') { // Assume URL (Image or Video based on extension?)
                const extension = content.split('.').pop().toLowerCase();
                if (['mp4', 'webm', 'ogg'].includes(extension)) {
                    contentType = 'video';
                    await this.videoManager.loadVideo(content);
                    loadedTexture = this.videoManager.getTexture();
                    videoNeedsPlaying = true;
                } else { // Assume image otherwise
                    contentType = 'image';
                    // Ensure any previously playing video is fully cleared when switching to image
                    this.videoManager.clear();
                    loadedTexture = await this.textureManager.loadImageTexture(content);
                }
            } else if (content instanceof File) { // Handle File object
                 const fileType = content.type.split('/')[0];
                 contentType = fileType; // 'image' or 'video'
                 const url = URL.createObjectURL(content);
                 // Note: Revoke URL later if needed: URL.revokeObjectURL(url);
                 if (contentType === 'image') {
                     loadedTexture = await this.textureManager.loadImageTexture(url);
                 } else if (contentType === 'video') {
                     await this.videoManager.loadVideo(url);
                     loadedTexture = this.videoManager.getTexture();
                     videoNeedsPlaying = true;
                 } else {
                     throw new Error(`Unsupported file type: ${content.type}`);
                 }
            } else if (typeof content === 'object' && content !== null) { // Handle config object
                contentType = content.type;
                switch (contentType) {
                    case 'image':
                        // Ensure any previously playing video is fully cleared when switching to image
                        this.videoManager.clear();
                        loadedTexture = await this.textureManager.loadImageTexture(content.url);
                        break;
                    case 'video':
                        await this.videoManager.loadVideo(content.url);
                        loadedTexture = this.videoManager.getTexture();
                        videoNeedsPlaying = true;
                        break;
                    case 'color':
                        // Ensure any previously playing video is fully cleared when switching to solid color
                        this.videoManager.clear();
                        await this.loadSolidColor(content.value); // Handles its own reset/apply logic
                        // Reduce risk of GPU driver crashes by using performant glass after content change
                        try { this.modelManager?.setGlassQuality('performant'); } catch (e) {}
                        handled = true; // Mark as handled
                        break; // Exit switch, handled flag is set
                    case 'test': // This now generates a checkerboard
                        contentType = 'image'; // Test pattern acts like an image
                        // Ensure any previously playing video is fully cleared when switching to image
                        this.videoManager.clear();
                        // Replace existing texture references so subsequent applies work cleanly
                        loadedTexture = this.textureManager.createCheckerboardTexture();
                        // Ensure fresh GPU upload sequence
                        loadedTexture.needsUpdate = true;
                        console.log(`[ScreenManager] Created checkerboard texture:`, loadedTexture); // Log created texture
                        break;
                    case 'testPattern': // New case for the complex test pattern
                        contentType = 'image'; // Test pattern acts like an image
                        // Ensure any previously playing video is fully cleared when switching to image
                        this.videoManager.clear();
                        loadedTexture = this.textureManager.createTestScreenTexture();
                        loadedTexture.needsUpdate = true;
                        console.log(`[ScreenManager] Created test pattern texture:`, loadedTexture); // Log created texture
                        break;
                    default:
                        throw new Error(`Unknown content type in config object: ${contentType}`);
                }
            } else {
                throw new Error(`Invalid content format: ${typeof content}`);
            }

            // --- Mean Edge Extension logic for video ---
            if (!handled && loadedTexture && contentType === 'video' && meanEdgeEnabled && window.debugTools) {
                // Use mean edge extension rendering
                const videoElement = this.videoManager.videoElement;
                const screenMesh = this.getScreenMesh();
                const videoAspect = videoElement.videoWidth / videoElement.videoHeight;
                let screenAspect = 1.0;
                if (screenMesh && screenMesh.geometry && screenMesh.geometry.parameters) {
                  screenAspect = screenMesh.geometry.parameters.width / screenMesh.geometry.parameters.height;
                }
                // Guard: only apply if both mesh and material are valid
                if (screenMesh && screenMesh.material) {
                    window.debugTools.applyMeanEdgeExtension(videoElement, screenMesh, videoAspect, screenAspect);
                }
                // Set up per-frame update (assume animation loop will call updateExpandedVideoTexture)
                handled = true;
            }

            // --- Apply Loaded Texture (only if not handled otherwise and texture exists) ---
            if (!handled && loadedTexture) { // Check handled flag
                 console.log(`[ScreenManager] Applying texture. Material:`, this.getScreenMaterial(), `Geometry:`, this.geometryManager?.getScreenGeometry()); // Log material/geometry before applying
                 
                 // Ensure we have latest references
                 this._updateMeshAndMaterialReferences();
                 
                await this.applyTexture(loadedTexture, fitMode); // This now handles adaptation
                
                // Reduce risk of GPU driver crashes by using performant glass after any content change
                try { this.modelManager?.setGlassQuality('performant'); } catch (e) {}

                 // If it was a video texture, start playing AFTER texture applied
                 if (videoNeedsPlaying) {
                    await this.playVideo(); // Play video if loaded
                 }
                handled = true; // Mark as handled after applying texture
                // Record applied content info for later reapply
                this.currentContentSource = contentType;
                // Normalize to a stable content descriptor for reuse
                if (typeof content === 'object' && content && content.type) {
                    this.currentContentValue = content;
                } else if (typeof content === 'string') {
                    // Convert URL to typed descriptor if possible
                    const ext = content.split('.').pop().toLowerCase();
                    if (['mp4','webm','ogg'].includes(ext)) this.currentContentValue = { type: 'video', url: content };
                    else this.currentContentValue = { type: 'image', url: content };
                } else {
                    this.currentContentValue = content;
                }
            }

            // --- Final Check and Return ---
            if (handled) {
                // Final safeguard: ensure glass is set to performant after any content change
                try { this.modelManager?.setGlassQuality('performant'); } catch (e) {}
                // After successfully applying content, ensure the correct material type is maintained
                // Get current screen material preference from checkbox if available
                const screenMaterialToggle = document.getElementById('screen-material-toggle');
                if (screenMaterialToggle) {
                    const useBasic = screenMaterialToggle.checked;
                    // Re-apply the material setting to ensure content appears with the right material
                    console.log(`[ScreenManager] Re-applying screen material preference after content set: useBasic=${useBasic}`);
                    this.toggleScreenMaterial(useBasic);
                }
                
                return true; // Success
            } else {
                // This case means no texture was loaded AND it wasn't handled as color
                console.warn('Content processing resulted in no action for:', content);
                await this.resetScreen();
                return false;
            }

        } catch (error) {
            ErrorHandler.handleError(error, `Failed to set screen content (type: ${contentType || typeof content})`);
            await this.resetScreen(); // Reset screen on any error
            return false;
        }
    }


    /**
     * Reset the screen: clear content and reset procedural device shape if applicable.
     */
    async resetScreen() {
        console.log("Resetting screen and potentially device dimensions...");
        this.resetScreenMaterial(); // Clears texture/video, sets material to 'off' state

        // Reset procedural model dimensions if applicable
        if (this.modelManager?.isProceduralModel()) {
            const adaptCheckbox = document.getElementById('adapt-device-shape');
            // Only reset if checkbox exists and is NOT checked (or doesn't exist)
            // If checkbox IS checked, we assume the user wants the adapted state even with no content (maybe show black adapted screen?)
            // Let's refine: Always reset dimensions when content is explicitly cleared/reset.
             console.log("Resetting procedural device dimensions during screen reset.");
             await this.modelManager.resetProceduralModelDimensions();
             this._updateMeshAndMaterialReferences(); // Update internal refs after reset
        }
    }

    /**
     * Reset the screen material to its default 'off' state.
     */
    resetScreenMaterial() {
        const currentMaterial = this.getScreenMaterial(); // Use getter
        if (!currentMaterial) {
            console.log("Screen material already null or invalid, cannot reset.");
            return;
        }

        this.videoManager.clear(); // Stop and clear any video

        // Clear all maps on all screen materials to avoid stale frames
        this._clearScreenMapsOnAllMaterials();

        // Reset to a basic black material, slightly emissive to represent an "off" screen
        currentMaterial.map = null;
        currentMaterial.emissiveMap = null;
        if (currentMaterial.emissive && currentMaterial.emissive.isColor) {
            currentMaterial.emissive.set(0x050505); // Very dark grey emissive
        }
        if (currentMaterial.color && currentMaterial.color.isColor) {
            currentMaterial.color.set(0x000000); // Black base color
        }
        currentMaterial.emissiveIntensity = 1.0; // Keep intensity at 1 for the dark emissive color
        currentMaterial.needsUpdate = true;
        console.log("Screen material reset to default off state.");
    }


    /**
     * Updates internal references to screenMesh and screenMaterial.
     * Should be called after model resize/reset.
     * @private
     */
    _updateMeshAndMaterialReferences() {
        this.screenMesh = this.geometryManager.getScreenMesh(); // Re-get mesh from geometry manager
        this.screenMaterial = this.getScreenMaterial(); // Use getter to update material reference
        console.debug("Updated internal screen mesh/material references.", this.screenMesh, this.screenMaterial);
        
        // Re-check validity
        if(!this.screenMesh) {
            console.warn("_updateMeshAndMaterialReferences: Screen mesh is null after update.");
            this._meshIsReady = false; // Mark as not ready if no mesh is found
        } else {
            this._meshIsReady = true; // Mark as ready if mesh is found
        }
        
        if(!this.screenMaterial) console.warn("_updateMeshAndMaterialReferences: Screen material is null after update.");
    }

  
  /**
   * Play the current video
   */
  async playVideo() {
        try {
    await this.videoManager.play();
        } catch (error) {
            ErrorHandler.handleError(error, "Failed to play video");
        }
  }
  
  /**
   * Pause the current video
   */
  pauseVideo() {
    this.videoManager.pause();
  }
  
  /**
   * Clear the current video
   */
  clearVideo() {
    this.videoManager.clear();
         // Optionally reset the material map if clearing video means black screen
         // this.resetScreenMaterial(); // Consider if this should happen here
  }
  
  /**
     * Set screen backlight intensity (adjusts emissiveIntensity)
     * @param {number} intensity - Backlight intensity (e.g., 0 to 2 or higher)
   */
  setScreenBacklight(intensity) {
        // Always update stored backlight intensity
        this.backlightIntensity = Math.max(0, intensity);
        const currentMaterial = this.getScreenMaterial(); // Use getter
        if (currentMaterial && 'emissiveIntensity' in currentMaterial) {
            currentMaterial.emissiveIntensity = this.backlightIntensity;
            currentMaterial.needsUpdate = true;
        } else {
            // Material not ready or does not support emissiveIntensity
        }
    }


    /**
     * Update the screen (called in animation loop if needed, e.g., for video textures)
   */
  update() {
        // Currently only needed for video textures
        this.videoManager.update(); // Updates video texture if playing
    }

    /**
     * Dispose of all resources used by the ScreenManager
   */
  dispose() {
        console.log('Disposing ScreenManager...');
        this.videoManager.clear(); // Ensure video element is cleaned up
    this.textureManager.dispose();
    this.geometryManager.dispose();

        // Materials are owned by the mesh/model, but break reference
    this.screenMaterial = null;
        this.screenMesh = null;
        this.modelManager = null; // Break reference
        console.log('ScreenManager disposed.');
    }

     // Add other methods like loadImageToScreen, loadVideoToScreen if specific logic needed
     // But setScreenContent is now the main entry point.

  /**
   * Sets the base rotation of the screen mesh itself, relative to its parent model.
   * @param {number} angleDegrees - The desired rotation angle in degrees (0, 90, 180, 270).
   */
  setScreenBaseRotation(angleDegrees) {
    if (!this.screenMesh) {
      console.warn('[ScreenManager] Cannot set screen base rotation: Screen mesh not set yet.');
      // Store the desired angle to apply when mesh is available?
      // this._pendingBaseRotation = angleDegrees;
      return;
    }

    if (typeof angleDegrees !== 'number' || ![0, 90, 180, 270].includes(angleDegrees)) {
      ErrorHandler.handleError(new Error(`Invalid angle for screen base rotation: ${angleDegrees}`), 'Screen Rotation Error');
      return;
    }

    const angleRadians = THREE.MathUtils.degToRad(angleDegrees);

    // Assuming Z is the axis perpendicular to the screen face.
    // Adjust axis if screen geometry is oriented differently.
    this.screenMesh.rotation.z = angleRadians;

    console.log(`[ScreenManager] Set screen base rotation to ${angleDegrees}° (Z-axis: ${angleRadians.toFixed(3)} rad)`);
    
    // We might need to re-apply texture fitting logic after rotating the mesh
    // if the rotation affects how UVs or aspect ratio is interpreted.
    this._reapplyTextureFit();
  }

  /**
   * Helper to re-apply texture fitting based on current state.
   * @private
   */
   _reapplyTextureFit() {
      // Ensure references are up-to-date first
      this._updateMeshAndMaterialReferences(); 
      const currentMaterial = this.getScreenMaterial(); // Use updated material

      // Need the current texture and its aspect ratio (store these when texture is applied)
      const currentTexture = this.textureManager.getCurrentTexture(); // Assume TextureManager tracks this
      const textureAspectRatio = this.textureManager.getCurrentTextureAspectRatio(); // Assume TextureManager tracks this

      if (!currentTexture || !this.screenMesh || !currentMaterial) {
          console.warn("[_reapplyTextureFit] Missing texture, mesh, or material. Skipping.");
          return;
      }

      const screenAspectRatio = this.geometryManager.calculateScreenAspectRatio(); // Use geometryManager
      const displayMode = window.app?.configManager?.getConfigValue('model.displayMode') || 'fit';
      const maintainAspectRatio = window.app?.configManager?.getConfigValue('model.maintainAspectRatio') ?? true;
      // Flip orientation might need to consider base rotation now? Revisit this logic.
      const flipOrientation = window.app?.configManager?.getConfigValue('model.deviceOrientation.flipOrientation') || false;

      console.log('[ScreenManager] Re-applying texture fit after potential screen mesh change.');

      // Use TextureManager to fit the texture
      this.textureManager.fitTextureToScreen(
         currentTexture,
         textureAspectRatio,
         screenAspectRatio,
         displayMode,
         maintainAspectRatio,
         flipOrientation // Pass flip status
      );

      // Apply emissive for video glow (mirror applyTexture behavior)
      currentMaterial.emissiveMap = currentTexture;
      if (currentMaterial.emissive && currentMaterial.emissive.isColor) {
          currentMaterial.emissive.set(0xffffff);
      }
      // Preserve existing backlight intensity on reapply
      currentMaterial.emissiveIntensity = this.backlightIntensity;
      currentMaterial.color.set(0x000000);
      // --- END FIX ---

      this.setScreenBacklight(this.backlightIntensity); // Re-apply backlight intensity (this is fine)
      currentMaterial.needsUpdate = true;
   }

   /**
   * Save the current screen material state for recovery if needed
   * @private
   */
   _saveScreenMaterialState() {
     if (!this.screenMaterial) return;
     
     this._lastScreenMaterialState = {
       map: this.screenMaterial.map,
       emissiveMap: this.screenMaterial.emissiveMap,
       emissive: this.screenMaterial.emissive ? this.screenMaterial.emissive.clone() : null,
       color: this.screenMaterial.color ? this.screenMaterial.color.clone() : null,
       emissiveIntensity: this.screenMaterial.emissiveIntensity
     };
   }
   
   /**
   * Restore the screen material state from saved state
   * @private 
   */
   _restoreScreenMaterialState() {
     if (!this.screenMaterial || !this._lastScreenMaterialState) return;
     
     const state = this._lastScreenMaterialState;
     
     if (state.map) this.screenMaterial.map = state.map;
     if (state.emissiveMap) this.screenMaterial.emissiveMap = state.emissiveMap;
     if (state.emissive) this.screenMaterial.emissive.copy(state.emissive);
     if (state.color) this.screenMaterial.color.copy(state.color);
     if (state.emissiveIntensity !== undefined) this.screenMaterial.emissiveIntensity = state.emissiveIntensity;
     
     this.screenMaterial.needsUpdate = true;
   }

   // --- NEW: Method to toggle between standard and basic materials ---
   /**
    * Toggles the screen material between MeshStandardMaterial and MeshBasicMaterial.
    * @param {boolean} useBasic - If true, uses MeshBasicMaterial; otherwise uses MeshStandardMaterial.
    */
   toggleScreenMaterial(useBasic) {
     const mesh = this.getScreenMesh();
     if (!mesh) {
       console.warn("[ScreenManager] Cannot toggle material: No screen mesh found.");
       
       // --- NEW: Queue the operation for later if mesh not ready ---
       if (!this._meshIsReady) {
           console.log(`[ScreenManager] Queuing toggleScreenMaterial(${useBasic}) for when mesh is ready`);
           this.pendingOperations.push(() => this.toggleScreenMaterial(useBasic));
       }
       // --- END NEW ---
       
       return;
     }

    if (useBasic && this.basicMaterial) {
       if (mesh.material !== this.basicMaterial) {
         // Transfer all relevant properties from the current material to the basic material
         const sourceMaterial = this.standardMaterial || this.screenMaterial;
         
         // Copy texture map or, if that is not set, fall back to the emissiveMap so basic material shows the texture
        const srcMap = sourceMaterial ? (sourceMaterial.map || sourceMaterial.emissiveMap) : null;
        this.basicMaterial.map = srcMap || null;
        // If there is a texture, set white to avoid tinting; otherwise preserve/copy the source color for solid colour mode
        if (this.basicMaterial.color && this.basicMaterial.color.isColor) {
            if (srcMap) this.basicMaterial.color.set(0xffffff);
            else if (sourceMaterial && sourceMaterial.color && sourceMaterial.color.isColor) this.basicMaterial.color.copy(sourceMaterial.color);
            else this.basicMaterial.color.set(0x000000);
        }
         
         // Copy basic properties that MeshBasicMaterial supports
         if (sourceMaterial.opacity !== undefined) this.basicMaterial.opacity = sourceMaterial.opacity;
         if (sourceMaterial.transparent !== undefined) this.basicMaterial.transparent = sourceMaterial.transparent;
         if (sourceMaterial.side !== undefined) this.basicMaterial.side = sourceMaterial.side;
         if (sourceMaterial.blending !== undefined) this.basicMaterial.blending = sourceMaterial.blending;
         if (sourceMaterial.alphaTest !== undefined) this.basicMaterial.alphaTest = sourceMaterial.alphaTest;
         
         // Store emissive properties from standard material (even though basic doesn't support them)
         // so we can restore them if switching back
         if (sourceMaterial.emissive) this._lastEmissiveColor = sourceMaterial.emissive.clone();
         if (sourceMaterial.emissiveIntensity !== undefined) this._lastEmissiveIntensity = sourceMaterial.emissiveIntensity;
         if (sourceMaterial.emissiveMap) this._lastEmissiveMap = sourceMaterial.emissiveMap;
         
         console.log("[ScreenManager] Transferred material properties to Basic material");
         
         this.basicMaterial.needsUpdate = true;
         mesh.material = this.basicMaterial;
         this.screenMaterial = this.basicMaterial; // Update internal reference
         console.log("[ScreenManager] Switched to Basic material.");
       }
     } else if (!useBasic && this.standardMaterial) {
       if (mesh.material !== this.standardMaterial) {
         // Transfer relevant properties from basic to standard material
        this.standardMaterial.map = this.basicMaterial.map || null;
        if (this.basicMaterial.color && this.standardMaterial.color) this.standardMaterial.color.copy(this.basicMaterial.color);
         if (this.basicMaterial.opacity !== undefined) this.standardMaterial.opacity = this.basicMaterial.opacity;
         if (this.basicMaterial.transparent !== undefined) this.standardMaterial.transparent = this.basicMaterial.transparent;
         if (this.basicMaterial.side !== undefined) this.standardMaterial.side = this.basicMaterial.side;
         
         // Restore emissive properties we saved
         if (this._lastEmissiveColor) this.standardMaterial.emissive = this._lastEmissiveColor;
         if (this._lastEmissiveIntensity !== undefined) this.standardMaterial.emissiveIntensity = this._lastEmissiveIntensity;
         if (this._lastEmissiveMap) this.standardMaterial.emissiveMap = this._lastEmissiveMap;
         
         console.log("[ScreenManager] Transferred material properties to Standard material");
         
         this.standardMaterial.needsUpdate = true;
         mesh.material = this.standardMaterial;
         this.screenMaterial = this.standardMaterial; // Update internal reference
         console.log("[ScreenManager] Switched to Standard material.");
       }
     } else {
        console.warn(`[ScreenManager] Cannot switch material. useBasic=${useBasic}, basicMaterial=${!!this.basicMaterial}, standardMaterial=${!!this.standardMaterial}`);
     }
     
     // Ensure the newly assigned material updates
     if(mesh.material) {
         mesh.material.needsUpdate = true;
     }
   }
   // --- END NEW ---
}