/**
 * Manages the scene's environment map, background, and related lighting adjustments.
 */
import * as THREE from 'three';
import { ENVIRONMENTS } from '../presets.js';
import AssetLoader from '../utils/AssetLoader.js';
import { ErrorHandler } from '../utils/ErrorHandler.mjs';
import { DEFAULT_CONFIG } from '../config.js';

export default class EnvironmentManager {
  constructor(scene, renderer, lightingManager) {
    this.scene = scene;
    this.renderer = renderer;
    this.lightingManager = lightingManager;
    this.assetLoader = new AssetLoader();
    this.textureLoader = new THREE.TextureLoader();
    this.currentEnvMap = null;
    this.currentEnvPreset = null;
    this.showBackground = true; // Default to showing background

    this.log = console.log.bind(console, '[EnvManager]');
    this.warn = console.warn.bind(console, '[EnvManager]');
    this.error = console.error.bind(console, '[EnvManager]');
  }

  /**
   * Loads and applies an environment based on the provided preset index or name.
   * @param {number|string} presetIdentifier - Index or name of the environment preset.
   */
  async loadAndApplyEnvironment(presetIdentifier) {
    let preset;
    if (typeof presetIdentifier === 'number' && presetIdentifier >= 0 && presetIdentifier < ENVIRONMENTS.length) {
      preset = ENVIRONMENTS[presetIdentifier];
    } else if (typeof presetIdentifier === 'string') {
      preset = ENVIRONMENTS.find(p => p.key === presetIdentifier);
    }

    if (!preset) {
      this.error(`Environment preset not found: ${presetIdentifier}`);
      return;
    }

    this.currentEnvPreset = preset;
    this.log(`Loading environment: ${preset.name}`);

    // Dispose previous environment map if it exists
    if (this.currentEnvMap && this.currentEnvMap.dispose) {
      this.currentEnvMap.dispose();
      this.currentEnvMap = null;
    }

    try {
      if (preset.envMap === 'procedural') {
        this.log('Generating procedural environment map...');
        this.currentEnvMap = this.generateProceduralEnvMap();
      } else if (preset.envMapFile) {
        this.log(`Loading environment map: ${preset.envMapFile}`);
        this.currentEnvMap = await this.assetLoader.loadEnvironmentMap(preset.envMapFile);
      } else {
        this.warn(`Environment preset '${preset.name}' has no envMap ('procedural') or envMapFile defined.`);
        this.currentEnvMap = null; // Ensure envMap is null if not defined/loaded
      }

      // Apply the loaded (or nullified) map to the scene
      this.scene.environment = this.currentEnvMap;

      // Update background visibility based on current state and loaded map
      this.updateEnvironmentBackgroundVisibility(this.showBackground);

      // Update lighting based on environment preset
      if (this.lightingManager) {
        this.updateLightingForEnvironment(preset, this.lightingManager.lights);
      } else {
        this.warn('LightingManager not available to update light intensities.');
      }

      this.log(`Environment '${preset.name}' applied successfully.`);

    } catch (err) {
      this.error(`Failed to load or apply environment '${preset.name}':`, err);
      ErrorHandler.handleError(err, 'Environment Loading');
      this.currentEnvMap = null;
      this.scene.environment = null;
      // Ensure background is updated even on error
      this.updateEnvironmentBackgroundVisibility(this.showBackground);
    }
  }

  updateEnvironmentBackgroundVisibility(show) {
    this.showBackground = show;
    const env = this.currentEnvPreset;

    // Handle body background first
    if (env && env.background) {
      document.body.style.background = env.background;
    } else {
      document.body.style.background = ''; // Reset if not specified
    }

    // Handle scene background and clear color
    if (env && env.isTransparent) {
      // Case 1: Environment is explicitly transparent
      this.scene.background = null;
      this.renderer.setClearColor(0x000000, 0);
      this.log('Setting background to transparent');
    } else if (this.showBackground && this.currentEnvMap) {
      // Case 2: Show background is true AND we have a valid environment map
      this.scene.background = this.currentEnvMap;
      this.renderer.setClearColor(0x000000, 1); // Use black clear color with env map
      this.log('Setting background to current env map texture');
    } else {
      // Case 3: Fallback (env is null, or showBackground is false, or no currentEnvMap)
      const fallbackColorHex = env?.backgroundColor || DEFAULT_CONFIG.scene.backgroundColor || '#cccccc';
      const fallbackColor = new THREE.Color(fallbackColorHex);
      this.scene.background = fallbackColor;
      this.renderer.setClearColor(fallbackColor, 1); // Use fallback color for clearing
      this.log(`Setting background to fallback color: ${fallbackColorHex}`);
    }
  }

  updateLightingForEnvironment(env, lights) {
    if (!env || !lights) return;
    // Use overall intensity from preset or default to 1.0
    const intensity = env.intensity ?? 1.0;
    // Get specific light intensities map, default to empty object
    const lightIntensities = env.lightIntensities || {};

    // Apply intensities, using specific value OR fallback based on overall intensity
    if (lights.ambient) lights.ambient.intensity = lightIntensities.ambient ?? 0.5 * intensity;
    if (lights.key) lights.key.intensity = lightIntensities.key ?? 1.0 * intensity;
    if (lights.fill) lights.fill.intensity = lightIntensities.fill ?? 0.5 * intensity;
    if (lights.back) lights.back.intensity = lightIntensities.back ?? 0.5 * intensity;
    if (lights.hemi) lights.hemi.intensity = lightIntensities.hemi ?? 0.2 * intensity;
    this.log('Updated light intensities for environment:', env.name);
  }

  generateProceduralEnvMap() {
    const size = 512;
    const renderTarget = new THREE.WebGLCubeRenderTarget(size, {
      generateMipmaps: true,
      minFilter: THREE.LinearMipmapLinearFilter
    });

    const tempScene = new THREE.Scene();
    const tempCamera = new THREE.PerspectiveCamera(90, 1, 0.1, 100);

    // Simple gradient or studio setup for procedural map
    this.createStudioEnvironment(tempScene); // Using the studio setup

    const tempRenderer = new THREE.WebGLRenderer({ antialias: true });
    tempRenderer.setSize(size, size);

    const cubeCamera = new THREE.CubeCamera(0.1, 100, renderTarget);
    cubeCamera.update(tempRenderer, tempScene);

    // Dispose temporary objects
    tempRenderer.dispose();
    tempScene.traverse((object) => {
      if (object.geometry) object.geometry.dispose();
      if (object.material) {
        if (Array.isArray(object.material)) {
          object.material.forEach(material => material.dispose());
        } else {
          object.material.dispose();
        }
      }
    });

    renderTarget.texture.mapping = THREE.CubeReflectionMapping; // Ensure mapping is set
    return renderTarget.texture;
  }

  createStudioEnvironment(scene) {
    // Example studio setup - can be customized
    const geometry = new THREE.SphereGeometry(50, 32, 32);
    geometry.scale(-1, 1, 1); // Invert sphere for inside view

    const materials = {
        top: new THREE.MeshBasicMaterial({ color: 0xffffff, side: THREE.DoubleSide }),
        bottom: new THREE.MeshBasicMaterial({ color: 0x808080, side: THREE.DoubleSide }),
        sides: new THREE.MeshBasicMaterial({ color: 0xc0c0c0, side: THREE.DoubleSide })
    };

    const sphere = new THREE.Mesh(geometry, [materials.top, materials.sides, materials.bottom]); // Simplified example
    scene.add(sphere);

    // Add some lights to make the procedural map interesting
    const light1 = new THREE.PointLight(0xffffff, 1.5, 200);
    light1.position.set(50, 50, 50);
    scene.add(light1);

    const light2 = new THREE.PointLight(0xffffff, 0.8, 200);
    light2.position.set(-50, 30, -50);
    scene.add(light2);

    const ambient = new THREE.AmbientLight(0xffffff, 0.4);
    scene.add(ambient);
  }

  getEnvironments() {
    return ENVIRONMENTS;
  }

  dispose() {
    this.log('Disposing EnvironmentManager...');
    if (this.currentEnvMap && this.currentEnvMap.dispose) {
      this.currentEnvMap.dispose();
      this.currentEnvMap = null;
      this.scene.environment = null;
      this.scene.background = null; // Clear background on dispose
    }
    // Add disposal for other resources if needed
  }
} 