import { PhoneModelBuilder_Rounded } from './PhoneModelBuilder_Rounded.js';

export class PhoneModelBuilder_RoundedThinBezel extends PhoneModelBuilder_Rounded {
  constructor(scene, config = {}) {
    // Set default bezel thickness to a thinner value (0.05) unless overridden
    const thinConfig = { ...config, bezelThickness: config.bezelThickness ?? 0.05 };
    super(scene, thinConfig);
    // Rename the group for clarity
    this.phoneGroup.name = 'phoneModelThinBezel';
  }
} 