/**
 * ConfigManager.js
 * Configuration management for 3D phone web application
 */

import { DEFAULT_CONFIG } from '../config.js';
import { ErrorHandler } from '../utils/ErrorHandler.mjs';

/**
 * Manages configuration settings for the application
 */
export default class ConfigManager {
  constructor() {
    this.config = {};
    this.userConfig = {};
    this.urlConfig = {};
    
    // Initialize configuration
    this.initialize();
  }
  
  /**
   * Initialize configuration - load default, user config, and URL params
   */
  initialize() {
    try {
      // 1. Start with default configuration
      this.config = this.deepClone(DEFAULT_CONFIG);
      console.log('Loaded default config.', this.config);
      
      // 2. Try to load user config (e.g., from window.CUSTOM_CONFIG)
      this.loadUserConfig();
      this.config = this.deepMerge(this.config, this.userConfig);
      console.log('Config after merging user config:', this.config);
      
      // 3. Parse and merge URL parameters
      this.urlConfig = this._parseUrlParams();
      this.config = this.deepMerge(this.config, this.urlConfig);
      console.log('Final config after merging URL params:', this.config);
      
      // Store merged config in window.app for global access
      if (!window.app) {
        window.app = {};
      }
      window.app.config = this.config;
      
      console.log('Configuration initialized successfully');
    } catch (error) {
      // Use the static ErrorHandler method
      ErrorHandler.handleError(error, 'Failed to initialize configuration');
      // Fall back to default config
      this.config = this.deepClone(DEFAULT_CONFIG);
      if (!window.app) window.app = {};
      window.app.config = this.config;
    }
  }
  
  /**
   * Load user configuration from custom-config.js if available
   */
  loadUserConfig() {
    try {
      // Check if custom config exists in global scope
      if (window.CUSTOM_CONFIG && typeof window.CUSTOM_CONFIG === 'object') {
        this.userConfig = window.CUSTOM_CONFIG;
        console.log('Loaded custom configuration from global CUSTOM_CONFIG');
      } else {
        // console.log('No custom configuration found');
        this.userConfig = {};
      }
    } catch (error) {
      console.warn('Error loading custom configuration:', error);
      this.userConfig = {};
    }
  }
  
  /**
   * Parses URL parameters and converts them into a configuration object fragment.
   * @returns {Object} An object representing the configuration overrides from the URL.
   * @private
   */
  _parseUrlParams() {
    const params = new URLSearchParams(window.location.search);
    const urlConfig = {};

    params.forEach((value, key) => {
      if (!key) {
        ErrorHandler.handleError(new Error(`URL parameter has missing key: =${value}`), 'Invalid URL parameter format');
        return; // Skip parameters without keys
      }
      if (!value && value !== '') { // Allow empty string values
        ErrorHandler.handleError(new Error(`URL parameter has missing value: ${key}=`), 'Invalid URL parameter format');
        return; // Skip parameters without values (unless intentionally empty string)
      }

      try {
        this._setConfigValueByPath(urlConfig, key, value);
      } catch (error) {
        ErrorHandler.handleError(error, `Error processing URL parameter: ${key}=${value}`);
      }
    });

    return urlConfig;
  }

  /**
   * Helper function to set a value in a nested object based on a dot-notation path.
   * Converts the value to the appropriate type based on the existing structure or heuristics.
   * @param {Object} obj The object to modify.
   * @param {string} path The dot-notation path (e.g., "controls.autoRotateSpeed").
   * @param {string} value The string value from the URL parameter.
   * @private
   */
  _setConfigValueByPath(obj, path, value) {
    const parts = path.split('.');
    let currentObj = obj;
    let currentDefault = this.config; // Use the currently loaded config (defaults + user) for type checking

    for (let i = 0; i < parts.length - 1; i++) {
      const part = parts[i];
      // STRICTER CHECK: The path part MUST exist in the defaults/user config and be an object
      if (!currentDefault || typeof currentDefault !== 'object' || !(part in currentDefault) || typeof currentDefault[part] !== 'object') {
          // Throw error if the path part doesn't exist or isn't an object in the reference config
          throw new Error(`Invalid configuration path '${path}'. Part '${part}' is invalid or does not lead to an object.`);
      }
      currentDefault = currentDefault[part]; // Move down the default structure

      // Create nested objects in urlConfig if they don't exist
      // This only happens now if the path was valid in the default structure
      if (!currentObj[part] || typeof currentObj[part] !== 'object') {
          currentObj[part] = {};
      }
      currentObj = currentObj[part];
    }

    const finalKey = parts[parts.length - 1];
    
    // Log specifically for deviceOrientation parameters
    if (path.startsWith('model.deviceOrientation')) {
        console.log(`[_setConfigValueByPath] Processing path: ${path}, value: ${value}`);
        console.log(`[_setConfigValueByPath] Target default object (currentDefault):`, currentDefault);
        console.log(`[_setConfigValueByPath] Final key: ${finalKey}`);
    }

    // STRICTER CHECK: Check if the final key exists in the default object at this path
    if (!currentDefault || typeof currentDefault !== 'object' || !(finalKey in currentDefault)) {
       throw new Error(`Invalid configuration path '${path}'. Key '${finalKey}' does not exist at this path in the default configuration.`);
    }
    let targetDefaultValue = currentDefault[finalKey];

    // Log type checking
    if (path.startsWith('model.deviceOrientation')) {
        console.log(`[_setConfigValueByPath] Target default value type: ${typeof targetDefaultValue}, URL value: ${value}`);
    }

    // Convert value based on the type of the default value or heuristics
    let convertedValue;
    const isString = typeof value === 'string';
    const isBooleanString = isString && (value.toLowerCase() === 'true' || value.toLowerCase() === 'false');
    if (typeof targetDefaultValue === 'boolean' || isBooleanString) {
      if (isString) {
        convertedValue = (value.toLowerCase() === 'true');
      } else {
        convertedValue = Boolean(value);
      }
    } else if (typeof targetDefaultValue === 'number' && value !== '' && value !== null && !isNaN(Number(value))) {
      // More strictly convert to number only if default is number AND value is numeric
      convertedValue = (typeof value === 'number') ? value : Number(value);
    } else if (typeof targetDefaultValue === 'string' || typeof targetDefaultValue === 'undefined'){
      // Keep as string if default is string or if the path was somehow invalid earlier (though previous checks should prevent this)
      convertedValue = value;
    } else {
        // Type mismatch between URL value and default config type (e.g. trying to set an object/array via URL)
        throw new Error(`Type mismatch for path '${path}'. Cannot set value '${value}' for a default value of type '${typeof targetDefaultValue}'.`);
    }

    currentObj[finalKey] = convertedValue;
    
    if (path.startsWith('model.deviceOrientation')) {
        console.log(`[_setConfigValueByPath] Set ${path} to:`, convertedValue);
    }
  }

  /**
   * Get the complete configuration
   * @returns {Object} The merged configuration
   */
  getConfig() {
    // Return a clone to prevent external modification?
    // return this.deepClone(this.config);
    return this.config;
  }
  
  /**
   * Update a specific configuration value (primarily for runtime changes, e.g., via UI)
   * @param {string} path - Dot notation path to the config property
   * @param {any} value - The new value to set
   */
  updateConfig(path, value) {
    try {
      // Use the same path setting logic, but modify the main config object
      this._setConfigValueByPath(this.config, path, value);
      // Make sure window.app.config stays in sync
      if (window.app) {
        window.app.config = this.config;
      }
      return this.config;
    } catch (error) {
       ErrorHandler.handleError(error, `Failed to update configuration path '${path}'`);
       return this.config; // Return existing config on error
    }
  }
  
  /**
   * Save the current configuration
   * This could save to localStorage, a server, or output as JSON
   */
  saveConfig() {
    try {
      // For now, just output the config to console
      console.log('Configuration to save:', JSON.stringify(this.config, null, 2));
      
      // In the future, this could save to localStorage or a server
      return true;
    } catch (error) {
      ErrorHandler.handleError(error, 'Failed to save configuration');
      return false;
    }
  }
  
  /**
   * Create a deep clone of an object
   * @param {Object} obj - The object to clone
   * @returns {Object} A deep copy of the object
   */
  deepClone(obj) {
    if (typeof obj !== 'object' || obj === null) return obj; // Handle non-objects
    try {
        return JSON.parse(JSON.stringify(obj));
    } catch (e) {
        console.error("Deep clone failed:", e);
        return {}; // Return empty object on failure
    }
  }
  
  /**
   * Deep merge two objects
   * @param {Object} target - The target object
   * @param {Object} source - The source object
   * @returns {Object} The merged object
   */
  deepMerge(target, source) {
    const output = this.deepClone(target); // Start with a clone of the target

    if (this.isObject(source)) {
        Object.keys(source).forEach(key => {
            const targetValue = output[key]; // Check the key on the output/cloned target
            const sourceValue = source[key];

            if (this.isObject(sourceValue)) {
                // If source value is an object...
                if (this.isObject(targetValue)) {
                    // ...and target value is also an object, merge recursively
                    output[key] = this.deepMerge(targetValue, sourceValue);
                } else {
                    // ...but target value is not an object, source replaces it
                    output[key] = this.deepClone(sourceValue);
                }
            } else if (sourceValue !== undefined) { // Only assign if sourceValue is defined
                // If source value is not an object, it replaces/adds the key in the output
                // No need for Object.assign here, direct assignment is fine
                output[key] = sourceValue;
            }
            // If sourceValue is undefined, we do nothing, preserving the target value
        });
    }
    return output;
  }
  
  /**
   * Check if a value is an object
   * @param {any} item - The value to check
   * @returns {boolean} True if the value is an object
   */
  isObject(item) {
    return (item && typeof item === 'object' && !Array.isArray(item));
  }
} 