import * as THREE from 'three';

export class VideoManager {
  constructor() {
    this.videoElement = null;
    this.videoTexture = null;
    this.currentVideoUrl = null;
    this.isPlaying = false;
    this.playbackState = {
      isAutoplayEnabled: true,
      isMuted: true,
      isLooping: true
    };
  }

  /**
   * Initialize video element and texture
   */
  initialize() {
    this.videoElement = document.createElement('video');
    this.videoElement.playsInline = true;
    this.videoElement.muted = this.playbackState.isMuted;
    this.videoElement.loop = this.playbackState.isLooping;
    // Add additional attributes for better autoplay support
    this.videoElement.setAttribute('playsinline', '');
    this.videoElement.setAttribute('webkit-playsinline', '');
    this.videoElement.setAttribute('autoplay', '');
    this.videoElement.setAttribute('muted', '');
    
    this.videoTexture = new THREE.VideoTexture(this.videoElement);
    this.videoTexture.minFilter = THREE.LinearFilter;
    this.videoTexture.magFilter = THREE.LinearFilter;
    this.videoTexture.format = THREE.RGBAFormat;
    this.videoTexture.colorSpace = THREE.SRGBColorSpace;
  }

  /**
   * Load a video from URL
   * @param {string} url - URL of the video to load
   * @returns {Promise<void>}
   */
  async loadVideo(url) {
    // Guard: If the requested video is already loaded and playing, do nothing
    if (this.currentVideoUrl === url && this.isPlaying && this.videoElement && !this.videoElement.paused) {
      console.debug('Video already loaded and playing:', url);
      return Promise.resolve(); // Return resolved promise
    }
    // Guard: If URL is same and video element exists (already loaded or loading)
    if (this.currentVideoUrl === url && this.videoElement) {
        if (this.videoElement.readyState >= HTMLMediaElement.HAVE_METADATA) { // HAVE_METADATA or higher
            console.debug('Video already loaded (or loading and metadata available):', url);
            // If it's already beyond loading/canplay, resolve, otherwise wait for canplay
            return new Promise((resolve, reject) => {
                if (this.videoElement.readyState >= 3) { // HAVE_FUTURE_DATA / HAVE_ENOUGH_DATA implies canplay
                    resolve();
                } else {
                    const canPlayHandler = () => {
                        this.videoElement.removeEventListener('canplay', canPlayHandler);
                        this.videoElement.removeEventListener('error', errorHandler);
                        resolve();
                    };
                    const errorHandler = (event) => {
                        this.videoElement.removeEventListener('canplay', canPlayHandler);
                        this.videoElement.removeEventListener('error', errorHandler);
                        console.error('Error event during video load while waiting for canplay:', event);
                        reject(event.target.error || new Error('Video load error while waiting for canplay'));
                    };
                    this.videoElement.addEventListener('canplay', canPlayHandler);
                    this.videoElement.addEventListener('error', errorHandler);
                }
            });
        }
        // If this.currentVideoUrl === url but no videoElement or readyState is too low, proceed to load.
    }

    this.currentVideoUrl = url; // Set current URL
    
    // Ensure videoElement is initialized
    if (!this.videoElement) {
        this.initialize(); // This also creates this.videoTexture
    }

    return new Promise((resolve, reject) => {
      try {
        // Apply settings each time a video is loaded
        this.videoElement.loop = this.playbackState.isLooping;
        this.videoElement.muted = true; // Always mute for autoplay
        this.videoElement.playsInline = true;
        this.videoElement.setAttribute('playsinline', '');
        this.videoElement.setAttribute('webkit-playsinline', '');
        this.videoElement.preload = 'auto';
        
        const canPlayHandler = () => {
          this.videoElement.removeEventListener('canplay', canPlayHandler);
          this.videoElement.removeEventListener('error', errorHandler);
          console.debug('Video canplay event fired:', url);
          resolve();
        };

        const errorHandler = (event) => {
          this.videoElement.removeEventListener('canplay', canPlayHandler);
          this.videoElement.removeEventListener('error', errorHandler);
          console.error('Error loading video:', event.target.error || event);
          this.currentVideoUrl = null; // Reset URL on error
          reject(event.target.error || new Error('Video load error'));
        };

        this.videoElement.addEventListener('canplay', canPlayHandler);
        this.videoElement.addEventListener('error', errorHandler);
        
        // Load the video
        this.videoElement.src = url;
        this.videoElement.load(); // Initiates loading, 'canplay' will fire when ready
      } catch (error) {
        console.error('Error setting up video load:', error);
        this.currentVideoUrl = null; // Reset URL on error
        reject(error);
      }
    });
  }

  /**
   * Play the video
   * @param {boolean} forcePlay - Whether to force play even if autoplay is disabled
   */
  async play(forcePlay = false) {
    if (!this.videoElement) return;

    // Guard: If already playing, do nothing
    if (!this.videoElement.paused && !this.videoElement.ended) {
      this.isPlaying = true;
      console.debug('Video is already playing, skipping play() call.');
      return;
    }

    if (forcePlay || this.playbackState.isAutoplayEnabled) {
      try {
        // Ensure video is muted and has playsinline to maximize autoplay chances
        this.videoElement.muted = true;
        this.videoElement.playsInline = true;
        this.videoElement.setAttribute('playsinline', '');
        this.videoElement.setAttribute('webkit-playsinline', '');
        
        // Set lower playback rate initially to help with autoplay
        const originalPlaybackRate = this.videoElement.playbackRate;
        this.videoElement.playbackRate = 0.1;
        
        await this.videoElement.play();
        
        // If successful, restore original playback rate
        this.videoElement.playbackRate = originalPlaybackRate;
        this.isPlaying = true;
        console.log('Video playback started successfully');
      } catch (error) {
        console.warn('Failed to autoplay video:', error);
        
        // Try again with a slight delay (helps in some browsers)
        setTimeout(() => {
          this.videoElement.play()
            .then(() => {
              this.isPlaying = true;
              console.log('Video playback started after delay');
            })
            .catch(delayError => {
              console.warn('Failed to play video after delay:', delayError);
              
              // Set up interaction-based playback
              const playOnInteraction = () => {
                this.videoElement.play()
                  .then(() => {
                    this.isPlaying = true;
                    document.removeEventListener('click', playOnInteraction);
                    document.removeEventListener('touchstart', playOnInteraction);
                    console.log('Video started after user interaction');
                  })
                  .catch(err => console.error('Error playing video on interaction:', err));
              };

              document.addEventListener('click', playOnInteraction);
              document.addEventListener('touchstart', playOnInteraction);
            });
        }, 200);
      }
    }
  }

  /**
   * Pause the video
   */
  pause() {
    if (this.videoElement) {
      this.videoElement.pause();
      this.isPlaying = false;
    }
  }

  /**
   * Update video playback settings
   * @param {Object} settings - New playback settings
   */
  updatePlaybackSettings(settings) {
    this.playbackState = { ...this.playbackState, ...settings };
    
    if (this.videoElement) {
      this.videoElement.muted = this.playbackState.isMuted;
      this.videoElement.loop = this.playbackState.isLooping;
    }
  }

  /**
   * Clear video resources
   */
  clear() {
    if (this.videoElement) {
      this.videoElement.pause();
      this.videoElement.src = '';
      this.videoElement.load();
      this.isPlaying = false;
      this.currentVideoUrl = null;
    }
  }

  /**
   * Get the video texture
   * @returns {THREE.VideoTexture|null}
   */
  getTexture() {
    return this.videoTexture;
  }

  /**
   * Update the video texture
   */
  update() {
    if (this.videoTexture && this.isPlaying) {
      this.videoTexture.needsUpdate = true;
    }
  }
} 