import * as THREE from 'three';
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls.js';
import { DeviceOrientationControls } from '../utils/DeviceOrientationControls.js';
import { ErrorHandler } from '../utils/ErrorHandler.mjs';

export default class AnimationManager {
    constructor(scene, camera, renderer) {
        this.scene = scene;
        this.camera = camera;
        this.renderer = renderer;
        this.controls = null;
        this.interactableModel = null; // Reference to the model to rotate
        
        // Animation properties
        this.animations = new Map();
        this.isAnimating = false;
        this.animationStyle = 'none'; // Default to no animation if config fails to apply
        this.animationTime = 0;
        this.pingPongDirection = 1;
        this.lastAnimationUpdate = performance.now();

        // Animation active state (controlled by UI checkbox)
        this.isActive = true; // Start active by default (matches checkbox checked state)

        // Device orientation controls (for mobile)
        this.deviceOrientationControls = null;
        this.isUsingDeviceOrientation = false;

        // Debugging
        this.animationDebug = true; // Turn on debug logging to help troubleshoot
        this._lastLogTime = null;
        this._lastRenderLog = null;
        
        // Set a default speed for animations
        this.deviceAutoRotateSpeed = 15; // Add default speed value for animations
        
        // Base device orientation (degrees) applied as Z-rotation offset
        this.baseDeviceOrientationDeg = 0;
    }
    
    setControls(controls) {
        this.controls = controls;
    }

    setInteractableModel(model) {
        this.interactableModel = model;
        console.log('Interactable model set in AnimationManager:', this.interactableModel);
    }

    // Allow UI/config to set the base device orientation in degrees
    setBaseDeviceOrientation(angleDeg) {
        if (!Number.isFinite(angleDeg)) return;
        this.baseDeviceOrientationDeg = angleDeg;
        // Apply immediately if a model is present
        if (this.interactableModel) {
            const rad = THREE.MathUtils.degToRad(-this.baseDeviceOrientationDeg);
            // Keep X/Y as-is; set Z to the base angle (animations will add on top)
            this.interactableModel.rotation.z = rad;
        }
    }

    setActive(isActive) {
        this.isActive = isActive;
        console.log(`Animation active state set to: ${this.isActive}`);
        // If activating, maybe reset time?
        if (isActive) {
            this.lastAnimationUpdate = performance.now(); // Prevent large jump
        }
    }

    setDeviceAutoRotateSpeed(speed) {
        // Ensure speed is a valid number, default to 0 if not
        this.deviceAutoRotateSpeed = Number.isFinite(speed) ? speed : 0;
        console.log('Device auto-rotate speed set:', this.deviceAutoRotateSpeed);
    }
    
    setAnimationStyle(style) {
        console.log(`[AnimationManager] Setting animation style to: ${style}`);
        // Reset animation state
        this.pingPongDirection = 1;
        this.lastAnimationUpdate = performance.now();
        this.animationTime = 0; // Reset time on style change

        // Store the animation style
        this.animationStyle = style;

        // Map legacy style names if necessary (though likely not needed now)
        // if (style === 'ping-pong-180') {
        //     this.animationStyle = 'ping-pong';
        // }

        // Ensure OrbitControls auto-rotate is always disabled for these styles
        if (this.controls) {
             // Always disable OrbitControls autoRotate when a specific style is set by this method
             if (this.controls.autoRotate) {
                 console.log('[AnimationManager] Disabling OrbitControls.autoRotate due to setAnimationStyle.');
                 this.controls.autoRotate = false;
             }
        }

        // Reset model rotation to face forward, but preserve base orientation on Z
        if (this.interactableModel) {
            const baseZ = THREE.MathUtils.degToRad(-this.baseDeviceOrientationDeg);
            this.interactableModel.rotation.set(0, 0, baseZ);
        }

        // console.log(`Animation style set to: ${style}, Device auto-rotate: ${this.isDeviceAutoRotating}`); // Old log, potentially inaccurate
    }
    
    enableDeviceOrientation() {
        try {
            // Only create if not already existing
            if (!this.deviceOrientationControls) {
                this.deviceOrientationControls = new DeviceOrientationControls(this.camera);
            }
            
            // Disable orbit controls if they exist
            if (this.controls) {
                this.controls.enabled = false;
            }
            
            this.isUsingDeviceOrientation = true;
            
            // Request device orientation permissions if needed
            if (typeof DeviceOrientationEvent !== 'undefined' && 
                typeof DeviceOrientationEvent.requestPermission === 'function') {
                DeviceOrientationEvent.requestPermission()
                    .then(permissionState => {
                        if (permissionState === 'granted') {
                            console.log('Device orientation permissions granted');
                        } else {
                            console.warn('Device orientation permissions denied');
                            this.disableDeviceOrientation();
                        }
                    })
                    .catch(error => {
                        ErrorHandler.handleError(error, 'Error requesting device orientation permissions');
                        this.disableDeviceOrientation();
                    });
            }
            
        } catch (error) {
            ErrorHandler.handleError(error, 'Error enabling device orientation controls');
            this.disableDeviceOrientation();
        }
    }
    
    disableDeviceOrientation() {
        this.isUsingDeviceOrientation = false;
        
        // Re-enable orbit controls if they exist
        if (this.controls) {
            this.controls.enabled = true;
        }
        
        // Reset animation style to none
        this.setAnimationStyle('none');
        // Should we re-enable orbit controls autoRotate here based on config?
        // if (this.controls && window.app?.config?.controls?.animate) {
        //     this.controls.autoRotate = true;
        // }
    }
    
    updateAnimation(deltaTime) {
        // Update animation time
        // const now = performance.now(); // REMOVED
        // const deltaTime = (now - this.lastAnimationUpdate) / 1000; // REMOVED
        // this.lastAnimationUpdate = now; // REMOVED

        // Only increment time if animation is active
        if (this.isActive && this.animationStyle !== 'none') { 
            this.animationTime += deltaTime; // Use passed deltaTime
        }

        // Handle device orientation controls if active (takes precedence)
        if (this.isUsingDeviceOrientation && this.deviceOrientationControls) {
            this.deviceOrientationControls.update();
            return;
        }

        // Get the model to animate
        const model = this.interactableModel;
        if (!model) {
            // console.warn('[AnimationManager.updateAnimation] No model available for animation.'); // Optional warning
            return; // No model to animate
        }

        // Only apply animations if the 'Animate' checkbox is checked
        if (!this.isActive) {
            return; // Skip animation updates if not active
        }

        // Define base speed factor - ADJUST THIS MULTIPLIER FOR DESIRED SPEED
        const speedFactor = Math.max(0.2, (this.deviceAutoRotateSpeed || 15) * 0.015); // Ensure we have a minimum speed value

        // Debug logging disabled to improve performance

        // Handle different animation styles (always offset by base device orientation on Z)
        switch (this.animationStyle) {
            case 'none':
                // No animation - model stays where it is unless manually moved
                break;
                
            case 'continuous-rotate': // Added case
                // Simple rotation on Y axis
                model.rotation.y += speedFactor * deltaTime;
                break;

            case 'ping-pong-90':
                // Oscillate +/- 45 degrees (PI/4 radians)
                model.rotation.y = Math.cos(this.animationTime * speedFactor * 2.5) * (Math.PI / 4);
                model.rotation.x = 0; // Ensure no x rotation
                break;

            case 'ping-pong-45':
                // Oscillate +/- 22.5 degrees (PI/8 radians)
                model.rotation.y = Math.cos(this.animationTime * speedFactor * 3.0) * (Math.PI / 8);
                model.rotation.x = 0; // Ensure no x rotation
                break;

            case 'gentle-sway':
                // Oscillate +/- 10 degrees (PI/18 radians)
                model.rotation.y = Math.cos(this.animationTime * speedFactor * 0.8) * (Math.PI / 18);
                model.rotation.x = 0; // Ensure no x rotation
                break;

            // case 'full-spin-pause':
                // TODO: Implement state machine for spin-pause logic
                // Needs to track state (spinning, paused), rotation progress, pause duration.
                // model.rotation.y += speedFactor * deltaTime; // Example spin part
                // break;

            case 'look-around':
                // Front-facing wobble effect with limited Y rotation
                // Gentle oscillation on X axis (+/- 15 degrees = PI/12)
                model.rotation.x = Math.sin(this.animationTime * speedFactor * 0.7) * (Math.PI / 12);
                // Slight oscillation on Z axis for a more dynamic wobble
                model.rotation.z = Math.cos(this.animationTime * speedFactor * 1.2) * (Math.PI / 18) + THREE.MathUtils.degToRad(-this.baseDeviceOrientationDeg);
                // Limited Y rotation to keep screen visible (+/- 20 degrees = PI/9)
                model.rotation.y = Math.sin(this.animationTime * speedFactor * 0.5) * (Math.PI / 9);
                break;

            case 'vertical-tilt':
                // Tilt on X (+/- 30 degrees = PI/6)
                model.rotation.x = Math.cos(this.animationTime * speedFactor * 1.5) * (Math.PI / 6);
                model.rotation.y = 0; // Ensure no y rotation
                break;

            case 'zoom-spin':
                // Continuous rotation on Y
                model.rotation.y += speedFactor * deltaTime;
                model.rotation.x = 0; // Ensure no x rotation
                // Zoom camera
                const initialZ = 5; // Base camera Z position
                const zoomAmount = 0.5; // How much to zoom in/out
                const zoomFrequency = speedFactor * 0.5; 
                const zoomFactor = Math.sin(this.animationTime * zoomFrequency) * zoomAmount;
                // Make sure camera exists before trying to set position
                if (this.camera) { 
                    this.camera.position.z = initialZ - zoomFactor;
                } else {
                    console.warn('[AnimationManager] Camera reference missing for zoom-spin.');
                }
                break;

            default:
                console.warn(`[AnimationManager] Unknown animation style: ${this.animationStyle}`);
                // Treat as 'none'
                break;
        }
    }
    
    resetSceneRotation() {
        // This might still be useful if some styles manipulate the scene directly
        // Reset scene rotation to face forward
        this.scene.rotation.set(0, 0, 0);
        // Consider resetting model rotation too? Maybe optional?
        if (this.interactableModel) { this.interactableModel.rotation.set(0,0,0); } // Added reset here too
    }
    
    animate() {
        // Wrap animation loop in try-catch to pinpoint errors
        try {
            // Schedule the next frame
            requestAnimationFrame(this.animate.bind(this)); // Bind 'this' correctly

            // Calculate time delta since last frame
            const now = performance.now();
            const deltaTime = (now - this.lastAnimationUpdate) / 1000; // Convert ms to seconds
            this.lastAnimationUpdate = now; // Update last timestamp
            
            // --- Mean Edge Extension per-frame update ---
            if (window.debugTools && window.debugTools.useExpandedVideoTexture) {
                // Get the current screen mesh from ScreenManager if available
                const screenManager = window.app?.screenManager;
                const screenMesh = screenManager ? screenManager.getScreenMesh() : null;
                if (screenMesh) {
                    window.debugTools.updateExpandedVideoTexture(screenMesh);
                }
            }
            // --- End Mean Edge Extension update ---

            // Update custom animations (ping-pong, sway etc.) based on current style and delta time
            this.updateAnimation(deltaTime); 
            
            // Update orbit controls. This handles damping, zoom, pan, and also auto-rotation if enabled.
            // We disabled auto-rotation in setAnimationStyle, so this call mainly handles damping/manual control updates.
            if (this.controls) { // Removed this.controls.enabled check
                this.controls.update();
            }
            
            // Render the scene if components are valid
            if (this.renderer && this.scene && this.camera) {
                this.renderer.render(this.scene, this.camera);
            } else {
                 console.error('[AnimationManager.animate] Cannot render: missing components.',
                             'renderer:', !!this.renderer,
                             'scene:', !!this.scene,
                             'camera:', !!this.camera);
            }
        } catch (error) {
            console.error('[AnimationManager.animate] Caught error during animation loop:', error);
            // Rethrow to allow global error handlers to catch it
            throw error;
        }
    }
    
    startAnimationLoop() {
        // Start the animation loop if not already running
        if (this.isAnimating) return; // Prevent multiple loops
        
        console.log('[AnimationManager.startAnimationLoop] Starting animation loop...');
        this.isAnimating = true;
        this.lastAnimationUpdate = performance.now(); // Initialize last time when starting
        
        // Bind the animate function to ensure 'this' context is correct
        this._boundAnimate = this.animate.bind(this); 
        
        // Start the loop
        this._boundAnimate(); 
    }
    
    stopAnimationLoop() { // Renamed for clarity
        console.log('[AnimationManager.stopAnimationLoop] Stopping animation loop.');
        this.isAnimating = false;
        // We don't need to explicitly cancel the frame request, 
        // the check `if (!this.isAnimating) return;` inside animate() stops the chain.
        // However, if using a variable for request ID: cancelAnimationFrame(this.animationFrameId);
    }
    
    // Removed redundant startAnimation/stopAnimation which were less clear than start/stopAnimationLoop
}