// For backward compatibility - keeping the old name as an alias
export const phoneModelConfig = phoneVariantConfig;

// Function to create a phone 3D model using the configuration
export function createPhoneModel(scene) {
  // Use the imported Box builder instead of Rounded
  const phoneBuilder = new PhoneModelBuilder_Box(scene, phoneVariantConfig); 
  const modelData = phoneBuilder.build();
  
  // Ensure builder reference is properly stored in userData
  if (modelData.model && (!modelData.model.userData || !modelData.model.userData.builder)) {
    console.log('Setting builder reference in model userData');
    modelData.model.userData = modelData.model.userData || {};
    modelData.model.userData.builder = phoneBuilder;
  }
  
  // Include the builder in the returned object
  modelData.builder = phoneBuilder;
  
  return modelData;
} 