/**
 * Factory function to create different types of device 3D models.
 * Determines whether to load a GLTF/GLB or use a procedural builder.
 *
 * @param {string} category - The type/category of device model to create (e.g., 'phone').
 * @param {object} config - Configuration object containing details like source, variantId, url, scene, etc.
 * @returns {Promise<object|null>} A promise resolving to an object containing the model, materials map, builder instance (if procedural), and screen mesh, or null on failure.
 */
export async function createModel(category, config) {
    console.log(`[createModel Factory] Called for category: ${category}. Config:`, config);

    if (!config || !config.scene) {
        ErrorHandler.handleError(new Error('Scene is required in config for createModel'), 'Model Creation Failed');
        console.error('[createModel Factory] Missing required scene in configuration.', config);
        return null;
    }
    
    const source = config.source || 'procedural'; // Default to procedural if source isn't specified
    console.log(`[createModel Factory] Determined source: ${source}`);

    try {
        if (source === 'gltf' || source === 'glb') {
            if (!config.url) {
                ErrorHandler.handleError(new Error('URL is required for GLTF/GLB model source'), 'Model Creation Failed');
                console.error('[createModel Factory] Missing URL for GLTF/GLB source.', config);
                return null;
            }
            console.log(`[createModel Factory] Attempting to load GLTF model from URL: ${config.url}`);
            const loader = AssetLoader.getLoader(GLTFLoader);
            const gltf = await loader.loadAsync(config.url);
            console.log(`[createModel Factory] GLTF loaded successfully from ${config.url}`, gltf);
            
            const model = gltf.scene;
            let screenMesh = null;
            let screenMaterial = null;
            const materialsMap = new Map();
            
            model.traverse((child) => {
                if (child.isMesh) {
                    // Store materials by name if available, otherwise UUID
                    const materialKey = child.material?.name || child.material?.uuid || `mat_${child.uuid}`;
                    if (child.material) {
                        materialsMap.set(materialKey, child.material);
                    }
                    // Identify screen mesh (common names)
                    if (child.name.toLowerCase().includes('screen') || child.name.toLowerCase().includes('display')) {
                        screenMesh = child;
                        screenMaterial = child.material;
                        console.log(`[createModel Factory] Identified screen mesh in GLTF: ${child.name}`);
                    }
                }
            });
            
            if (!screenMesh) {
                console.warn(`[createModel Factory] Could not automatically find a screen mesh named 'screen' or 'display' in the loaded GLTF: ${config.url}`);
            }
            
            console.log('[createModel Factory] Returning loaded GLTF model data.');
            return { 
                model: model, 
                materials: materialsMap, 
                builder: null, // No builder for GLTF
                screen: screenMesh, 
                screenMaterial: screenMaterial
            };
            
        } else if (source && source.startsWith('procedural')) {
            console.log(`[createModel Factory] Attempting to create procedural model for category: ${category} with source: ${source}`);
            let builder = null;
            switch (category) {
                case 'phone':
                    // Check for both variant and variantId to make it more robust
                    const variantId = config.variantId || config.variant || 'phone_box_standard'; // Default to box standard
                    
                    // Check for thin bezel variant first
                    if (source === 'procedural_rounded_thinbezel' || variantId.includes('thinbezel')) {
                        console.log(`[createModel Factory] Instantiating PhoneModelBuilder_RoundedThinBezelSmallCorner for variant ${variantId}...`);
                        builder = new PhoneModelBuilder_RoundedThinBezelSmallCorner(config.scene, { ...PhoneConfig.phoneModelConfig, ...config });
                    } else if (source === 'procedural_rounded' || variantId.includes('rounded')) {
                        console.log(`[createModel Factory] Instantiating PhoneModelBuilder_Rounded for variant ${variantId}...`);
                        builder = new PhoneModelBuilder_Rounded(config.scene, { ...PhoneConfig.phoneModelConfig, ...config });
                    } else if (source === 'procedural_box' || variantId.includes('box')) {
                        console.log(`[createModel Factory] Instantiating PhoneModelBuilder_Box for variant ${variantId}...`);
                        builder = new PhoneModelBuilder_Box(config.scene, { ...PhoneConfig.phoneModelConfig, ...config });
                    } /* else if (variantId.includes('beveled')) {
                        console.log(`[createModel Factory] Instantiating PhoneModelBuilder_Beveled for variant ${variantId}...`);
                        builder = new PhoneModelBuilder_Beveled(config.scene, { ...PhoneConfig.phoneModelConfig, ...config });
                    } else if (variantId.includes('curved')) {
                        console.log(`[createModel Factory] Instantiating PhoneModelBuilder_Curved for variant ${variantId}...`);
                        builder = new PhoneModelBuilder_Curved(config.scene, { ...PhoneConfig.phoneModelConfig, ...config });
                    } */ else { // Default to Box builder if unknown variant or source
                        console.log(`[createModel Factory] Defaulting to PhoneModelBuilder_Box for unknown variant/source: ${variantId} / ${source}...`);
                        builder = new PhoneModelBuilder_Box(config.scene, { ...PhoneConfig.phoneModelConfig, ...config });
                    }
                    break;
                case 'tablet':
                    const tabletVariantId = config.variantId || 'default_tablet';
                    console.log(`[createModel Factory] Instantiating TabletModelBuilder for variant ${tabletVariantId}...`);
                    // Assuming TabletModelBuilder exists in ./tablet/
                    // builder = new TabletModelBuilder(config.scene, { ...TabletConfig.tabletModelConfig, ...config }); 
                    ErrorHandler.handleError(new Error('Procedural tablet models not yet implemented'), 'Model Creation Skipped');
                    console.warn(`[createModel Factory] Procedural tablet model creation not implemented for variant: ${tabletVariantId}`);
                    return null; // Indicate not implemented
                    // break; // Keep break commented out until implemented
                case 'laptop':
                    const laptopVariantId = config.variantId || 'default_laptop';
                    console.log(`[createModel Factory] Instantiating LaptopModelBuilder for variant ${laptopVariantId}...`);
                     // Assuming LaptopModelBuilder exists in ./laptop/
                    // builder = new LaptopModelBuilder(config.scene, { ...LaptopConfig.laptopModelConfig, ...config });
                    ErrorHandler.handleError(new Error('Procedural laptop models not yet implemented'), 'Model Creation Skipped');
                    console.warn(`[createModel Factory] Procedural laptop model creation not implemented for variant: ${laptopVariantId}`);
                    return null; // Indicate not implemented
                    // break; // Keep break commented out until implemented
                default:
                    ErrorHandler.handleError(new Error(`Unsupported model category: ${category}`), 'Model Creation Failed');
                    console.error(`[createModel Factory] Unsupported model category: ${category}`);
                    return null;
            }
            
            console.log('[createModel Factory] Calling builder.build()...');
            let modelData = null; // Initialize to null
            try {
                modelData = await builder.build(); // Assuming build is async
                console.log('[createModel Factory] builder.build() returned:', modelData);
            } catch (buildError) {
                console.error('[createModel Factory] Error executing builder.build():', buildError);
                ErrorHandler.handleError(buildError, `Procedural model builder failed during build for category ${category}`);
                // Attempt to dispose the builder if it failed partially
                if (builder && typeof builder.dispose === 'function') {
                   builder.dispose();
                }
                return null; // Return null on build error
            }
            
            if (!modelData || !modelData.model) {
                 console.error('[createModel Factory] Procedural builder failed to return a valid model structure (modelData or modelData.model is null/undefined). Returned data:', modelData);
                 return null;
            }

            console.log('[createModel Factory] Procedural model built successfully. Returning data.');
            // Ensure the builder instance is included in the returned object
            return { 
                ...modelData, // Spread result from builder (includes model, materials, screen, screenMaterial)
                builder: builder // Add the builder instance itself
            };
            
        } else {
            ErrorHandler.handleError(new Error(`Unsupported model source: ${source}`), 'Model Creation Failed');
            console.error(`[createModel Factory] Unsupported model source specified: ${source}`);
            return null;
        }
    } catch (error) {
        ErrorHandler.handleError(error, `Failed to create model (Category: ${category}, Source: ${source})`);
        console.error(`[createModel Factory] Error during model creation (Category: ${category}, Source: ${source}):`, error);
        return null;
    }
}

// Factory function to apply configuration to a 3D model
// This might be useful for generic transforms, but less so for materials now
export function applyModelConfig(model, config) {
  if (!model || !config) return;

  // Apply position
  if (config.position) {
    model.position.set(config.position.x, config.position.y, config.position.z);
  }

  // Apply rotation
  if (config.rotation) {
    model.rotation.set(config.rotation.x, config.rotation.y, config.rotation.z);
  }

  // Apply scale
  if (config.scale) {
    model.scale.set(config.scale.x, config.scale.y, config.scale.z);
  }

  // Apply material properties - CAUTION: This generic application might conflict
  // with specific material handling in the builder or loaded models. Might need removal or refinement.
  /* 
  if (config.materials) {
    model.traverse(child => {
      if (child.isMesh && child.material) {
        const material = Array.isArray(child.material) ? child.material[0] : child.material;
        if (material) {
          Object.assign(material, config.materials); // Overwrites properties
          material.needsUpdate = true;
        }
      }
    });
  }
  */
}