import { PhoneModelBuilder_RoundedThinBezel } from './PhoneModelBuilder_RoundedThinBezel.js';

export class PhoneModelBuilder_RoundedThinBezelSmallCorner extends PhoneModelBuilder_RoundedThinBezel {
  constructor(scene, config = {}) {
    // Override the corner radius unconditionally to a smaller value (0.15) and default bezel thickness to 0.05
    const smallCornerConfig = {
      ...config,
      dimensions: { ...config.dimensions, cornerRadius: 0.15 },
      bezelThickness: config.bezelThickness ?? 0.05
    };
    super(scene, smallCornerConfig);
    // Rename the group for clarity
    this.phoneGroup.name = 'phoneModelThinBezelSmallCorner';
  }
} 