import * as THREE from 'three';
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader.js';
import { DRACOLoader } from 'three/examples/jsm/loaders/DRACOLoader.js';
import { KTX2Loader } from 'three/examples/jsm/loaders/KTX2Loader.js';
import { RGBELoader } from 'three/examples/jsm/loaders/RGBELoader.js';
import { ErrorHandler } from './ErrorHandler.mjs';

/**
 * Asset loader utility for loading various types of assets
 * Supports: models (GLTF), textures, environment maps, etc.
 */
class AssetLoader {
  constructor() {
    // Initialize loaders
    this.textureLoader = new THREE.TextureLoader();
    this.gltfLoader = new GLTFLoader();
    
    // Setup DRACO decoder for compressed models
    this.dracoLoader = new DRACOLoader();
    this.dracoLoader.setDecoderPath('js/libs/draco/');
    this.gltfLoader.setDRACOLoader(this.dracoLoader);
    
    // Setup KTX2 loader for compressed textures
    this.ktx2Loader = new KTX2Loader();
    this.ktx2Loader.setTranscoderPath('js/libs/basis/');
    this.gltfLoader.setKTX2Loader(this.ktx2Loader);
    
    // Environment map loader
    this.rgbeLoader = new RGBELoader();
    
    // Set default loading options
    this.defaultOptions = {
      onProgress: (url, loaded, total) => {
        const progress = (loaded / total * 100).toFixed(0);
        console.debug(`Loading: ${url} - ${progress}%`);
      },
      onError: (url, error) => {
        ErrorHandler.handleError(error, `Failed to load asset: ${url}`);
      }
    };
  }
  
  /**
   * Load a 3D model (GLTF/GLB)
   * @param {string} url - URL of the model to load
   * @param {Object} options - Loading options
   * @returns {Promise<Object>} - Promise resolving to the loaded model
   */
  loadModel(url, options = {}) {
    const opts = { ...this.defaultOptions, ...options };
    
    return new Promise((resolve, reject) => {
      try {
        this.gltfLoader.load(
          url,
          (gltf) => {
            console.debug(`Model loaded successfully: ${url}`);
            resolve(gltf);
          },
          (xhr) => {
            if (opts.onProgress) {
              opts.onProgress(url, xhr.loaded, xhr.total);
            }
          },
          (error) => {
            if (opts.onError) {
              opts.onError(url, error);
            }
            reject(error);
          }
        );
      } catch (error) {
        if (opts.onError) {
          opts.onError(url, error);
        }
        reject(error);
      }
    });
  }
  
  /**
   * Load a texture
   * @param {string} url - URL of the texture to load
   * @param {Object} options - Loading options
   * @returns {Promise<THREE.Texture>} - Promise resolving to the loaded texture
   */
  loadTexture(url, options = {}) {
    const opts = { ...this.defaultOptions, ...options };
    
    return new Promise((resolve, reject) => {
      try {
        this.textureLoader.load(
          url,
          (texture) => {
            console.debug(`Texture loaded successfully: ${url}`);
            
            // Apply texture options if provided
            if (options.repeat) {
              texture.repeat.set(options.repeat.x || 1, options.repeat.y || 1);
              texture.wrapS = THREE.RepeatWrapping;
              texture.wrapT = THREE.RepeatWrapping;
            }
            
            if (options.flipY !== undefined) {
              texture.flipY = options.flipY;
            }
            
            if (options.colorSpace) {
              texture.colorSpace = options.colorSpace;
            } else if (options.encoding === THREE.SRGBColorSpace) {
              console.warn('AssetLoader: "encoding" option is deprecated. Use "colorSpace" with THREE.SRGBColorSpace.');
              texture.colorSpace = THREE.SRGBColorSpace;
            }
            
            resolve(texture);
          },
          (xhr) => {
            if (opts.onProgress) {
              opts.onProgress(url, xhr.loaded, xhr.total);
            }
          },
          (error) => {
            if (opts.onError) {
              opts.onError(url, error);
            }
            reject(error);
          }
        );
      } catch (error) {
        if (opts.onError) {
          opts.onError(url, error);
        }
        reject(error);
      }
    });
  }
  
  /**
   * Load an HDR environment map
   * @param {string} url - URL of the HDR file to load
   * @param {Object} options - Loading options
   * @returns {Promise<THREE.Texture>} - Promise resolving to the loaded HDR texture
   */
  loadEnvironmentMap(url, options = {}) {
    const opts = { ...this.defaultOptions, ...options };
    
    return new Promise((resolve, reject) => {
      try {
        const fileExtension = url.split('.').pop()?.toLowerCase();

        let loader;
        if (fileExtension === 'hdr') {
          loader = this.rgbeLoader;
        } else if (['jpg', 'jpeg', 'png', 'webp'].includes(fileExtension)) {
          loader = this.textureLoader;
        } else {
          reject(new Error(`Unsupported environment map file type: ${fileExtension}`));
          return;
        }

        loader.load(
          url,
          (texture) => {
            console.debug(`Environment map loaded successfully: ${url}`);
            
            // Set mapping for all environment maps
            texture.mapping = THREE.EquirectangularReflectionMapping;
            
            // Set colorspace for standard images (replaces encoding)
            if (loader === this.textureLoader) {
              texture.colorSpace = THREE.SRGBColorSpace;
            }

            resolve(texture);
          },
          (xhr) => {
            if (opts.onProgress) {
              opts.onProgress(url, xhr.loaded, xhr.total);
            }
          },
          (error) => {
            if (opts.onError) {
              opts.onError(url, error);
            }
            reject(error);
          }
        );
      } catch (error) {
        if (opts.onError) {
          opts.onError(url, error);
        }
        reject(error);
      }
    });
  }
  
  /**
   * Load a video texture
   * @param {string} url - URL of the video to load
   * @param {Object} options - Loading options
   * @returns {Promise<THREE.VideoTexture>} - Promise resolving to the loaded video texture
   */
  loadVideoTexture(url, options = {}) {
    return new Promise((resolve, reject) => {
      try {
        const video = document.createElement('video');
        video.src = url;
        video.crossOrigin = 'anonymous';
        video.loop = options.loop !== undefined ? options.loop : true;
        video.muted = options.muted !== undefined ? options.muted : true;
        video.autoplay = options.autoplay !== undefined ? options.autoplay : true;
        video.playbackRate = options.playbackRate || 1.0;
        
        video.addEventListener('loadeddata', () => {
          console.debug(`Video loaded successfully: ${url}`);
          const texture = new THREE.VideoTexture(video);
          
          // Apply texture options if provided
          if (options.repeat) {
            texture.repeat.set(options.repeat.x || 1, options.repeat.y || 1);
            texture.wrapS = THREE.RepeatWrapping;
            texture.wrapT = THREE.RepeatWrapping;
          }
          
          if (options.flipY !== undefined) {
            texture.flipY = options.flipY;
          }
          
          if (options.colorSpace) {
            texture.colorSpace = options.colorSpace;
          } else if (options.encoding === THREE.SRGBColorSpace) {
            console.warn('AssetLoader: "encoding" option is deprecated. Use "colorSpace" with THREE.SRGBColorSpace.');
            texture.colorSpace = THREE.SRGBColorSpace;
          }
          
          resolve(texture);
        });
        
        video.addEventListener('error', (error) => {
          if (options.onError) {
            options.onError(url, error);
          }
          reject(error);
        });
        
        // Start loading
        video.load();
      } catch (error) {
        if (options.onError) {
          options.onError(url, error);
        }
        reject(error);
      }
    });
  }
  
  /**
   * Load multiple assets in parallel
   * @param {Array<Object>} assets - Array of asset configs {type, url, options}
   * @returns {Promise<Object>} - Promise resolving to object with loaded assets
   */
  loadAssets(assets) {
    const promises = assets.map(asset => {
      switch (asset.type) {
        case 'model':
          return this.loadModel(asset.url, asset.options).then(result => ({ id: asset.id, result }));
        case 'texture':
          return this.loadTexture(asset.url, asset.options).then(result => ({ id: asset.id, result }));
        case 'environment':
          return this.loadEnvironmentMap(asset.url, asset.options).then(result => ({ id: asset.id, result }));
        case 'video':
          return this.loadVideoTexture(asset.url, asset.options).then(result => ({ id: asset.id, result }));
        default:
          return Promise.reject(new Error(`Unknown asset type: ${asset.type}`));
      }
    });
    
    return Promise.all(promises).then(results => {
      const loadedAssets = {};
      results.forEach(({ id, result }) => {
        loadedAssets[id] = result;
      });
      return loadedAssets;
    });
  }
  
  /**
   * Dispose of loaded assets to free memory
   * @param {Object} asset - Asset to dispose
   */
  dispose(asset) {
    if (!asset) return;
    
    if (asset.isTexture) {
      asset.dispose();
    } else if (asset.scene) {
      // GLTF model
      asset.scene.traverse(child => {
        if (child.isMesh) {
          if (child.geometry) child.geometry.dispose();
          
          if (child.material) {
            if (Array.isArray(child.material)) {
              child.material.forEach(material => this.disposeMaterial(material));
            } else {
              this.disposeMaterial(child.material);
            }
          }
        }
      });
    }
  }
  
  /**
   * Helper to dispose a material and its textures
   * @param {THREE.Material} material - Material to dispose
   * @private
   */
  disposeMaterial(material) {
    // Dispose textures
    for (const prop in material) {
      const value = material[prop];
      if (value && value.isTexture) {
        value.dispose();
      }
    }
    
    // Dispose material
    material.dispose();
  }
}

// Export the class definition directly
export default AssetLoader; 