import * as THREE from 'three';
import { LIGHTING_PRESETS } from '../presets.js';

export default class LightingManager {
  constructor(scene) {
    this.scene = scene;
    
    // Store light elements
    this.lights = {};
    
    // Setup the basic lights
    this.setupLights();
  }
  
  setupLights() {
    // Ambient light
    this.ambientLight = new THREE.AmbientLight(0xffffff, 0.5);
    this.ambientLight.visible = true; // Start enabled to provide base illumination
    this.scene.add(this.ambientLight);
    
    // Directional light (key light)
    this.keyLight = new THREE.DirectionalLight(0xffffff, 1.0);
    this.keyLight.position.set(5, 5, 5);
    this.keyLight.castShadow = true;
    this.keyLight.shadow.mapSize.width = 1024;
    this.keyLight.shadow.mapSize.height = 1024;
    this.keyLight.shadow.bias = -0.001;
    this.keyLight.visible = true; // Start enabled
    this.scene.add(this.keyLight);
    
    // Fill light
    this.fillLight = new THREE.DirectionalLight(0xffffff, 0.5);
    this.fillLight.position.set(-5, 0, 5);
    this.fillLight.castShadow = true;
    this.fillLight.shadow.mapSize.width = 1024;
    this.fillLight.shadow.mapSize.height = 1024;
    this.fillLight.visible = true; // Start enabled
    this.scene.add(this.fillLight);
    
    // Back light
    this.backLight = new THREE.DirectionalLight(0xffffff, 0.5);
    this.backLight.position.set(0, 0, -5);
    this.backLight.castShadow = true;
    this.backLight.visible = true; // Start enabled
    this.scene.add(this.backLight);
    
    // Add environmental lighting
    this.hemiLight = new THREE.HemisphereLight(0xffffff, 0x444444, 0.2);
    this.scene.add(this.hemiLight);
    
    // Store references to lights for easier access
    this.lights = {
      ambient: this.ambientLight,
      key: this.keyLight,
      fill: this.fillLight,
      back: this.backLight,
      hemi: this.hemiLight
    };
  }
  
  // Method to set a lighting preset
  setLightingPreset(preset) {
    if (!preset) return;
    
    // Set ambient light
    if (preset.ambientLight) {
      this.ambientLight.color.set(preset.ambientLight.color);
      this.ambientLight.intensity = preset.ambientLight.intensity;
    }
    
    // Set key light
    if (preset.keyLight) {
      this.keyLight.color.set(preset.keyLight.color);
      this.keyLight.intensity = preset.keyLight.intensity;
      if (preset.keyLight.position) {
        this.keyLight.position.set(
          preset.keyLight.position[0],
          preset.keyLight.position[1],
          preset.keyLight.position[2]
        );
      }
    }
    
    // Set fill light
    if (preset.fillLight) {
      this.fillLight.color.set(preset.fillLight.color);
      this.fillLight.intensity = preset.fillLight.intensity;
      if (preset.fillLight.position) {
        this.fillLight.position.set(
          preset.fillLight.position[0],
          preset.fillLight.position[1],
          preset.fillLight.position[2]
        );
      }
    }
    
    // Set back light
    if (preset.backLight) {
      this.backLight.color.set(preset.backLight.color);
      this.backLight.intensity = preset.backLight.intensity;
      if (preset.backLight.position) {
        this.backLight.position.set(
          preset.backLight.position[0],
          preset.backLight.position[1],
          preset.backLight.position[2]
        );
      }
    }
    
    // Hemisphere light intensity based on ambient
    if (preset.ambientLight) {
      this.hemiLight.intensity = preset.ambientLight.intensity * 0.4;
    }
  }
  
  // Update light controls with current values
  updateLightControlValues(controls) {
    if (!controls) return;
    
    // Update ambient light controls
    if (controls.ambientIntensity) {
      controls.ambientIntensity.value = this.ambientLight.intensity;
    }
    if (controls.ambientColor) {
      controls.ambientColor.value = '#' + this.ambientLight.color.getHexString();
    }
    
    // Update key light controls
    if (controls.keyIntensity) {
      controls.keyIntensity.value = this.keyLight.intensity;
    }
    if (controls.keyColor) {
      controls.keyColor.value = '#' + this.keyLight.color.getHexString();
    }
    
    // Update fill light controls
    if (controls.fillIntensity) {
      controls.fillIntensity.value = this.fillLight.intensity;
    }
    if (controls.fillColor) {
      controls.fillColor.value = '#' + this.fillLight.color.getHexString();
    }
    
    // Update back light controls
    if (controls.backIntensity) {
      controls.backIntensity.value = this.backLight.intensity;
    }
    if (controls.backColor) {
      controls.backColor.value = '#' + this.backLight.color.getHexString();
    }
  }
  
  getLightingPresets() {
    return LIGHTING_PRESETS;
  }
  
  // Get light helper objects for debug visualization
  createLightHelpers() {
    const helpers = [];
    
    // Key light helper
    const keyLightHelper = new THREE.DirectionalLightHelper(this.keyLight, 1);
    keyLightHelper.visible = false;
    this.scene.add(keyLightHelper);
    helpers.push(keyLightHelper);
    
    // Fill light helper
    const fillLightHelper = new THREE.DirectionalLightHelper(this.fillLight, 1);
    fillLightHelper.visible = false;
    this.scene.add(fillLightHelper);
    helpers.push(fillLightHelper);
    
    // Back light helper
    const backLightHelper = new THREE.DirectionalLightHelper(this.backLight, 1);
    backLightHelper.visible = false;
    this.scene.add(backLightHelper);
    helpers.push(backLightHelper);
    
    return helpers;
  }
} 