// Immediate console.log to verify script execution
console.log('SCRIPT LOADED: main.js is executing');

import * as THREE from 'three';
import ConfigManager from '../js/components/ConfigManager.js';
import SceneManager from '../js/components/SceneManager.js';
import ModelManager from '../js/components/ModelManager.js';
import LightingManager from '../js/components/LightingManager.js';
import EnvironmentManager from '../js/components/EnvironmentManager.js';
import ScreenManager from '../js/components/ScreenManager.js';
import UIManager from '../js/components/UIManager.js';
import AnimationManager from '../js/components/AnimationManager.js';
import { ErrorHandler } from '../js/utils/ErrorHandler.mjs';
import { DEFAULT_CONFIG, applyDefaults } from '../js/config.js';
import { SAMPLE_SCREENS, ENVIRONMENTS } from '../js/presets.js';
import AssetLoader from '../js/utils/AssetLoader.js';

// Wait for the DOM to be fully loaded before executing main logic
document.addEventListener('DOMContentLoaded', async () => {
  const loadingIndicator = document.getElementById('loading-indicator');
  const errorDisplay = document.getElementById('error-display');

  // Show loading indicator immediately
  if (loadingIndicator) loadingIndicator.style.display = 'block';

  try {
    // --- Config Loading ---
    console.log('[Main] Instantiating ConfigManager...');
    const configManager = new ConfigManager(); // Instantiate the manager
    let config = configManager.getConfig(); // Get config AFTER instantiation (loading happens in constructor)
    console.log('[Main] Config loaded via ConfigManager instance:', config);

    // Apply custom config overrides if they exist (use instance method? or handle here?)
    // Note: ConfigManager's constructor already merges window.CUSTOM_CONFIG (renamed from window.customConfig)
    // and URL params. We might not need the manual merge here unless window.customConfig is different.
    // Let's check if window.customConfig is still relevant or if it should be window.CUSTOM_CONFIG
    if (window.customConfig && typeof window.customConfig === 'object') {
        console.warn('[Main] Found window.customConfig. ConfigManager uses window.CUSTOM_CONFIG. Merging manually for now.');
        config = configManager.deepMerge(config, window.customConfig); // Use instance's deepMerge
        // Should we update the instance's config? configManager.config = config;
        console.log('[Main] Config after merging window.customConfig:', config);
    }

    // --- Initialize ErrorHandler ---
    ErrorHandler.init(errorDisplay, config?.debug?.logLevel);
    console.log('[Main] ErrorHandler initialized.');

    // --- Container Check ---
    const finalContainerId = config.general?.containerId || 'viewer-container';
    console.log(`[Main] Attempting to find container with ID: #${finalContainerId}`);
    const finalContainer = document.getElementById(finalContainerId);
    if (!finalContainer) {
      throw new Error(`Container element #${finalContainerId} specified in config not found.`);
    }
    console.log('[Main] Container found:', finalContainer);

    // --- Manager Instantiation ---
    console.log('[Main] Instantiating managers...');
    let sceneManager, lightingManager, envManager, modelManager, screenManager, animationManager, uiManager;

    sceneManager = new SceneManager(finalContainer);
    console.log('SceneManager initialized.');

    lightingManager = new LightingManager(sceneManager.scene);
    console.log('LightingManager initialized.');

    envManager = new EnvironmentManager(sceneManager.scene, sceneManager.renderer, lightingManager);
    console.log('EnvironmentManager initialized.');

    modelManager = new ModelManager(sceneManager.scene, sceneManager.camera);
    console.log('[Main] ModelManager initialized.');

    // Instantiate ScreenManager and inject dependencies
    screenManager = new ScreenManager();
    console.log('ScreenManager initialized.');
    // Inject ModelManager into ScreenManager
    if (screenManager && modelManager && typeof screenManager.setModelManager === 'function') {
        screenManager.setModelManager(modelManager);
        console.log('ModelManager injected into ScreenManager.');
    } else {
        console.error('Failed to inject ModelManager into ScreenManager.');
    }
    // Inject ScreenManager into ModelManager
    modelManager.setScreenManager(screenManager);
    console.log('Injected shared ScreenManager into ModelManager.');
    // Inject ScreenManager into SceneManager for per-frame updates
    if (sceneManager && typeof sceneManager.setScreenManager === 'function') {
        sceneManager.setScreenManager(screenManager);
        console.log('ScreenManager injected into SceneManager for per-frame updates.');
    }

    // animationManager = new AnimationManager(config.animation, sceneManager); // Incorrect arguments
    // Correctly pass scene, camera, and renderer from sceneManager
    animationManager = new AnimationManager(
        sceneManager.scene, 
        sceneManager.camera, 
        sceneManager.renderer 
        // config.animation // Pass config separately if needed, or handle inside AnimationManager
    );
    // Optionally, pass config later or modify constructor
    if (config.animation) {
        animationManager.setDeviceAutoRotateSpeed(config.animation.deviceAutoRotateSpeed);
        // Set other config properties as needed
    }
    // *** ADDED: Set initial animation style from controls config ***
    const initialAnimationStyle = config.controls?.animationStyle || DEFAULT_CONFIG.controls.animationStyle;
    if (initialAnimationStyle) {
        animationManager.setAnimationStyle(initialAnimationStyle);
        console.log(`[Main] Set initial animation style to: ${initialAnimationStyle}`);
    }
    // *** END ADDED ***
    console.log('AnimationManager initialized.');

    // Explicitly set the animationManager on the sceneManager instance
    sceneManager.animationManager = animationManager;
    console.log('AnimationManager instance set on SceneManager.');

    // Pass the configManager INSTANCE to UIManager
    uiManager = new UIManager({
      sceneManager,
      modelManager,
      lightingManager,
      envManager,
      animationManager,
      screenManager,
      configManager: configManager, // Pass the instance
      config
    });
    console.log('UIManager initialized.');

    // Expose key managers on window.app for runtime updates from UI
    try {
      window.app = window.app || {};
      window.app.configManager = configManager;
      window.app.uiManager = uiManager;
    } catch (_) { /* no-op */ }

    // Initialize the UIManager with the loaded config
    console.log('[Main] Initializing UIManager...');
    uiManager.init(config); 
    console.log('[Main] UIManager initialization complete.');

    // Initialize hover panel functionality for the side menu
    initHoverPanel();

    // --- Setup Lighting & Initial Environment ---
    console.log('[Main] Setting up initial lighting and environment...');
    lightingManager.setupLights();
    console.log('Initial lights setup complete.');

    window.app = window.app || {}; // Ensure global app namespace exists
    window.app.environmentManager = envManager;
    window.app.presets = { ENVIRONMENTS, SAMPLE_SCREENS };

    const initialEnvKey = config.environment?.initialEnvironment;
    const initialEnv = ENVIRONMENTS.find(e => e.key === initialEnvKey) || ENVIRONMENTS[0];
    console.log('[Main] Loading initial environment:', initialEnv ? initialEnv.name : 'Default');
    if (initialEnv) {
        await envManager.loadAndApplyEnvironment(initialEnv.key);
        // Update lighting AFTER environment is loaded
        envManager.updateLightingForEnvironment(initialEnv, lightingManager.lights);
        console.log('Initial environment loaded and applied:', initialEnv.name);
    } else {
        console.warn("Initial environment not found or specified.");
    }

    // --- Model Loading ---
    console.log('[Main] Starting model loading process...');
    let modelDataResult = null;
    try {
        console.log('[Main] Calling modelManager.loadModel with config:', config.model);
        modelDataResult = await modelManager.loadModel(config.model);
        console.log('[Main] modelManager.loadModel finished. Result:', modelDataResult);
        if (!modelDataResult || !modelDataResult.model) {
             console.warn('[Main] Initial model loading failed or returned no model. Checking for internal fallback...');
             if (!modelManager.getCurrentModelObject()) { 
                throw new Error('Initial model load failed and no fallback generated by loadModel.');
             }
        }
        console.log('[Main] Model loaded successfully (via loadModel or internal fallback).');
    } catch (initialLoadError) {
        console.error('[Main] Error during initial model load, attempting explicit fallback:', initialLoadError);
        try {
             console.log('[Main] Calling modelManager.createDefaultDeviceModel...');
             // Ensure scene is passed correctly if needed by the builder via createModel
             // Modify createDefaultDeviceModel or the createModel factory if scene must come from config
             modelDataResult = await modelManager.createDefaultDeviceModel(); // Pass necessary args if method signature changed
             console.log('[Main] modelManager.createDefaultDeviceModel finished. Result:', modelDataResult);
             if (!modelDataResult || !modelDataResult.model) {
                 throw new Error('Failed to create default procedural model during explicit fallback.');
             }
             console.log('[Main] Default procedural model created successfully as explicit fallback.');
        } catch (fallbackError) {
             ErrorHandler.handleError(fallbackError, "Critical: Failed to load initial model and explicit fallback model creation also failed.");
             if (loadingIndicator) loadingIndicator.style.display = 'none';
             return; // Critical error, cannot proceed
        }
    }
    console.log('[Main] Model loading process complete.');

    // --- Post Model Load Setup ---
    if (!modelDataResult || !modelDataResult.model) {
        ErrorHandler.handleError(new Error("Model data result is invalid after loading attempts."), "Critical Model Load Error");
        if (loadingIndicator) loadingIndicator.style.display = 'none';
        return;
    }

    const { model: currentModelObject, screen: screenMesh, screenMaterial } = modelDataResult;
    console.log('[Main] Destructured model data.');

    // Pass interactable model to SceneManager and AnimationManager
    if (currentModelObject) {
        console.log('[Main] Setting interactable model...');
        sceneManager.setInteractableModel(currentModelObject);
        animationManager.setInteractableModel(currentModelObject);
    } else {
        console.warn('[Main] No model object available to set as interactable.');
    }

    // --- Screen Content --- 
    if (screenManager && screenManager.getScreenMesh()) {
        // --- CORRECTED: Use model.initialScreen index to get preset --- 
        const initialScreenIndex = config.model?.initialScreen;
        let screenPreset = null;

        if (initialScreenIndex !== undefined && initialScreenIndex !== null && initialScreenIndex >= 0 && initialScreenIndex < SAMPLE_SCREENS.length) {
            screenPreset = SAMPLE_SCREENS[initialScreenIndex];
        } else {
            console.warn(`[Main] Invalid or missing initialScreen index (${initialScreenIndex}) in config.model. Checking config.screen...`);
            // Fallback to config.screen?.initialContent only if model index is bad
            screenPreset = config.screen?.initialContent || null;
        }
        // --- END CORRECTION ---

        const fitMode = config.screen?.initialFitMode || 'fit'; // Keep using config.screen for fitMode
        console.log(`[Main] Setting initial screen content (Fit: ${fitMode}):`, screenPreset);
        
        if (screenPreset) {
            // If the preset is Block Colour, override the value with persisted colour if available
            if (screenPreset.type === 'color') {
                try {
                    const savedHex = window.localStorage.getItem('blockColourHex');
                    if (savedHex && /^#?[0-9a-fA-F]{6}$/.test(savedHex)) {
                        const normalized = savedHex.startsWith('#') ? savedHex : ('#' + savedHex);
                        const savedNum = parseInt(normalized.replace('#',''), 16);
                        screenPreset = { ...screenPreset, value: savedNum };
                    }
                } catch (_) { /* no-op */ }
            }
            // Pass the retrieved or fallback preset object
            await screenManager.setScreenContent(screenPreset, fitMode);
            // Sync UI dropdown to reflect the preset shown at load
            try {
              const ui = window.app?.uiManager || window.uiManager;
              if (ui && ui.sampleScreensSelect) {
                const presetName = screenPreset.name || (SAMPLE_SCREENS[initialScreenIndex] && SAMPLE_SCREENS[initialScreenIndex].name);
                if (presetName) ui.sampleScreensSelect.value = presetName;
              }
            } catch (_) { /* no-op */ }
        } else {
            console.warn('[Main] No valid initial screen content found in config. Resetting screen.');
            await screenManager.resetScreen();
        }
        console.log('[Main] Initial screen content set/reset complete.');
    } else {
        console.warn('[Main] ScreenManager unavailable or has no screen mesh; cannot set initial content.');
    }

    // --- Animation & Controls Setup ---
    console.log('[Main] Setting up controls and animation...');
    const controls = sceneManager.setupOrbitControls(); 
    animationManager.setControls(controls);
    console.log('OrbitControls and Animation controls setup complete.');

    // Force disable autoRotate if animation style is not 'none' 
    const currentAnimationStyle = config.controls?.animationStyle || DEFAULT_CONFIG.controls.animationStyle;
    if (currentAnimationStyle && currentAnimationStyle !== 'none') {
        console.log(`[Main] Ensuring orbit controls autoRotate is disabled for animation style: ${currentAnimationStyle}`);
        if (controls && controls.autoRotate) {
            controls.autoRotate = false;
            console.log('[Main] Explicitly disabled controls.autoRotate to prevent conflict with animation style');
        }
    }

    // Re-apply animation style to ensure it takes effect after controls setup
    if (currentAnimationStyle) {
        console.log(`[Main] Re-applying animation style: ${currentAnimationStyle} after controls setup`);
        animationManager.setAnimationStyle(currentAnimationStyle);
    }

    // Force initial resize
    window.setTimeout(() => {
        sceneManager.onWindowResize();
        console.log('[Main] Forced initial viewport resize.');
    }, 100);

    // --- Apply Defaults & Start Loop ---
    console.log('[Main] Applying default settings...');
    const appState = {
      config: config,
      sceneManager: sceneManager,
      lightingManager: lightingManager,
      modelManager: modelManager,
      screenManager: screenManager,
      animationManager: animationManager,
      uiManager: uiManager,
      environmentManager: envManager,
      camera: sceneManager.camera,
      renderer: sceneManager.renderer,
      controls: controls,
      scene: sceneManager.scene
    };
    applyDefaults(appState);
    console.log('[Main] Default settings applied.');

    console.log('[Main] Starting animation loop...');
    sceneManager.animate();
    console.log('[Main] Animation loop started.');

    // Hide loading indicator once everything is ready
    if (loadingIndicator) {
      console.log('[Main] Hiding loading indicator.');
      loadingIndicator.style.display = 'none';
    }
    console.log('[Main] Initialization complete.');

  } catch (error) {
    console.error('[Main] A critical error occurred during application initialization:', error);
    ErrorHandler.handleError(error, 'A critical error occurred during application initialization.');
    if (loadingIndicator) loadingIndicator.style.display = 'none';
  }
});

// Helper function to initialize collapsible sections in the UI
// This is kept separate as it's a UI-only function that doesn't depend on the 3D components
function initCollapsibleSections() {
  const collapsibleSections = document.querySelectorAll('.collapsible');
  console.log('Found collapsible sections:', collapsibleSections.length);
  
  collapsibleSections.forEach((section) => {
    const header = section.querySelector('.collapsible-header');
    const content = section.querySelector('.collapsible-content');
    
    if (header && content) {
      // Set initial state (collapsed by default)
      content.style.display = 'none';
      header.classList.remove('expanded'); // Ensure initial state is not expanded
      
      // Add click handler
      header.addEventListener('click', () => {
        // Toggle content visibility
        const isExpanded = content.style.display === 'block';
        content.style.display = isExpanded ? 'none' : 'block';
        header.classList.toggle('expanded', !isExpanded); 
      });
    }
  });
}

// Helper function to initialize the hover-triggered panel behavior
function initHoverPanel() {
  const controlsContainer = document.querySelector('.controls-container');
  if (!controlsContainer) {
    console.warn('Could not find .controls-container for hover panel initialization.');
    return;
  }

  const triggerWidth = 20; // Pixels from the left edge to trigger show

  console.log('Initializing hover panel logic.');

  document.addEventListener('mousemove', (event) => {
    const mouseX = event.clientX;

    if (mouseX < triggerWidth) {
      // Mouse is in the trigger zone, show panel
      if (!controlsContainer.classList.contains('visible')) {
        // console.log('Mouse entered trigger zone, showing panel.');
        controlsContainer.classList.add('visible');
      }
    } else if (!controlsContainer.matches(':hover')) {
      // Mouse is outside trigger zone AND not hovering over the panel itself
      if (controlsContainer.classList.contains('visible')) {
        // console.log('Mouse left trigger zone and panel, hiding panel.');
        controlsContainer.classList.remove('visible');
      }
    }
    // If mouse is outside trigger zone BUT hovering over the panel, do nothing (keep it open)
  });

  // Ensure it closes if the mouse leaves the window entirely
  document.documentElement.addEventListener('mouseleave', () => {
     if (controlsContainer.classList.contains('visible')) {
       // console.log('Mouse left window, hiding panel.');
       controlsContainer.classList.remove('visible');
     }
  });
}
