/**
 * Error handling utility for 3D phone web application
 * Provides centralized error handling and display
 */

/**
 * ErrorHandler - Static class for handling application errors
 */
import { MAX_ERROR_HISTORY } from '../config.js';

export class ErrorHandler {
  static errors = []; // Initialize static property here

  /**
   * Initialize the error handler
   * @param {string} errorElementId - ID of the HTML element where errors will be displayed
   */
  static init(errorElementId = 'error-display') {
    this.errorElement = document.getElementById(errorElementId);
    
    // Create error element if it doesn't exist
    if (!this.errorElement) {
      this.errorElement = document.createElement('div');
      this.errorElement.id = errorElementId;
      this.errorElement.classList.add('error-container');
      this.errorElement.style.display = 'none';
      
      // Add error element to body if not already in DOM
      document.body.appendChild(this.errorElement);
    }
    
    // Set up default error styling if not already styled
    if (!this.errorElement.classList.contains('error-container')) {
      this.errorElement.classList.add('error-container');
      this.errorElement.style.position = 'fixed';
      this.errorElement.style.top = '20px';
      this.errorElement.style.left = '50%';
      this.errorElement.style.transform = 'translateX(-50%)';
      this.errorElement.style.backgroundColor = 'rgba(220, 53, 69, 0.9)';
      this.errorElement.style.color = 'white';
      this.errorElement.style.padding = '15px 20px';
      this.errorElement.style.borderRadius = '5px';
      this.errorElement.style.maxWidth = '80%';
      this.errorElement.style.zIndex = '10000';
      this.errorElement.style.boxShadow = '0 4px 8px rgba(0,0,0,0.2)';
    }
    
    // Add close button if not already present
    if (!this.errorElement.querySelector('.close-error')) {
      const closeButton = document.createElement('button');
      closeButton.textContent = '×';
      closeButton.classList.add('close-error');
      closeButton.style.marginLeft = '15px';
      closeButton.style.background = 'none';
      closeButton.style.border = 'none';
      closeButton.style.color = 'white';
      closeButton.style.fontSize = '20px';
      closeButton.style.cursor = 'pointer';
      closeButton.style.float = 'right';
      closeButton.style.lineHeight = '1';
      
      closeButton.addEventListener('click', () => {
        this.clearError();
      });
      
      this.errorElement.appendChild(closeButton);
    }
    
    // Set up global error handler
    window.addEventListener('error', (event) => {
      this.handleError(event.error || new Error(event.message), 'Unhandled JavaScript Error');
      return false; // Let other error handlers run too
    });
    
    console.log('ErrorHandler initialized');
  }
  
  /**
   * Handle an error
   * @param {Error} error - The error object
   * @param {string} context - Optional context information
   * @returns {boolean} Always returns false to indicate error was handled
   */
  static handleError(error, context = '') {
    // Log error to console with context
    console.error(`${context ? context + ': ' : ''}`, error);
    
    // Add error to history using the class name to access static property
    ErrorHandler.errors.push({
      timestamp: new Date(),
      error: error,
      context: context,
      message: error.message
    });
    
    // Keep history size manageable
    if (ErrorHandler.errors.length > MAX_ERROR_HISTORY) {
      ErrorHandler.errors.shift(); // Remove the oldest error
    }
    
    // Create user-friendly message
    let userMessage = '';
    if (context) {
      userMessage += `<strong>${context}</strong><br>`;
    }
    userMessage += error.message || 'An unknown error occurred.';
    
    // Display error to user
    this.showError(userMessage);
    
    return false;
  }
  
  /**
   * Display an error message to the user
   * @param {string} message - The message to display
   * @param {number} timeout - Optional auto-hide timeout in ms, 0 to disable
   */
  static showError(message, timeout = 0) {
    if (!this.errorElement) {
      this.init();
    }
    
    // Set error message content
    const messageElement = this.errorElement.querySelector('.error-message') || document.createElement('div');
    messageElement.classList.add('error-message');
    messageElement.innerHTML = message;
    
    // Add message element if not present
    if (!this.errorElement.querySelector('.error-message')) {
      this.errorElement.appendChild(messageElement);
    }
    
    // Show error
    this.errorElement.style.display = 'block';
    
    // Auto-hide after timeout (if enabled)
    if (timeout > 0) {
      setTimeout(() => {
        this.clearError();
      }, timeout);
    }
  }
  
  /**
   * Clear the current error display
   */
  static clearError() {
    if (this.errorElement) {
      this.errorElement.style.display = 'none';
    }
  }
  
  /**
   * Get error history
   * @returns {Array} Array of past errors
   */
  static getErrorHistory() {
    return ErrorHandler.errors;
  }
  
  /**
   * Clear the error history
   */
  static clearErrorHistory() {
    ErrorHandler.errors = [];
  }
} 