import * as THREE from 'three';

export class TextureManager {
  constructor() {
    this.textureLoader = new THREE.TextureLoader();
    this.textureCache = new Map();
    this.currentTexture = null;
    this.currentAspect = 1.0;
  }

  /**
   * Load an image texture from URL
   * @param {string} url - URL of the image to load
   * @returns {Promise<THREE.Texture>}
   */
  async loadImageTexture(url) {
    if (this.textureCache.has(url)) {
      return this.textureCache.get(url);
    }

    return new Promise((resolve, reject) => {
      this.textureLoader.load(
        url,
        (texture) => {
          this.optimizeTexture(texture);
          this.textureCache.set(url, texture);
          this.currentTexture = texture;
          if (texture.image && texture.image.width && texture.image.height) {
            this.currentAspect = texture.image.width / texture.image.height;
          }
          resolve(texture);
        },
        undefined,
        (error) => {
          console.error('Error loading texture:', error);
          reject(error);
        }
      );
    });
  }

  /**
   * Optimize texture settings for screen display
   * @param {THREE.Texture} texture - Texture to optimize
   */
  optimizeTexture(texture) {
    texture.minFilter = THREE.LinearFilter;
    texture.magFilter = THREE.LinearFilter;
    texture.format = THREE.RGBAFormat;
    texture.colorSpace = THREE.SRGBColorSpace;
    // For image textures used on screen quads, keep Y-up visual orientation
    texture.flipY = true;
    texture.generateMipmaps = false;
  }

  /**
   * Create a solid color texture
   * @param {string|number} color - Color value (hex string or number)
   * @returns {THREE.Texture}
   */
  createSolidColorTexture(color) {
    const canvas = document.createElement('canvas');
    canvas.width = 1;
    canvas.height = 1;
    const ctx = canvas.getContext('2d');
    
    // Convert color to RGB values
    let r, g, b;
    if (typeof color === 'string') {
      const hex = color.replace('#', '');
      r = parseInt(hex.substr(0, 2), 16);
      g = parseInt(hex.substr(2, 2), 16);
      b = parseInt(hex.substr(4, 2), 16);
    } else {
      r = (color >> 16) & 255;
      g = (color >> 8) & 255;
      b = color & 255;
    }
    
    ctx.fillStyle = `rgb(${r},${g},${b})`;
    ctx.fillRect(0, 0, 1, 1);
    
    const texture = new THREE.CanvasTexture(canvas);
    this.optimizeTexture(texture);
    this.currentTexture = texture;
    this.currentAspect = 1.0;
    return texture;
  }

  /**
   * Create a checkerboard pattern texture
   * @param {number} width - Width of the texture
   * @param {number} height - Height of the texture
   * @returns {THREE.Texture}
   */
  createCheckerboardTexture(width = 256, height = 256) {
    const canvas = document.createElement('canvas');
    canvas.width = width;
    canvas.height = height;
    const ctx = canvas.getContext('2d');
    
    // Draw checkerboard pattern
    const tileSize = 32;
    for (let y = 0; y < height; y += tileSize) {
      for (let x = 0; x < width; x += tileSize) {
        const isEven = ((x / tileSize) + (y / tileSize)) % 2 === 0;
        ctx.fillStyle = isEven ? '#ffffff' : '#000000';
        ctx.fillRect(x, y, tileSize, tileSize);
      }
    }
    
    const texture = new THREE.CanvasTexture(canvas);
    texture.name = 'checkerboard';
    this.optimizeTexture(texture);
    texture.flipY = true;
    this.currentTexture = texture;
    this.currentAspect = width / height;
    return texture;
  }

  /**
   * Creates a test pattern texture with a grid to debug texture mapping
   * @returns {THREE.Texture}
   */
  createTestScreenTexture() {
    // Create a canvas for the test pattern
    const canvas = document.createElement('canvas');
    canvas.width = 512;
    canvas.height = 512;
    const ctx = canvas.getContext('2d');
    
    // Fill the background
    ctx.fillStyle = 'black';
    ctx.fillRect(0, 0, canvas.width, canvas.height);
    
    // Draw a border
    ctx.strokeStyle = 'red';
    ctx.lineWidth = 16;
    ctx.strokeRect(0, 0, canvas.width, canvas.height);
    
    // Draw crosshairs
    ctx.beginPath();
    ctx.moveTo(0, canvas.height/2);
    ctx.lineTo(canvas.width, canvas.height/2);
    ctx.moveTo(canvas.width/2, 0);
    ctx.lineTo(canvas.width/2, canvas.height);
    ctx.strokeStyle = 'yellow';
    ctx.lineWidth = 8;
    ctx.stroke();
    
    // Draw grid
    ctx.beginPath();
    for (let x = 0; x < canvas.width; x += 64) {
      ctx.moveTo(x, 0);
      ctx.lineTo(x, canvas.height);
    }
    for (let y = 0; y < canvas.height; y += 64) {
      ctx.moveTo(0, y);
      ctx.lineTo(canvas.width, y);
    }
    ctx.strokeStyle = 'lime';
    ctx.lineWidth = 2;
    ctx.stroke();
    
    // Draw a text label
    ctx.font = 'bold 48px Arial';
    ctx.fillStyle = 'white';
    ctx.textAlign = 'center';
    ctx.textBaseline = 'middle';
    ctx.fillText('TEST PATTERN', canvas.width/2, canvas.height/2);
    
    // Return as a THREE.js texture
    const texture = new THREE.CanvasTexture(canvas);
    texture.name = 'testPattern';
    this.optimizeTexture(texture); // Apply standard optimizations
    texture.flipY = true;
    this.currentTexture = texture;
    this.currentAspect = 1.0;
    return texture;
  }

  /**
   * Fit a texture to match screen aspect ratio
   * @param {THREE.Texture} texture - Texture to fit
   * @param {number} textureAspect - Aspect ratio of the texture
   * @param {number} screenAspect - Aspect ratio of the screen
   * @param {string} displayMode - Display mode ('fit', 'fill', 'stretch')
   * @param {boolean} maintainAspectRatio - Whether to maintain aspect ratio
   * @param {boolean} flipOrientation - Whether to flip orientation
   * @returns {Object} - Texture transformation parameters
   */
  fitTextureToScreen(texture, textureAspect, screenAspect, displayMode = 'fit', maintainAspectRatio = true, flipOrientation = false) {
    let scaleX = 1;
    let scaleY = 1;
    let offsetX = 0;
    let offsetY = 0;

    if (flipOrientation) {
      [textureAspect, screenAspect] = [screenAspect, textureAspect];
    }

    switch (displayMode) {
      case 'fill':
        if (textureAspect > screenAspect) {
          scaleX = screenAspect / textureAspect;
        } else {
          scaleY = textureAspect / screenAspect;
        }
        break;
      case 'stretch':
        scaleX = screenAspect / textureAspect;
        scaleY = 1;
        break;
      case 'fit':
      default:
        if (textureAspect > screenAspect) {
          scaleY = screenAspect / textureAspect;
        } else {
          scaleX = textureAspect / screenAspect;
        }
    }

    if (!maintainAspectRatio) {
      scaleX = 1;
      scaleY = 1;
    }

    return {
      scaleX,
      scaleY,
      offsetX,
      offsetY
    };
  }

  getCurrentTexture() {
    return this.currentTexture;
  }

  getCurrentTextureAspectRatio() {
    return this.currentAspect;
  }

  /**
   * Clear texture cache
   */
  clearCache() {
    this.textureCache.forEach(texture => {
      texture.dispose();
    });
    this.textureCache.clear();
  }

  /**
   * Dispose of all resources
   */
  dispose() {
    this.clearCache();
    if (this.currentTexture) {
      this.currentTexture.dispose();
      this.currentTexture = null;
    }
  }
} 