import * as THREE from 'three';
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls.js';

export default class SceneManager {
  constructor(container) {
    console.log('Initializing SceneManager with container:', container);
    this.container = container;
    this._resizeObserver = null;
    
    // Scene setup
    this.scene = new THREE.Scene();
    this.scene.background = new THREE.Color(0x333333); // Set visible background color for debugging
    console.log('Scene created with background color');
    
    // Camera setup
    this.setupCamera();
    
    // Renderer setup
    this.setupRenderer();
    
    // Reference to animation manager (set externally)
    this.animationManager = null;
    
    // Model Interaction properties
    this.interactableModel = null;
    this.raycaster = new THREE.Raycaster();
    this.mouse = new THREE.Vector2();
    this.isDraggingModel = false;
    this.previousMousePosition = { x: 0, y: 0 };
    
    // Handle window resize
    window.addEventListener('resize', this.onWindowResize.bind(this));
    
    // Responsive: ResizeObserver for container
    this._resizeObserver = new window.ResizeObserver(() => this.onContainerResize());
    this._resizeObserver.observe(this.container);
    
    // Initialize interaction handlers
    this.initInteraction();
    
    // Force a resize after a short delay to ensure correct sizing
    setTimeout(() => {
      this.onWindowResize();
      console.log('Initial forced viewport resize');
    }, 100);
    
    // Initialize animation timestamp to avoid NaN delta times on first frame
    this.lastAnimationUpdate = performance.now();
    
    // Add grid helper for debugging
    //const gridHelper = new THREE.GridHelper(10, 10);
    //this.scene.add(gridHelper);
    //console.log('Added grid helper to scene for orientation');
    
    this.screenManager = null; // Add property for screenManager
    this.fitPadding = 1.4; // Padding factor to ensure device is not cropped and default view is more zoomed out
  }
  
  setupCamera() {
    // Create a responsive camera with appropriate field of view
    this.camera = new THREE.PerspectiveCamera(
      60, // Slightly narrower field of view for better focus on the phone 
      window.innerWidth / window.innerHeight, 
      0.1, 
      1000
    );
    // Change camera position to be closer and slightly above for better view
    this.camera.position.set(0, 0, 5);
    this.camera.lookAt(0, 0, 0);
    console.log('Camera setup complete, position:', this.camera.position);
  }
  
  setupRenderer() {
    // Read persisted performance settings
    const useAA = window.localStorage.getItem('perf-antialias') === 'true';
    const usePDB = window.localStorage.getItem('perf-preserve-buffer') === 'true';
    
    // Try creating the renderer with current preferences, then fall back to safe defaults if needed
    try {
      this.renderer = new THREE.WebGLRenderer({
        antialias: !!useAA,
        alpha: true,
        preserveDrawingBuffer: !!usePDB,
        powerPreference: "high-performance"
      });
    } catch (err) {
      console.warn('[SceneManager] WebGLRenderer creation failed with current settings. Retrying with safe defaults...', err);
      // Retry with conservative options most likely to succeed
      this.renderer = new THREE.WebGLRenderer({
        antialias: false,
        alpha: false,
        preserveDrawingBuffer: false,
        powerPreference: 'default',
        failIfMajorPerformanceCaveat: false
      });
    }
    // Use container dimensions for initial size
    const width = this.container.offsetWidth || window.innerWidth;
    const height = this.container.offsetHeight || window.innerHeight;
    this.renderer.setSize(width, height);
    // Pixel ratio: FORCE pixel ratio 1 for best performance (see patch instructions)
    // const useHPR = window.localStorage.getItem('perf-pixel-ratio') === 'true';
    // this.renderer.setPixelRatio(useHPR ? Math.min(window.devicePixelRatio, 2) : 1);
    const pixelRatio = 1; // Force low pixel ratio for best performance
    this.renderer.setPixelRatio(pixelRatio);
    this.renderer.shadowMap.enabled = true;
    // Shadows: only enable if requested
    const shadowsOn = window.localStorage.getItem('perf-shadows') === 'true';
    this.renderer.shadowMap.enabled = !!shadowsOn;
    this.renderer.shadowMap.type = THREE.PCFSoftShadowMap; // Better shadow quality
    
    // Use correct color encoding & tone mapping for accurate, vibrant output
    this.renderer.outputColorSpace = THREE.SRGBColorSpace;
    this.renderer.toneMapping = THREE.ACESFilmicToneMapping;
    this.renderer.toneMappingExposure = 1.0;
    
    // Set clear color based on config
    const isTransparent = window.app?.config?.scene?.transparentBackground ?? false;
    this.renderer.setClearAlpha(isTransparent ? 0 : 1);
    // Use a neutral clear color if opaque, doesn't matter if transparent
    this.renderer.setClearColor(0x222222, isTransparent ? 0 : 1); 
    
    // Make sure the canvas uses the proper rendering context
    this.renderer.domElement.style.display = 'block';
    // Apply CSS best practices for sizing
    this.renderer.domElement.style.width = '100%';
    this.renderer.domElement.style.height = '100%';
    this.renderer.domElement.style.maxWidth = '100%';
    this.renderer.domElement.style.maxHeight = '100%';
    this.renderer.domElement.classList.add('device-showcase-canvas');
    
    // Handle WebGL context loss gracefully
    try {
      this.renderer.domElement.addEventListener('webglcontextlost', (event) => {
        event.preventDefault();
        console.warn('[SceneManager] WebGL context lost. Prevented default to allow restoration.');
      }, false);
      this.renderer.domElement.addEventListener('webglcontextrestored', () => {
        console.warn('[SceneManager] WebGL context restored. Re-applying size.');
        this.onWindowResize();
      }, false);
    } catch (e) {
      // Best-effort; some environments may not support these events
    }
    
    // Add renderer to container
    this.container.appendChild(this.renderer.domElement);
    console.log('Renderer setup complete, appended to container', 
                'size:', width, 'x', height,
                'pixel ratio:', this.renderer.getPixelRatio());
  }
  
  setupOrbitControls() {
    this.controls = new OrbitControls(this.camera, this.renderer.domElement);
    this.controls.enableDamping = true;
    this.controls.dampingFactor = 0.05;
    this.controls.autoRotate = false;
    this.controls.autoRotateSpeed = 2.0; // Set default speed
    this.controls.rotateSpeed = 0.7;
    
    // Pass controls to animation manager if it exists
    if (this.animationManager) {
      this.animationManager.setControls(this.controls);
    }
    
    return this.controls;
  }
  
  onWindowResize() {
    // Use container dimensions for resizing
    const width = this.container.offsetWidth || window.innerWidth;
    const height = this.container.offsetHeight || window.innerHeight;
    this.camera.aspect = width / height;
    this.camera.updateProjectionMatrix();
    this.renderer.setSize(width, height, true);
    // const pixelRatio = Math.min(window.devicePixelRatio, 2);
    const pixelRatio = 1; // Force low pixel ratio for best performance
    this.renderer.setPixelRatio(pixelRatio);
    console.log(`Resized viewport: ${width}x${height}, pixel ratio: ${pixelRatio}`);
  }
  
  onContainerResize() {
    this.onWindowResize();
  }
  
  /* // REMOVED - Auto-rotate is now handled by AnimationManager for the device model
  setAutoRotate(enabled) {
    this.controls.autoRotate = enabled;
  }
  
  setAutoRotateSpeed(speed) {
    this.controls.autoRotateSpeed = speed;
  }
  */
  
  // Forward animation style to animation manager
  setAnimationStyle(style) {
    if (this.animationManager) {
      this.animationManager.setAnimationStyle(style);
    } else {
      console.warn('Animation manager not set in SceneManager');
    }
  }
  
  resetView() {
    // Reset camera position to match the setup camera position
    this.camera.position.set(0, 0, 5); // Match the setupCamera value
    this.camera.lookAt(0, 0, 0);
    // Reset controls target to center
    if (this.controls) {
      this.controls.target.set(0, 0, 0);
      this.controls.update();
    }
    // Reset scene rotation
    this.scene.rotation.set(0, 0, 0);
    // Use container size for aspect ratio
    const width = this.container.offsetWidth || window.innerWidth;
    const height = this.container.offsetHeight || window.innerHeight;
    this.camera.aspect = width / height;
    this.camera.updateProjectionMatrix();
    // Trigger a resize to ensure proper viewport dimensions
    this.onWindowResize();
    // Move camera back by fitPadding
    this.camera.position.z *= this.fitPadding;
    if (this.controls) {
      this.controls.update();
    }
    console.log('View reset to default position: camera at', this.camera.position);
  }
  
  resetCameraView() {
    if (!this.controls) return;
    // Calculate the center and size of the current model
    let modelCenter = new THREE.Vector3();
    let modelSize = new THREE.Vector3();
    const boundingBox = new THREE.Box3();
    if (this.interactableModel) {
      boundingBox.setFromObject(this.interactableModel, true); // true = precise
      boundingBox.getCenter(modelCenter);
      boundingBox.getSize(modelSize);
    } else {
      // Default target if no model
      modelCenter.set(0, 0, 0);
      modelSize.set(1, 1, 1); // Assume a default size for positioning
    }
    // Get container aspect ratio
    const width = this.container.offsetWidth || window.innerWidth;
    const height = this.container.offsetHeight || window.innerHeight;
    const aspect = width / height;
    // Model dimensions
    const modelWidth = modelSize.x;
    const modelHeight = modelSize.y;
    // Camera FOV in radians
    const fov = THREE.MathUtils.degToRad(this.camera.fov);
    // Calculate distance required to fit the model height
    const fitHeightDistance = (modelHeight / 2) / Math.tan(fov / 2);
    // Calculate distance required to fit the model width, considering aspect ratio
    const fitWidthDistance = (modelWidth / 2) / (Math.tan(fov / 2) * aspect);
    // Use the larger distance, then apply fitPadding
    const distance = Math.max(fitHeightDistance, fitWidthDistance) * this.fitPadding;
    // Calculate a simple offset position instead of using current camera direction
    const offset = new THREE.Vector3(0, 0, distance); // Position camera along Z-axis relative to center
    const newPosition = modelCenter.clone().add(offset);
    // Apply new position and target
    this.camera.position.copy(newPosition);
    this.controls.target.copy(modelCenter);
    this.camera.lookAt(modelCenter);
    this.controls.update();
    console.log('Camera view reset to focus on model at', modelCenter, 'with distance', distance, 'aspect', aspect);
  }

  takeScreenshot() {
    try {
      // Render the scene
      this.renderer.render(this.scene, this.camera);
      
      // Get the image data
      const imgData = this.renderer.domElement.toDataURL('image/png');
      
      // Create a download link
      const link = document.createElement('a');
      link.href = imgData;
      link.download = 'phone-mockup-screenshot-' + new Date().toISOString().slice(0, 19).replace(/:/g, '-') + '.png';
      
      // Trigger the download
      document.body.appendChild(link);
      link.click();
      document.body.removeChild(link);
      
      console.log('Screenshot taken successfully');
    } catch (error) {
      console.error('Error taking screenshot:', error);
    }
  }
  
  setBackgroundTransparency(isTransparent) {
    if (isTransparent) {
      this.renderer.setClearColor(0x000000, 0); // Transparent
    } else {
      this.renderer.setClearColor(0x000000, 1); // Opaque
    }
  }
  
  // This method is kept for backwards compatibility
  // but delegates to AnimationManager if available
  animate() {
    // Wrap animation loop in try-catch to pinpoint errors
    try {
      // Schedule the next frame
      requestAnimationFrame(this.animate.bind(this)); // Bind 'this' correctly
      // Calculate time delta since last frame
      const now = performance.now();
      const deltaTime = (now - this.lastAnimationUpdate) / 1000; // Convert ms to seconds
      this.lastAnimationUpdate = now; // Update last timestamp
      // --- Mean Edge Extension per-frame update ---
      if (window.debugTools && window.debugTools.useExpandedVideoTexture) {
        // Get the current screen mesh from ScreenManager if available
        const screenManager = window.app?.screenManager;
        const screenMesh = screenManager ? screenManager.getScreenMesh() : null;
        if (screenMesh) {
          window.debugTools.updateExpandedVideoTexture(screenMesh);
        }
      }
      // --- End Mean Edge Extension update ---
      // Update custom animations (ping-pong, sway etc.) based on current style and delta time
      if (this.animationManager && typeof this.animationManager.updateAnimation === 'function') {
        this.animationManager.updateAnimation(deltaTime);
      }
      // Update orbit controls. This handles damping, zoom, pan, and also auto-rotation if enabled.
      if (this.controls) {
        this.controls.update();
      }
      // Clamp camera z to a minimum value to avoid cropping
      const minZ = 3;
      if (this.camera.position.z < minZ) {
        console.warn('[SceneManager] Camera z was too close, clamping to', minZ, 'from', this.camera.position.z);
        this.camera.position.z = minZ;
      }
      // Render the scene if components are valid
      if (this.renderer && this.scene && this.camera) {
        this.renderer.render(this.scene, this.camera);
      } else {
        console.error('[SceneManager.animate] Cannot render: missing components.',
          'renderer:', !!this.renderer,
          'scene:', !!this.scene,
          'camera:', !!this.camera);
      }
    } catch (error) {
      // Log but do not rethrow to avoid breaking the render loop
      console.error('[SceneManager.animate] Caught error during animation loop:', error);
    }
  }
  
  // Getter method for animation style
  get animationStyle() {
    return this.animationManager ? this.animationManager.animationStyle : 'none';
  }

  // Method to set the model that can be interacted with
  setInteractableModel(model) {
    this.interactableModel = model;
    console.log('Interactable model set in SceneManager:', this.interactableModel);
  }

  initInteraction() {
    this.renderer.domElement.addEventListener('pointerdown', this.onPointerDown.bind(this), false);
    this.renderer.domElement.addEventListener('pointermove', this.onPointerMove.bind(this), false);
    this.renderer.domElement.addEventListener('pointerup', this.onPointerUp.bind(this), false);
    // Add touch events as well for mobile
    this.renderer.domElement.addEventListener('touchstart', (event) => this.onPointerDown(event.touches[0]), { passive: true });
    this.renderer.domElement.addEventListener('touchmove', (event) => this.onPointerMove(event.touches[0]), { passive: true });
    this.renderer.domElement.addEventListener('touchend', this.onPointerUp.bind(this), false); // touchend doesn't have coordinates
    console.log('Interaction listeners initialized for pointer and touch events.');
  }

  onPointerDown(event) {
    if (!this.interactableModel || !this.controls) return;

    // Calculate mouse position in normalized device coordinates (-1 to +1)
    const rect = this.renderer.domElement.getBoundingClientRect();
    this.mouse.x = ((event.clientX - rect.left) / rect.width) * 2 - 1;
    this.mouse.y = -((event.clientY - rect.top) / rect.height) * 2 + 1;

    // Update the picking ray with the camera and mouse position
    this.raycaster.setFromCamera(this.mouse, this.camera);

    // Calculate objects intersecting the picking ray
    const intersects = this.raycaster.intersectObject(this.interactableModel, true); // Check recursively

    if (intersects.length > 0) {
      // Check if the intersection is with the interactable model or its children
      let intersectedModel = false;
      for(let i = 0; i < intersects.length; i++) {
          let obj = intersects[i].object;
          while(obj) {
              if (obj === this.interactableModel) {
                  intersectedModel = true;
                  break;
              }
              obj = obj.parent;
          }
          if (intersectedModel) break;
      }

      if (intersectedModel) {
          console.log('Pointer down intersects model');
          this.isDraggingModel = true;
          this.controls.enabled = false; // Disable camera controls
          this.previousMousePosition = {
              x: event.clientX,
              y: event.clientY
          };
          // Prevent default browser actions like text selection
          if (event.preventDefault) event.preventDefault();
      } else {
          // Clicked outside the model, allow camera controls
          this.isDraggingModel = false;
          this.controls.enabled = true;
      }
    } else {
      // Clicked background, allow camera controls
      this.isDraggingModel = false;
      this.controls.enabled = true;
    }
  }

  onPointerMove(event) {
    if (this.isDraggingModel && this.interactableModel) {
      const deltaMove = {
        x: event.clientX - this.previousMousePosition.x,
        y: event.clientY - this.previousMousePosition.y
      };

      // Rotation logic: Rotate around world Y for horizontal drag, and camera's X axis for vertical drag
      const rotationSpeed = 0.005; // Adjust sensitivity

      // Get camera's right vector (local X axis in world space)
      const cameraRight = new THREE.Vector3();
      this.camera.getWorldDirection(cameraRight); // Get forward vector
      cameraRight.cross(this.camera.up); // Cross with up to get right vector

      // Apply rotations
      this.interactableModel.rotateOnWorldAxis(new THREE.Vector3(0, 1, 0), deltaMove.x * rotationSpeed);
      this.interactableModel.rotateOnWorldAxis(cameraRight, deltaMove.y * rotationSpeed);


      this.previousMousePosition = {
        x: event.clientX,
        y: event.clientY
      };
    }
    // No need to update mouse/raycaster here unless we need hover effects
  }

  onPointerUp(event) {
    if (this.isDraggingModel) {
      console.log('Pointer up, re-enabling controls');
      this.isDraggingModel = false;
      if (this.controls) {
        this.controls.enabled = true; // Re-enable camera controls
      }
    }
    // Reset mouse position tracking if needed, but OrbitControls handle their own state
  }

  setScreenManager(screenManager) {
    this.screenManager = screenManager;
  }

  // Add this method to allow setting the animation manager after construction
  setAnimationManager(animationManager) {
    this.animationManager = animationManager;
  }

  destroy() {
    // Cleanup ResizeObserver
    if (this._resizeObserver) {
      this._resizeObserver.disconnect();
      this._resizeObserver = null;
    }
    window.removeEventListener('resize', this.onWindowResize.bind(this));
    // Remove renderer DOM element
    if (this.renderer && this.renderer.domElement && this.container.contains(this.renderer.domElement)) {
      this.container.removeChild(this.renderer.domElement);
    }
    // Additional cleanup as needed (dispose renderer, scene, etc.)
  }
} 