/**
 * Applies default configuration settings to the app state object and its components.
 * @param {Object} appState - Object containing managers, camera, controls, scene, config, etc.
 */
export function applyDefaults(appState) {
  console.log('Applying default configurations...');
  if (!appState || typeof appState !== 'object') {
    console.error('Invalid appState object provided to applyDefaults.');
    return;
  }
  
  // Get the config from the appState, ensuring it exists
  const config = appState.config || {}; 

  // Ensure config sections exist before applying defaults (used for state updates)
  // These might be different from the ones used for UI sync logic below
  const lightingConfig = config.lighting || {};
  const screenConfig = config.screen || {};
  // Definitions for UI sync logic moved here
  const inputControlsConfigForUI = config.controls || {};
  const inputModelConfigForUI = config.model || {};
  const inputEnvironmentConfigForUI = config.environment || {};

  // --- Camera Settings ---
  if (appState.camera) {
    // Use cameraConfig derived from appState.config, fallback to DEFAULT_CONFIG
    const positionConf = config.camera?.position ?? DEFAULT_CONFIG.camera.position;
    const lookAtConf = config.camera?.lookAt ?? DEFAULT_CONFIG.camera.lookAt;
    
    // Apply position using resolved config
    if (positionConf && typeof positionConf === 'object') {
      appState.camera.position.set(
        positionConf.x ?? 0,
        positionConf.y ?? 0,
        positionConf.z ?? 5
      );
    } else {
        console.warn('Default camera position is missing or invalid in DEFAULT_CONFIG');
    }

    // Apply lookAt using resolved config
    if (lookAtConf && typeof lookAtConf === 'object') {
        appState.camera.lookAt(
          lookAtConf.x ?? 0,
          lookAtConf.y ?? 0,
          lookAtConf.z ?? 0
        );
    } else {
        console.warn('Default camera lookAt is missing or invalid in DEFAULT_CONFIG');
    }

    // Apply other camera properties if they exist in cameraConfig (no defaults needed here usually)
    if (config.camera?.fov !== undefined) appState.camera.fov = config.camera.fov;
    if (config.camera?.aspect !== undefined) appState.camera.aspect = config.camera.aspect;
    if (config.camera?.near !== undefined) appState.camera.near = config.camera.near;
    if (config.camera?.far !== undefined) appState.camera.far = config.camera.far;

    appState.camera.updateProjectionMatrix();
    console.debug('Camera defaults applied based on appState.config');
  } else {
      console.warn('Cannot apply camera defaults: AppState.camera is missing.');
  }

  // --- Scene Settings ---
  if (appState.scene) {
    // Use sceneConfig derived from appState.config
    if (config.scene?.backgroundColor !== undefined) {
        appState.scene.background = new THREE.Color(config.scene.backgroundColor);
    }
    console.debug('Scene defaults applied based on appState.config');
  } else {
      console.warn('Cannot apply scene defaults: AppState.scene is missing.');
  }

  // --- Control Settings (OrbitControls) ---
  if (appState.controls) {
    // Derive the controls config, ensuring it's an object
    const inputControlsConfig = config.controls || {};
    // Merge input config with defaults for applying state
    const effectiveControlsConfig = { ...DEFAULT_CONFIG.controls, ...inputControlsConfig }; 

    // Check if custom animation style is active - if so, ensure animate is disabled
    const animationStyle = effectiveControlsConfig.animationStyle || DEFAULT_CONFIG.controls.animationStyle;
    const shouldDisableAnimate = animationStyle && animationStyle !== 'none';
    
    // Apply merged settings to appState.controls
    if (effectiveControlsConfig.animate !== undefined) {
        // Note: OrbitControls still uses 'autoRotate', but our config now uses 'animate'
        // Keep setting autoRotate when needed
        if (shouldDisableAnimate) {
            // Always disable orbit auto-rotation when custom animation is active
            appState.controls.autoRotate = false;
            if (effectiveControlsConfig.animate) {
                console.log(`[config] Preventing orbit autoRotate=true because animation style '${animationStyle}' is active`);
            }
        } else {
            // Only enable auto-rotation if no custom animation AND animate is true
            appState.controls.autoRotate = effectiveControlsConfig.animate;
        }
        
        // IMPORTANT: Also set the AnimationManager's isActive state from the animate config
        if (appState.animationManager) {
            appState.animationManager.setActive(effectiveControlsConfig.animate);
            console.log(`[config] Set AnimationManager.isActive = ${effectiveControlsConfig.animate} from config.controls.animate`);
        }
    }
    if (effectiveControlsConfig.autoRotateSpeed !== undefined) appState.controls.autoRotateSpeed = effectiveControlsConfig.autoRotateSpeed;
    if (effectiveControlsConfig.enableDamping !== undefined) appState.controls.enableDamping = effectiveControlsConfig.enableDamping;
    if (effectiveControlsConfig.dampingFactor !== undefined) appState.controls.dampingFactor = effectiveControlsConfig.dampingFactor;
    if (effectiveControlsConfig.enableZoom !== undefined) appState.controls.enableZoom = effectiveControlsConfig.enableZoom;
    if (effectiveControlsConfig.zoomSpeed !== undefined) appState.controls.zoomSpeed = effectiveControlsConfig.zoomSpeed;
    if (effectiveControlsConfig.enablePan !== undefined) appState.controls.enablePan = effectiveControlsConfig.enablePan;
    if (effectiveControlsConfig.panSpeed !== undefined) appState.controls.panSpeed = effectiveControlsConfig.panSpeed;
    if (effectiveControlsConfig.rotateSpeed !== undefined) appState.controls.rotateSpeed = effectiveControlsConfig.rotateSpeed;
    if (effectiveControlsConfig.minDistance !== undefined) appState.controls.minDistance = effectiveControlsConfig.minDistance;
    if (effectiveControlsConfig.maxDistance !== undefined) appState.controls.maxDistance = effectiveControlsConfig.maxDistance;
    if (effectiveControlsConfig.minPolarAngle !== undefined) appState.controls.minPolarAngle = effectiveControlsConfig.minPolarAngle;
    if (effectiveControlsConfig.maxPolarAngle !== undefined) appState.controls.maxPolarAngle = effectiveControlsConfig.maxPolarAngle;
    
    appState.controls.update();
    console.debug('Controls defaults applied based on appState.config');
  } else {
      console.warn('Cannot apply controls defaults: AppState.controls is missing.');
  }
  
  // --- Apply Lighting Defaults (using separate function but config from appState) ---
  // Ensure lightingConfig is passed correctly if the helper expects it
  if (appState.lightingManager && appState.uiManager) { // Check dependencies for helper
      applyLightingDefaults(appState, lightingConfig);
      console.debug('Lighting defaults applied based on appState.config');
  } else {
      console.warn('Cannot apply lighting defaults: LightingManager or UIManager missing in AppState.');
  }

  // --- Apply settings that require managers (before UI sync for certain items) ---
  // Set initial screen content FIRST, before syncing the UI dropdown that controls it
  // --- REMOVED redundant screen content setting --- 
  /*
  if (appState.screenManager && screenConfig.initialContent) {
      // Pass the whole initialContent object or string directly
      appState.screenManager.setScreenContent(screenConfig.initialContent);
      console.debug('Applied initial screen content (before UI sync)'); // Log updated
  }
  */

  // --- Synchronize UI elements (if UIManager exists) ---
  const uiManager = appState.uiManager;
  if (uiManager) {
    // Use the input configs defined above for UI sync logic
    // const inputControlsConfigForUI = config.controls || {}; // MOVED
    // const inputModelConfigForUI = config.model || {}; // MOVED
    // const inputEnvironmentConfigForUI = config.environment || {}; // MOVED

    // Sync controls UI
    if (uiManager.animateCheckbox) uiManager.animateCheckbox.checked = inputControlsConfigForUI.animate ?? false;
    if (uiManager.rotationSpeedInput) uiManager.rotationSpeedInput.value = inputControlsConfigForUI.autoRotateSpeed ?? 15;
    if (uiManager.animationStyleSelect) uiManager.animationStyleSelect.value = inputControlsConfigForUI.animationStyle ?? DEFAULT_CONFIG.controls.animationStyle; // Use default for style

    // Sync model UI
    if (uiManager.modelColorSelect) uiManager.modelColorSelect.value = inputModelConfigForUI.initialColor ?? DEFAULT_CONFIG.model.initialColor;
    if (uiManager.sampleScreensSelect) {
      try {
        // Options use preset names; map the configured index to the name
        const idx = inputModelConfigForUI.initialScreen ?? DEFAULT_CONFIG.model.initialScreen;
        const presetArray = (window && window.app && window.app.presets && Array.isArray(window.app.presets.SAMPLE_SCREENS))
          ? window.app.presets.SAMPLE_SCREENS
          : (typeof SAMPLE_SCREENS !== 'undefined' ? SAMPLE_SCREENS : []);
        const presetName = (Array.isArray(presetArray) && presetArray[idx]) ? presetArray[idx].name : '';
        if (presetName) {
          uiManager.sampleScreensSelect.value = presetName;
        }
      } catch (_) {
        // Fallback silently if SAMPLE_SCREENS is unavailable at this point
      }
    }
    if (uiManager.screenBacklightSlider) {
        uiManager.screenBacklightSlider.value = inputModelConfigForUI.backlightIntensity ?? DEFAULT_CONFIG.model.backlightIntensity;
        if (uiManager.screenBacklightValue) {
            uiManager.screenBacklightValue.textContent = parseFloat(uiManager.screenBacklightSlider.value).toFixed(1);
        }
    }
    if (uiManager.reflectionIntensitySlider) {
        uiManager.reflectionIntensitySlider.value = inputModelConfigForUI.reflectionIntensity ?? DEFAULT_CONFIG.model.reflectionIntensity;
        if (uiManager.reflectionIntensityValue) {
            uiManager.reflectionIntensityValue.textContent = parseFloat(uiManager.reflectionIntensitySlider.value).toFixed(1);
        }
    }
    if (uiManager.displayModeSelect) uiManager.displayModeSelect.value = inputModelConfigForUI.displayMode ?? DEFAULT_CONFIG.model.displayMode;
    if (uiManager.maintainAspectRatioCheckbox) uiManager.maintainAspectRatioCheckbox.checked = inputModelConfigForUI.maintainAspectRatio ?? DEFAULT_CONFIG.model.maintainAspectRatio;
    
    // Log the values being used
    // console.log(`[applyDefaults] Applying Device Orientation. Config value:`, inputModelConfigForUI.deviceOrientation);
    // const targetAngle = inputModelConfigForUI.deviceOrientation?.angle?.toString() ?? '0';
    // console.log(`[applyDefaults] Target angle for deviceOrientationSelect: ${targetAngle}`);

    // --- REMOVED: Let UIManager handle setting the initial value after populating options ---
    // if (uiManager.deviceOrientationSelect && inputModelConfigForUI.deviceOrientation) uiManager.deviceOrientationSelect.value = targetAngle;
    
    console.log(`[applyDefaults] Applying Media Handling. Config value:`, inputModelConfigForUI.deviceOrientation);
    const targetHandling = inputModelConfigForUI.deviceOrientation?.mediaHandling ?? 'match'; // Use 'match' as fallback from default if needed
    console.log(`[applyDefaults] Target value for mediaHandlingSelect: ${targetHandling}`);
    
    if (uiManager.mediaHandlingSelect && inputModelConfigForUI.deviceOrientation) uiManager.mediaHandlingSelect.value = targetHandling;
    
    // *** ADDED: Apply screenBaseRotation ***
    if (uiManager.rotationTypeSelect) { // Assuming rotationTypeSelect is cached in UIManager
        const targetBaseRotation = inputModelConfigForUI.screenBaseRotation?.toString() ?? '0';
        console.log(`[applyDefaults] Target value for rotationTypeSelect: ${targetBaseRotation}`);
        uiManager.rotationTypeSelect.value = targetBaseRotation;
    }
    // *** END ADDED ***
    
    // Sync environment UI
    if (uiManager.environmentSelect) uiManager.environmentSelect.value = inputEnvironmentConfigForUI.initialEnvironment ?? DEFAULT_CONFIG.environment.initialEnvironment;
    if (uiManager.lightingThemeSelect) uiManager.lightingThemeSelect.value = inputEnvironmentConfigForUI.initialLightingTheme ?? DEFAULT_CONFIG.environment.initialLightingTheme;
    if (uiManager.showEnvironmentBgCheckbox) {
        const showBg = inputEnvironmentConfigForUI.showBackground ?? true; // Default to true if not set
        uiManager.showEnvironmentBgCheckbox.checked = showBg;
    }
    if (uiManager.themeOverrideCheckbox) uiManager.themeOverrideCheckbox.checked = false; // Reset override on applyDefaults

    console.debug('UI synchronized with defaults from appState.config');
  } else {
      console.warn('Cannot synchronize UI: UIManager missing in AppState.');
  }

  // --- Apply OTHER settings that require managers (after UI sync) ---
  // Apply backlight to screenManager
  if (appState.screenManager) {
      appState.screenManager.setScreenBacklight(inputModelConfigForUI.backlightIntensity ?? DEFAULT_CONFIG.model.backlightIntensity);
  }
  // Apply lighting theme via lightingManager
  if (appState.lightingManager && appState.uiManager && uiManager.lightingThemeSelect) {
       const themeIndex = inputEnvironmentConfigForUI.initialLightingTheme ?? DEFAULT_CONFIG.environment.initialLightingTheme;
       const selectedTheme = LIGHTING_PRESETS[themeIndex];
       if (selectedTheme) {
          appState.lightingManager.setLightingPreset(selectedTheme);
          console.debug(`Applied lighting theme: ${selectedTheme.name}`);
       }
  }
  // Apply environment background visibility
  if (appState.environmentManager && appState.uiManager && uiManager.showEnvironmentBgCheckbox) {
      appState.environmentManager.updateEnvironmentBackgroundVisibility(uiManager.showEnvironmentBgCheckbox.checked);
  }
  // Set initial model color - REMOVED FROM HERE
  // This should be handled after the model_loaded event confirms materials are ready
  // if (appState.modelManager && appState.uiManager && uiManager.modelColorSelect) {
  //     // Assuming model color select value corresponds to preset key/index
  //     const initialColorKey = Object.keys(MODEL_COLOR_PRESETS)[inputModelConfigForUI.initialColor ?? 0]; // Make sure MODEL_COLOR_PRESETS is imported/available
  //     if (initialColorKey) {
  //         appState.modelManager.updateDeviceColor(initialColorKey);
  //         console.debug(`Applied initial model color: ${initialColorKey}`);
  //     }
  // }

  console.log('Default configurations applied successfully based on appState.config.');
}

/**
 * Apply lighting defaults to both the application and UI
 * @param {Object} appState - Object containing managers, components, and config
 * @param {Object} lightingConfig - Lighting configuration portion
 */
function applyLightingDefaults(appState, lightingConfig) {
  // Get lighting manager and UI manager
  const lightingManager = appState.lightingManager;
  const uiManager = appState.uiManager;
  if (!lightingManager || !uiManager) return;
  
  // Use provided config or fallback to defaults
  const ambientConf = lightingConfig.ambient ?? DEFAULT_CONFIG.lighting.ambient;
  const keyConf = lightingConfig.key ?? DEFAULT_CONFIG.lighting.key;
  const fillConf = lightingConfig.fill ?? DEFAULT_CONFIG.lighting.fill;
  const backConf = lightingConfig.back ?? DEFAULT_CONFIG.lighting.back;
  
  // Ambient light
  if (ambientConf) {
      if (lightingManager.lights.ambient) {
        if(ambientConf.color !== undefined) lightingManager.lights.ambient.color.setHex(ambientConf.color);
        if(ambientConf.intensity !== undefined) lightingManager.lights.ambient.intensity = ambientConf.intensity;
        if(ambientConf.enabled !== undefined) lightingManager.lights.ambient.visible = ambientConf.enabled;
      }
      if (uiManager.ambientLightToggle && ambientConf.enabled !== undefined) {
        uiManager.ambientLightToggle.checked = ambientConf.enabled;
      }
      if (uiManager.ambientLightIntensity && ambientConf.intensity !== undefined) {
        uiManager.ambientLightIntensity.value = ambientConf.intensity;
      }
      if (uiManager.ambientLightIntensityValue && ambientConf.intensity !== undefined) {
        uiManager.ambientLightIntensityValue.textContent = ambientConf.intensity.toFixed(1);
      }
      if (uiManager.ambientLightColor && ambientConf.color !== undefined) {
        uiManager.ambientLightColor.value = '#' + ambientConf.color.toString(16).padStart(6, '0');
      }
  }
  
  // Key light
  if (keyConf) {
      if (lightingManager.lights.key) {
        if(keyConf.color !== undefined) lightingManager.lights.key.color.setHex(keyConf.color);
        if(keyConf.intensity !== undefined) lightingManager.lights.key.intensity = keyConf.intensity;
        if(keyConf.enabled !== undefined) lightingManager.lights.key.visible = keyConf.enabled;
        if(keyConf.position) {
             lightingManager.lights.key.position.set(
               keyConf.position.x ?? 0,
               keyConf.position.y ?? 0,
               keyConf.position.z ?? 0
            );
        }
      }
      if (uiManager.keyLightToggle && keyConf.enabled !== undefined) {
        uiManager.keyLightToggle.checked = keyConf.enabled;
      }
      if (uiManager.keyLightIntensity && keyConf.intensity !== undefined) {
        uiManager.keyLightIntensity.value = keyConf.intensity;
      }
      if (uiManager.keyLightIntensityValue && keyConf.intensity !== undefined) {
        uiManager.keyLightIntensityValue.textContent = keyConf.intensity.toFixed(1);
      }
      if (uiManager.keyLightColor && keyConf.color !== undefined) {
        uiManager.keyLightColor.value = '#' + keyConf.color.toString(16).padStart(6, '0');
      }
  }
  
  // Fill light
  if (fillConf) {
      if (lightingManager.lights.fill) {
        if(fillConf.color !== undefined) lightingManager.lights.fill.color.setHex(fillConf.color);
        if(fillConf.intensity !== undefined) lightingManager.lights.fill.intensity = fillConf.intensity;
        if(fillConf.enabled !== undefined) lightingManager.lights.fill.visible = fillConf.enabled;
        if(fillConf.position) {
            lightingManager.lights.fill.position.set(
              fillConf.position.x ?? 0,
              fillConf.position.y ?? 0,
              fillConf.position.z ?? 0
            );
        }
      }
      if (uiManager.fillLightToggle && fillConf.enabled !== undefined) {
        uiManager.fillLightToggle.checked = fillConf.enabled;
      }
      if (uiManager.fillLightIntensity && fillConf.intensity !== undefined) {
        uiManager.fillLightIntensity.value = fillConf.intensity;
      }
      if (uiManager.fillLightIntensityValue && fillConf.intensity !== undefined) {
        uiManager.fillLightIntensityValue.textContent = fillConf.intensity.toFixed(1);
      }
      if (uiManager.fillLightColor && fillConf.color !== undefined) {
        uiManager.fillLightColor.value = '#' + fillConf.color.toString(16).padStart(6, '0');
      }
  }
  
  // Back light
  if (backConf) {
      if (lightingManager.lights.back) {
        if(backConf.color !== undefined) lightingManager.lights.back.color.setHex(backConf.color);
        if(backConf.intensity !== undefined) lightingManager.lights.back.intensity = backConf.intensity;
        if(backConf.enabled !== undefined) lightingManager.lights.back.visible = backConf.enabled;
        if(backConf.position) {
            lightingManager.lights.back.position.set(
              backConf.position.x ?? 0,
              backConf.position.y ?? 0,
              backConf.position.z ?? 0
            );
        }
      }
      if (uiManager.backLightToggle && backConf.enabled !== undefined) {
        uiManager.backLightToggle.checked = backConf.enabled;
      }
      if (uiManager.backLightIntensity && backConf.intensity !== undefined) {
        uiManager.backLightIntensity.value = backConf.intensity;
      }
      if (uiManager.backLightIntensityValue && backConf.intensity !== undefined) {
        uiManager.backLightIntensityValue.textContent = backConf.intensity.toFixed(1);
      }
      if (uiManager.backLightColor && backConf.color !== undefined) {
        uiManager.backLightColor.value = '#' + backConf.color.toString(16).padStart(6, '0');
      }
  }

  // Ensure Theme Override Checkbox is disabled if applying presets initially
  if (uiManager.themeOverrideCheckbox) {
    uiManager.themeOverrideCheckbox.checked = false;
    // Optionally disable controls if needed, though UIManager listener might handle this
    const lightControls = uiManager.getLightControls ? uiManager.getLightControls() : null;
    if (lightControls) {
        const setEnabled = (elements, isEnabled) => {
          Object.values(elements).forEach(element => {
            if (element) element.disabled = !isEnabled;
          });
        };
        if (lightControls.key) setEnabled(lightControls.key, false);
        if (lightControls.fill) setEnabled(lightControls.fill, false);
        if (lightControls.back) setEnabled(lightControls.back, false);
        if (lightControls.ambient) setEnabled(lightControls.ambient, false);
    }
  }
}

