import * as THREE from 'three';

export class ScreenGeometryManager {
  constructor() {
    this.screenMesh = null;
    this.screenGeometry = null;
    this.debugSettings = {
      useMeanEdgeExtension: false
    };
  }

  /**
   * Set the screen mesh and configure it for optimal display
   * @param {THREE.Mesh} mesh - The screen mesh to use
   */
  setScreenMesh(mesh) {
    if (!mesh) {
      console.warn('Attempted to set null screen mesh');
      return;
    }

    this.screenMesh = mesh;
    this.screenGeometry = mesh.geometry;
    
    // Fix any geometry issues that might cause tearing
    this.fixScreenGeometry();
  }

  /**
   * Fix common screen geometry issues
   */
  fixScreenGeometry() {
    if (!this.screenGeometry) return;

    // Ensure proper winding order
    this.checkIfNormalsNeedFlipping();
    
    // Fix UV coordinates if needed
    this.fixUVCoordinates();
    
    // Optimize geometry for performance
    this.optimizeGeometry();
  }

  /**
   * Check if screen normals need to be flipped
   * @returns {boolean} Whether normals need to be flipped
   */
  checkIfNormalsNeedFlipping() {
    if (!this.screenGeometry) return false;

    // Handle BufferGeometry
    if (this.screenGeometry.isBufferGeometry) {
      const position = this.screenGeometry.attributes.position;
      const normal = this.screenGeometry.attributes.normal;
      
      if (!position || !normal) return false;

      // Get first three vertices to form a face
      const v1 = new THREE.Vector3();
      const v2 = new THREE.Vector3();
      const v3 = new THREE.Vector3();
      
      v1.fromBufferAttribute(position, 0);
      v2.fromBufferAttribute(position, 1);
      v3.fromBufferAttribute(position, 2);
      
      // Calculate face normal
      const edge1 = new THREE.Vector3().subVectors(v2, v1);
      const edge2 = new THREE.Vector3().subVectors(v3, v1);
      const faceNormal = new THREE.Vector3().crossVectors(edge1, edge2).normalize();
      
      // Check if normal points towards camera
      const cameraPosition = new THREE.Vector3(0, 0, 5); // Assuming camera is at z=5
      const dotProduct = faceNormal.dot(cameraPosition);
      
      return dotProduct < 0;
    }
    
    // Handle legacy Geometry
    if (this.screenGeometry.faces && this.screenGeometry.faces.length > 0) {
      const face = this.screenGeometry.faces[0];
      const v1 = this.screenGeometry.vertices[face.a];
      const v2 = this.screenGeometry.vertices[face.b];
      const v3 = this.screenGeometry.vertices[face.c];
      
      const edge1 = new THREE.Vector3().subVectors(v2, v1);
      const edge2 = new THREE.Vector3().subVectors(v3, v1);
      const faceNormal = new THREE.Vector3().crossVectors(edge1, edge2).normalize();
      
      const cameraPosition = new THREE.Vector3(0, 0, 5);
      const dotProduct = faceNormal.dot(cameraPosition);
      
      return dotProduct < 0;
    }

    return false;
  }

  /**
   * Flip screen normals if needed
   */
  flipScreenNormals() {
    if (!this.screenGeometry) return;

    // Handle BufferGeometry
    if (this.screenGeometry.isBufferGeometry) {
      const normal = this.screenGeometry.attributes.normal;
      if (!normal) return;

      // Flip all normals
      for (let i = 0; i < normal.count; i++) {
        normal.setXYZ(
          i,
          -normal.getX(i),
          -normal.getY(i),
          -normal.getZ(i)
        );
      }
      normal.needsUpdate = true;
    } else {
      // Handle legacy Geometry
      for (let i = 0; i < this.screenGeometry.faces.length; i++) {
        const face = this.screenGeometry.faces[i];
        const temp = face.a;
        face.a = face.c;
        face.c = temp;
      }

      this.screenGeometry.computeFaceNormals();
      this.screenGeometry.computeVertexNormals();
    }
  }

  /**
   * Fix UV coordinates to prevent texture distortion
   */
  fixUVCoordinates() {
    if (!this.screenGeometry) return;

    // Handle BufferGeometry
    if (this.screenGeometry.isBufferGeometry) {
      if (!this.screenGeometry.attributes.uv) {
        this.screenGeometry.computeBoundingBox();
        const bbox = this.screenGeometry.boundingBox;
        const position = this.screenGeometry.attributes.position;
        
        const uvs = new Float32Array(position.count * 2);
        for (let i = 0; i < position.count; i++) {
          const x = position.getX(i);
          const y = position.getY(i);
          uvs[i * 2] = (x - bbox.min.x) / (bbox.max.x - bbox.min.x);
          uvs[i * 2 + 1] = (y - bbox.min.y) / (bbox.max.y - bbox.min.y);
        }
        
        this.screenGeometry.setAttribute('uv', new THREE.BufferAttribute(uvs, 2));
      }
    } else {
      // Handle legacy Geometry
      if (!this.screenGeometry.uvs || this.screenGeometry.uvs.length === 0) {
        this.screenGeometry.computeBoundingBox();
        const bbox = this.screenGeometry.boundingBox;
        
        this.screenGeometry.uvs = [];
        for (let i = 0; i < this.screenGeometry.vertices.length; i++) {
          const vertex = this.screenGeometry.vertices[i];
          const u = (vertex.x - bbox.min.x) / (bbox.max.x - bbox.min.x);
          const v = (vertex.y - bbox.min.y) / (bbox.max.y - bbox.min.y);
          this.screenGeometry.uvs.push(new THREE.Vector2(u, v));
        }
      }
    }
  }

  /**
   * Optimize geometry for better performance
   */
  optimizeGeometry() {
    if (!this.screenGeometry) return;

    // Handle BufferGeometry
    if (this.screenGeometry.isBufferGeometry) {
      this.screenGeometry.computeBoundingBox();
      this.screenGeometry.computeBoundingSphere();
      
      // Merge vertices if they're very close
      const position = this.screenGeometry.attributes.position;
      const mergedVertices = new Map();
      
      for (let i = 0; i < position.count; i++) {
        const x = position.getX(i);
        const y = position.getY(i);
        const z = position.getZ(i);
        const key = `${x.toFixed(3)},${y.toFixed(3)},${z.toFixed(3)}`;
        
        if (!mergedVertices.has(key)) {
          mergedVertices.set(key, i);
        }
      }
      
      // Update indices if needed
      if (this.screenGeometry.index) {
        const indices = this.screenGeometry.index.array;
        for (let i = 0; i < indices.length; i++) {
          const x = position.getX(indices[i]);
          const y = position.getY(indices[i]);
          const z = position.getZ(indices[i]);
          const key = `${x.toFixed(3)},${y.toFixed(3)},${z.toFixed(3)}`;
          
          if (mergedVertices.has(key)) {
            indices[i] = mergedVertices.get(key);
          }
        }
        this.screenGeometry.index.needsUpdate = true;
      }
    } else {
      // Handle legacy Geometry
      this.screenGeometry.mergeVertices();
      this.removeDuplicateFaces();
      this.screenGeometry.computeBoundingBox();
      this.screenGeometry.computeBoundingSphere();
    }
  }

  /**
   * Remove duplicate faces from the geometry
   */
  removeDuplicateFaces() {
    if (!this.screenGeometry || !this.screenGeometry.faces) return;

    const uniqueFaces = new Set();
    const newFaces = [];

    for (const face of this.screenGeometry.faces) {
      const key = `${face.a},${face.b},${face.c}`;
      if (!uniqueFaces.has(key)) {
        uniqueFaces.add(key);
        newFaces.push(face);
      }
    }

    this.screenGeometry.faces = newFaces;
  }

  /**
   * Apply mean edge extension to prevent z-fighting
   * @param {number} extensionAmount - Amount to extend edges by
   */
  applyMeanEdgeExtension(extensionAmount = 0.001) {
    if (!this.screenGeometry || !this.debugSettings.useMeanEdgeExtension) return;

    // Handle BufferGeometry
    if (this.screenGeometry.isBufferGeometry) {
      const position = this.screenGeometry.attributes.position;
      const normal = this.screenGeometry.attributes.normal;
      
      if (!position || !normal) return;
      
      // Calculate mean edge length
      let totalEdgeLength = 0;
      let edgeCount = 0;
      
      for (let i = 0; i < position.count; i += 3) {
        const v1 = new THREE.Vector3();
        const v2 = new THREE.Vector3();
        const v3 = new THREE.Vector3();
        
        v1.fromBufferAttribute(position, i);
        v2.fromBufferAttribute(position, i + 1);
        v3.fromBufferAttribute(position, i + 2);
        
        totalEdgeLength += v1.distanceTo(v2);
        totalEdgeLength += v2.distanceTo(v3);
        totalEdgeLength += v3.distanceTo(v1);
        edgeCount += 3;
      }
      
      const meanEdgeLength = totalEdgeLength / edgeCount;
      const extension = meanEdgeLength * extensionAmount;
      
      // Extend vertices along their normals
      for (let i = 0; i < position.count; i++) {
        const nx = normal.getX(i);
        const ny = normal.getY(i);
        const nz = normal.getZ(i);
        
        position.setXYZ(
          i,
          position.getX(i) + nx * extension,
          position.getY(i) + ny * extension,
          position.getZ(i) + nz * extension
        );
      }
      
      position.needsUpdate = true;
    } else {
      // Handle legacy Geometry
      const vertices = this.screenGeometry.vertices;
      const faces = this.screenGeometry.faces;
      
      // Calculate mean edge length
      let totalEdgeLength = 0;
      let edgeCount = 0;
      
      for (const face of faces) {
        const v1 = vertices[face.a];
        const v2 = vertices[face.b];
        const v3 = vertices[face.c];
        
        totalEdgeLength += v1.distanceTo(v2);
        totalEdgeLength += v2.distanceTo(v3);
        totalEdgeLength += v3.distanceTo(v1);
        edgeCount += 3;
      }
      
      const meanEdgeLength = totalEdgeLength / edgeCount;
      const extension = meanEdgeLength * extensionAmount;
      
      // Extend vertices along their normals
      for (let i = 0; i < vertices.length; i++) {
        const vertex = vertices[i];
        const normal = new THREE.Vector3();
        
        // Calculate average normal for this vertex
        let normalCount = 0;
        for (const face of faces) {
          if (face.a === i || face.b === i || face.c === i) {
            normal.add(face.normal);
            normalCount++;
          }
        }
        
        if (normalCount > 0) {
          normal.divideScalar(normalCount).normalize();
          vertex.add(normal.multiplyScalar(extension));
        }
      }
    }
  }

  /**
   * Get the screen mesh
   * @returns {THREE.Mesh|null}
   */
  getScreenMesh() {
    return this.screenMesh;
  }

  /**
   * Get the screen geometry
   * @returns {THREE.BufferGeometry|THREE.Geometry|null}
   */
  getScreenGeometry() {
    return this.screenGeometry;
  }

  /**
   * Update debug settings
   * @param {Object} settings - New debug settings
   */
  updateDebugSettings(settings) {
    this.debugSettings = { ...this.debugSettings, ...settings };
  }

  /**
   * Dispose of resources
   */
  dispose() {
    if (this.screenGeometry) {
      this.screenGeometry.dispose();
      this.screenGeometry = null;
    }
    this.screenMesh = null;
  }
} 